.path-checkout {
  font-family: $checkout-font-family;
  -webkit-font-smoothing: subpixel-antialiased;

  .callout {
    position: fixed;
    z-index: 1000;
    right: 15px;
    top: 15px;
    box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.15);

    @include breakpoint(small only) {
      left: 15px;
    }

    a {
      color: $checkout-primary-color;
    }
  }
}

.checkout-pane {
  clear: both;
}

.commerce-checkout-flow {
  overflow: hidden;
  font-family: $checkout-font-family;
}

.checkout-form {
  &-sidebar {
    background-color: $checkout-light-gray;
    padding-left: $global-padding * 2.5;

    @include breakpoint(medium) {
      border-left: 1px solid $checkout-medium-gray;
      padding-top: 50px;
    }

    @include breakpoint(large) {
      padding-bottom: 180px;
      min-height: 100vh;
    }

    @include breakpoint(medium down) {
      padding-top: 65px;
      padding-left: 0;
      padding-right: 0;
      margin-bottom: 30px;
    }

    h3 {
      font-family: $checkout-font-family;
      font-size: rem-calc(20);
      font-weight: normal;
      color: $checkout-black;

      @include breakpoint(small only) {
        font-size: rem-calc(18);
        font-weight: normal;
      }
    }

    @include breakpoint(large) {
      position: relative;

      &::after {
        content: '';
        display: block;
        background-color: $checkout-light-gray;
        position: absolute;
        top: 0;
        left: 100%;
        height: 100%;
        width: 50vw;
        bottom: 0;
      }
    }
  }

  &-sidebar-inner {
    position: relative;

    @include breakpoint(large down) {
      background-color: $checkout-light-gray;
    }
  }

  &-main {
    padding-bottom: 20px;
    background-color: $checkout-white;
    @include breakpoint(medium) {
      padding-top: 10px;
    }

    @include breakpoint(large) {
      padding-top: 130px;
      padding-right: 35px;
    }

    @include breakpoint(xlarge) {
      padding-right: 60px;
    }

    @include breakpoint(medium down) {
      float: none !important;
    }

    .checkout-form-title-wrapper {
      position: absolute;
      top: 30px;
      left: 1rem;
      margin: 0;

      @include breakpoint(medium down) {
        top: 0;
        left: 0;
        right: 0;
        background-color: $checkout-white;

        h1 {
          max-width: 530px;
          margin: 13px auto;
        }
      }

      .checkout-form-title,
      .checkout-form-title-logo {
        @include breakpoint(small only) {
          padding-left: 10px;
          padding-right: 10px;
        }
      }

      .checkout-form-title-logo {
        height: 50px;
      }

      &.is-logo {
        @include breakpoint(medium down) {
          background-color: transparent;
          margin-top: 10px;
          text-align: center;

          .checkout-form-title-logo {
            height: 45px;
          }
        }
      }
    }
  }

  &-main-inner {
    height: 100%;

    @include breakpoint(medium down) {
      max-width: 530px;
      margin-left: auto;
      margin-right: auto;
    }
  }

  &.is-open {
    .checkout-form-sidebar {
      padding-bottom: 180px;

      @include breakpoint(medium down) {
        border-bottom: 1px solid $checkout-medium-gray;
      }
    }

    .checkout-form-main {
      @include breakpoint(large down) {
        max-width: 530px;
        margin-left: auto;
        margin-right: auto;
      }

      @include breakpoint(medium down) {
        padding-top: 185px;
      }
    }

    .checkout-summary-content {
      padding-top: 25px;
    }

    .fa-chevron-down {
      display: none;
    }

    .fa-chevron-up {
      display: inline-block;
    }
  }

  legend {
    margin-bottom: 0.8rem;
  }

  fieldset {
    margin-bottom: 20px;
  }

  &-title {
    margin-top: 20px;
    margin-bottom: 20px;
    color: $checkout-black;
    font-family: $checkout-font-family;
    font-size: rem-calc(28);
  }

  &-login-link {
    font-size: 13px;
    text-align: right;
    position: relative;
    top: 26px;
    z-index: 1;
    display: none;

    @media (max-width: 440px) {
      top: -15px;
    }
  }

  // Form elements
  .js-form-type-email,
  .js-form-type-textfield,
  .js-form-type-tel,
  .js-form-type-password {
    position: relative;

    input[type='text'],
    input[type='email'],
    input[type='tel'],
    input[type='password'] {
      height: 44px;
      border-radius: 5px;
      outline: inherit;
      padding-left: 1rem;
      padding-top: 22px;
      margin-bottom: 10px;
      border-color: $checkout-form-border;

      &:focus,
      &:active {
        border: 2px solid $checkout-primary-color;
        outline: none;
      }

      @include placeholder {
        color: transparent;
      }
    }

    label {
      transition: all 0.2s ease-in-out;
      position: absolute;
      color: $checkout-dark-gray;
      top: 10px;
      left: 18px;
      font-size: 14px;
    }

    .fl-is-active,
    .fl-has-focus {
      label {
        font-size: 11px;
        top: 2px;
        left: 18px;
      }
    }
  }

  // Select
  .js-form-type-select {
    label {
      display: none;
    }

    select {
      height: 44px;
      border-radius: 5px;
      background-color: $checkout-white;
      margin-bottom: 10px;
      color: $checkout-dark-gray;
      font-size: 14px;

      &:active,
      &:focus {
        border: 2px solid $checkout-primary-color;
        outline: none;
      }
    }
  }

  .fieldset-legend {
    font-size: rem-calc(20);
    font-weight: normal;
  }

  .button {
    &.checkout {
      font-size: 13px;
      padding: 1.2em 1.5em;
      margin-bottom: 0;
      background-color: $checkout-primary-color;
      color: $white;

      &:hover {
        background-color: darken($checkout-primary-color, 15%);
      }
    }
  }

  // Radio buttons
  .js-form-type-radio {
    position: relative;

    .ajax-progress-throbber {
      font-size: 12px;
    }

    // Not checked
    label {
      &::before {
        content: '';
        height: 20px;
        width: 20px;
        border: 3px solid $medium-gray;
        border-radius: 50%;
        display: block;
        @include vertical-center;
        left: 12px;
        transition: all ease 0.3s;
      }
    }

    input[type='radio'] {
      visibility: hidden;
      margin: 0;

      // Checked
      &:checked + label {
        &::before {
          border-color: $checkout-primary-color;
        }

        &::after {
          content: '';
          display: block;
          @include vertical-center;
          left: 19px;
          height: 6px;
          width: 6px;
          border-radius: 50%;
          background-color: $checkout-primary-color;
          transition: all ease 0.3s;
        }
      }
    }
  }

  // Checkbox
  .js-form-type-checkbox {
    position: relative;
    margin-bottom: 15px;

    // Not checked
    label {
      padding-left: 1rem;

      &::before {
        content: '';
        height: 20px;
        width: 20px;
        background-color: $white;
        border: 3px solid $medium-gray;
        border-radius: 4px;
        display: block;
        @include vertical-center;
        left: 12px;
        transition: all ease 0.3s;
      }
    }

    input[type='checkbox'] {
      visibility: hidden;
      margin: 0;

      // Checked
      &:checked + label {
        &::after {
          content: '\f00c';
          font-family: 'Font Awesome 5';
          font-weight: bold;
          color: $white;
          display: block;
          @include vertical-center;
          left: 16px;
          transition: all ease 0.3s;
        }

        &::before {
          background-color: $checkout-primary-color;
          border-color: $checkout-primary-color;
        }
      }
    }
  }
}

.form-item-dcom-age-confirmation-age-confirmation {
  margin-left: -10px;

  .js-form-type-checkbox label {
    padding-left: 1.3rem;
  }
}

[id^='edit-sidebar-coupon-redemption-form'] {
  border-top: 1px solid $checkout-medium-gray;
  border-bottom: 1px solid $checkout-medium-gray;
  padding-top: $global-padding;
  padding-bottom: $global-padding;
  display: flex;

  label {
    color: $checkout-dark-gray;
  }

  input[type='text'] {
    background-color: $checkout-white;
    outline: 1px inherit;
    border-color: $checkout-form-border;
    margin-bottom: 0;

    @include breakpoint(small only) {
      background-color: $checkout-white;
    }

    &:focus,
    &:active {
      border: 2px solid $checkout-dark-gray;
      outline: none;
    }

    @include placeholder {
      color: transparent;
    }
  }

  input[type='submit'] {
    @include button-style(
      $checkout-coupon-button,
      $checkout-black,
      $checkout-white
    );
    margin-left: $global-margin;
    border-radius: 5px;
    height: 44px;
    margin-bottom: 0;
  }

  .button {
    font-size: 0.9rem;
    line-height: 0.8rem;
  }
}

// Bottom links
.footer-policy-links {
  font-size: 13px;
  text-align: left;
  border-top: 1px solid $checkout-medium-gray;
  padding-top: 13px;

  @include breakpoint(medium down) {
    margin-top: 25px;
    text-align: center;
  }

  ul {
    margin: 0;
    padding: 0;
  }

  li {
    list-style-type: none;
    display: inline-block;
    margin-right: rem-calc(20);

    &:last-child {
      margin-right: 0;
    }

    a {
      color: $checkout-primary-color;
      padding: 0;

      &:hover {
        color: $checkout-black;
      }
    }
  }
}

// Checkout process
.cpl-commerce-checkout-progress {
  @include grid-row;
  position: absolute;
  z-index: 1;
  top: 100px;

  left: 1rem;
  @include breakpoint(large) {
    left: calc(50vw - 520px);
  }
  @include breakpoint(xlarge) {
    left: calc((100vw - 1170px) / 2);
  }
  @include breakpoint(medium down) {
    display: none;
  }

  .checkout-progress {
    padding: 0;
    margin: 0 0 0 -6px;
    font-size: 11px;

    &--step {
      color: $checkout-dark-gray;
      padding-right: rem-calc(7);

      &::before {
        content: none;
      }

      &::after {
        content: '\f054';
        font-family: 'Font Awesome 5';
        font-weight: bold;
        color: $checkout-dark-gray;
        margin-left: rem-calc(12);
        font-size: 9px;
      }

      &:last-child {
        &::after {
          content: none;
        }
      }

      &__current {
        color: $checkout-black;
      }
    }
  }
}

.checkout-pane-order-summary {
  .cart-item {
    border: 0;
    padding-top: 0.6rem;
    padding-bottom: 0.6rem;
  }

  .cart-item-title {
    font-weight: normal;
    font-size: 15px;
    line-height: 1.3;
  }

  .cart-item-content {
    align-self: center;
  }

  .cart-item-weight {
    color: $checkout-cart-item-weight;
    font-size: 13px;
  }

  .badge {
    background-color: $checkout-cart-badge-bg-color;
    color: $checkout-cart-badge-text-color;
  }

  .cart-item-first {
    padding-left: 0;

    img {
      border: 1px solid $checkout-medium-gray;
      border-radius: 8px;
      width: 65px;
      height: 65px;
    }
  }

  .cart-item-last {
    align-self: center;
    font-weight: normal;
    font-size: 14px;
  }

  header {
    h3 {
      margin-top: 30px;
    }
  }

  .field-name-total-price {
    position: absolute;
    bottom: -185px;
    left: 0;
    right: 0;

    @include breakpoint(medium down) {
      bottom: -170px;
      max-width: 530px;
      margin-left: auto;
      margin-right: auto;
      padding-left: 10px;
      padding-right: 10px;
    }
  }

  .order-total-line {
    display: flex;
    justify-content: space-between;
    padding: 0;
    margin-bottom: 5px;

    &-value {
      font-weight: normal;
    }

    &__subtotal {
      font-size: 14px;
    }

    &__adjustment {
      font-size: 14px;
    }

    &__total {
      padding-top: 16px;
      margin-top: 16px;
      border-top: 1px solid $checkout-medium-gray !important;
      @include breakpoint(small only) {
        border-bottom: 1px solid $checkout-medium-gray;
        padding-bottom: 10px;
      }

      .order-total-line-label {
        font-size: 18px;
        font-weight: normal;
      }

      .order-total-line-value {
        font-size: 24px;
        font-weight: normal;
        width: initial;
      }
    }
  }
}

.checkout-pane-coupon-redemption {
  margin-top: $global-margin;
}

.checkout-summary-sm,
.checkout-summary-content-inner {
  @include breakpoint(medium down) {
    max-width: 530px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 10px;
    padding-right: 10px;
  }
}

.checkout-summary-content-inner {
  @include breakpoint(medium down) {
    padding-left: 10px;
    padding-right: 10px;
  }
}

.checkout-summary-sm {
  cursor: pointer;
  color: $checkout-primary-color;
  background-color: $checkout-light-gray;
  font-size: 14px;
  padding: 15px 0;
  @include breakpoint(small only) {
    padding: 15px 10px;
  }

  &-wrapper {
    @include breakpoint(medium down) {
      border-top: 1px solid $checkout-medium-gray;
      border-bottom: 1px solid $checkout-medium-gray;
    }
  }

  &-inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .fas {
    margin-left: 10px;
    margin-right: 10px;
    position: relative;
  }

  .fa-chevron-down,
  .fa-chevron-up {
    font-size: 14px;
    top: 1px;
  }

  .fa-chevron-up {
    display: none;
  }

  .fa-shopping-cart {
    font-size: 22px;
    top: 2px;
    margin-left: 0;
  }

  &-price {
    color: $checkout-black;
    font-size: 24px;
    font-weight: normal;
  }
}

.checkout-form-footer {
  margin-bottom: 70px;

  .form-actions {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 20px;

    input[type='submit'] {
      width: 100%;
    }

    @include breakpoint(medium) {
      flex-direction: row-reverse;
      justify-content: space-between;
      padding-top: 40px;

      input[type='submit'] {
        margin: 0;
        width: inherit;
      }
    }

    > * {
      @include breakpoint(medium down) {
        margin-top: $global-margin;
        margin-bottom: $global-margin;
      }
    }
  }

  a {
    font-size: 14px;
    position: relative;
    color: $checkout-primary-color;
    padding-left: 1rem;

    &::before {
      content: '\f053';
      font-family: 'Font Awesome 5';
      position: absolute;
      left: 0;
      top: 0;
      font-weight: bold;
    }
  }
}

.address-container-inline {
  display: flex;

  @include breakpoint(small only) {
    flex-direction: column;
  }

  > div {
    margin-right: 1rem !important;
    width: 100%;

    &:last-child {
      margin-right: 0 !important;
    }
  }
}

// Show login link only on first step
.checkout-step-customer-information {
  .checkout-form-login-link {
    display: block;
  }
}

.checkout-pane-cpl-commerce-payment-information > legend,
.checkout-pane-cpl-commerce-checkout-shipping-method > legend,
.checkout-pane-payment-information > legend {
  display: none;
}

fieldset[id^='edit-payment-information-payment-method'] {
  margin-bottom: 20px;
}

.cpl-commerce-shipping-method .shipments-container .fieldset-wrapper,
[id^='edit-payment-information-payment-method']:not(.form-wrapper) {
  border: 1px solid $checkout-medium-gray;
  border-radius: 5px;

  input[type='radio'] {
    margin: 0;
  }

  .js-form-type-radio {
    padding-top: 16px;
    padding-left: 16px;
    padding-bottom: 14px;

    &:not(:last-child) {
      border-bottom: 1px solid $checkout-medium-gray;
    }
  }
}

.form-item-payment-information-billing-information-reuse-profile {
  margin-bottom: 10px;
}

.checkout-summary-completion {
  .form-actions {
    display: none;
  }
}

.checkout-completion-header {
  margin-bottom: $global-margin;
  position: relative;

  h5 {
    font-weight: bold;
    font-family: $checkout-font-family;
    color: $checkout-black;
  }

  .svg-success {
    width: 58px;
    height: 58px;
    position: absolute;
    top: -2px;
    left: -73px;
    @include breakpoint(xlarge down) {
      display: none;
    }
  }
}

.checkout-form-footer-completion {
  font-size: 14px;
  a {
    padding-left: 5px;

    &::before {
      content: none;
    }
  }
}

.checkout-form-order-number {
  color: $checkout-dark-gray;
}

.checkout-order-pane {
  border: 1px solid $checkout-medium-gray;
  border-radius: 5px;
  padding: $global-padding;
  margin-bottom: $global-margin;
  font-size: rem-calc(14);

  h5,
  h6 {
    font-weight: normal;
    font-family: $checkout-font-family;
    color: $checkout-black;
  }

  h6 {
    margin-top: 5px;
  }

  p:last-child {
    margin-bottom: 0;
  }

  &.collapse .columns {
    padding-right: 10px !important;

    @include breakpoint(small only) {
      margin-bottom: 15px;
    }
  }

  .icon-credit {
    width: 40px;
    height: 25px;
    border: 1px solid $checkout-credit-card-border;
    display: inline-block;
    border-radius: 3px;
    position: relative;
    top: 7px;

    .svg-credit {
      width: 22px;
      height: 15px;
      @include absolute-center;
    }
  }
}

// Completion step
.commerce-checkout-flow-cpl-commerce-checkout-4step {
  .field-name-total-price {
    .order-total-line__total {
      border: none;
    }
  }

  .checkout-summary-content {
    > h3 {
      display: none;
    }
  }

  .order-total-line__subtotal {
    border: none;
  }
}

.checkout-sms {
  color: $checkout-primary-color;
  display: inline-block;

  .fas {
    font-size: 18px;
    margin-right: 5px;
    position: relative;
    top: 2px;
  }

  a {
    border: 1px solid $checkout-medium-gray;
    border-radius: 5px;
    display: block;
    padding: 5px 10px;
    font-size: 14px;

    &:hover {
      background-color: $checkout-light-gray;
    }
  }
}

#shipping-information-wrapper {
  margin-top: 20px;
}

.checkout-pane .field--type-address .address-line2 {
  display: none;
}

.credit-card-form {
  background-color: $checkout-credit-card-background;
  border: 1px solid $checkout-form-border;
  border-radius: 5px;
  padding: 0 $global-padding 5px;
  margin-top: 15px;
  @include clearfix;

  &-title {
    position: relative;
    background-color: $white;
    margin-left: -#{$global-padding};
    margin-right: -#{$global-padding};
    margin-bottom: $global-margin;
    padding: 10px $global-padding;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;

    &::after {
      content: '';
      display: block;
      background: url('@cpl_commerce/images/cards.svg') center no-repeat;
      position: absolute;
      right: 17px;
      top: 11px;
      height: 20px;
      width: 69px;
    }
  }

  .form-item-payment-process-offsite-payment-number {
    width: 100%;
    margin-bottom: 15px;
  }

  .credit-card-form__divider {
    position: relative;
    top: 10px;
    margin-left: 10px;
    margin-right: 10px;
  }

  .credit-card-form__expiration {
    width: 60%;
    float: left;

    > div {
      float: left;
    }
  }

  .form-item-payment-process-offsite-payment-security-code {
    float: right;
    width: 35%;

    @media (max-width: 440px) {
      width: 30%;
    }
  }

  .form-item-payment-process-offsite-payment-expiration-month,
  .form-item-payment-process-offsite-payment-expiration-year {
    width: 45%;

    @media (max-width: 440px) {
      width: 40%;

      select {
        padding: 0.7rem 1.6rem 0.7rem 0.5rem !important;
        background-position: right -1.2rem center !important;
      }
    }
  }
}

.region-footer-checkout-form {
  margin-top: 20px;
  margin-bottom: 15px;

  p {
    font-size: $footer-checkout-text-size;
    color: $footer-checkout-text-color;
    margin-bottom: 0;
  }
}
