.cpl-page-header {
  background-color: $header-bg-color;

  .header-nav-commerce {
    li {
      a {
        &:focus {
          outline: none;
        }

        // Icons
        @include breakpoint(xlarge down) {
          overflow: hidden;
          text-indent: -9999px;
          display: inline-block;
          padding: 0;
          margin: 0;
        }

        &::before {
          font-family: 'Font Awesome 5';
          font-size: 18px;
          font-weight: bold;
          position: absolute;
          left: 10px;
          top: 50%;
          transform: translateY(-50%);

          @include breakpoint(xlarge down) {
            text-indent: 0;
          }
        }
      }

      // Search
      &.menu-link-item- {
        a {
          @include breakpoint(small only) {
            margin-left: 15px;
          }

          &::before {
            content: '\f002';
          }
        }
      }

      // Login / Logout
      &.menu-link-item-user-login,
      &.menu-link-item-account {
        @include breakpoint(small only) {
          display: none;
        }

        a {
          &::before {
            content: '\f007';
          }
        }
      }

      // Cart
      &.menu-link-item-cart {
        a {
          &::before {
            content: '\f07a';
          }
        }
      }
    }
  }

  .header-bottom-line {
    margin: 0;
    padding: 0;
    border-color: $header-line-color;
    border-width: 2px;
    max-width: none;
  }

  .field-type-telephone,
  .field-type-email {
    padding-left: 21px;
    position: relative;

    a {
      &::before {
        font-family: 'Font Awesome 5';
        font-weight: bold;
        position: absolute;
        left: 0;
      }
    }
  }

  .field-type-telephone {
    top: 1px;

    a {
      &::before {
        content: '\f095';
      }
    }
  }

  .field-type-email {
    a {
      &::before {
        content: '\f0e0';
        top: 1px;
      }
    }
  }
}

// Sticky header
.header-main.sticking {
  width: 100%;
  z-index: 999;
  background-color: $header-bg-color;
  height: 60px !important;
  // For fixed width
  margin: 0 !important;
  max-width: none !important;

  .header-logo {
    img {
      height: 50px !important;
    }
  }

  .header-nav-shop,
  .header-nav-commerce {
    li {
      a {
        padding-top: 1.4rem !important;
        padding-bottom: 1.4rem !important;
      }
    }
  }

  .header-nav-commerce {
    @include breakpoint(xlarge down) {
      li {
        a {
          padding-bottom: 0.7rem !important;
        }
      }
    }
  }
}

.has-sticky-header {
  // TODO: find better solution
  padding-top: 0 !important;

  .header-promo,
  .header-row-top {
    display: none;
  }
}

// Header cart badge
.menu-link-item-cart {
  position: relative;

  .js-cart-items {
    position: absolute;

    color: $white;
    font-weight: bold;
    font-size: 12px;
    height: 20px;
    width: 20px;
    line-height: 20px;
    padding: 0;
    text-indent: 0;
    border-radius: 50%;

    left: 16px;
    top: -8px;
    @include breakpoint(medium) {
      left: 19px;
      top: 5px;
    }
    @include breakpoint(large) {
      left: 19px;
      top: 13px;
    }
    @include breakpoint(xlarge) {
      left: 19px;
      top: 13px;
    }
  }
}

.sticking {
  .menu-link-item-cart {
    .js-cart-items {
      @include breakpoint(small only) {
        left: 16px;
        top: 8px;
      }
    }
  }
}
