.cpl-layout-shop-sidebar-left {
  .block-region-main {
    height: 100%;
  }

  .breadcrumbs {
    margin-top: 1.2rem;
    @include breakpoint(small only) {
      display: none;
    }
  }

  // Shop sidebar
  .sidebar {
    background-color: $white;

    // Exposed filters on sidebar content region
    .views-exposed-form {
      form {
        border-top: 1px solid $light-gray;
        border-bottom: 1px solid $light-gray;
        padding: $global-padding;
        position: relative;

        input[type='text'] {
          margin-bottom: 0;
          border: 1px solid $light-gray;

          @include placeholder {
            visibility: hidden;
            opacity: 0;
          }
        }

        label {
          transition: all 0.2s ease-in-out;
          position: absolute;
          color: $medium-gray;
          top: 20px;
          left: $global-padding;
          font-size: 18px;
          font-family: $condensed-font-family;
          padding-left: 0.7rem;
        }

        .fl-is-active,
        .fl-has-focus {
          label {
            font-size: 12px;
            top: -1px;
            left: 5px;
          }
        }

        .button {
          background: url('@cpl_commerce/images/search.svg') no-repeat center
            center;
          background-size: 20px 20px;
          width: 20px;
          height: 20px;
          font-size: 0;
          margin: 0;
          @include vertical-center;
          right: 30px;

          &:hover {
            opacity: 0.8;
            background-color: transparent;
          }

          &:focus {
            outline: none;
          }
        }
      }
    }
  }

  // Shop main content
  .main {
    background-color: $light-gray;

    // Exposed filters on main content region
    .views-exposed-form {
      margin-top: 15px;

      @include breakpoint(medium) {
        margin-top: -43px;
        margin-right: 5px;
      }

      .js-form-type-select {
        display: flex;
        justify-content: flex-end;
        padding-right: 10px;
        padding-left: 10px;

        select {
          color: $dark-gray;
          width: 190px;
          height: 36px;
          padding: 0.5rem 3.6rem 0.4rem 0.8rem;

          @include breakpoint(small only) {
            width: 170px;
          }
        }

        label {
          font-size: 14px;
          color: $dark-gray;
          margin-right: 10px;
          position: relative;
          top: 5px;
          flex-shrink: 0;
        }
      }
    }
  }
}

.view-shop-products {
  padding-left: rem-calc(10px);
  padding-right: rem-calc(10px);
  color: $black;

  a {
    color: $black;
  }

  .views-row {
    position: relative;
    margin-bottom: rem-calc(20);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;

    padding-left: rem-calc(10px);
    padding-right: rem-calc(10px);

    &:last-child {
      float: none;
    }
  }

  header {
    width: 60%;
    margin: 0 auto;
    @include breakpoint(medium only) {
      width: 75%;
    }
    @include breakpoint(small only) {
      width: 90%;
    }

    .block-title {
      text-align: center;
      margin-top: $global-margin / 3;
      margin-bottom: $global-margin;
      @include breakpoint(xlarge) {
        margin-top: $global-margin;
        margin-bottom: $global-margin;
      }
    }

    p {
      text-align: center;
      font-size: rem-calc(14);
      line-height: 1.4;
      @include breakpoint(medium) {
        font-size: rem-calc(15);
        line-height: 1.6;
      }

      &:last-child {
        padding-bottom: 30px;
      }
    }
  }
}

// Mobile menu 1
.cpl-mobile-menu {
  &.cpl-menu-type-1 {
    .mobile-view {
      .viewsreference--view-title {
        text-transform: uppercase;
        color: $white;
        margin-bottom: $global-margin;
        margin-left: 30px;
        font-weight: bold;
      }

      .view-shop-products {
        padding-left: 20px;
        padding-right: 20px;
      }

      .product-card-info {
        white-space: normal;
        background-color: transparent;
        color: $white;
        text-align: left;
        padding-left: 0;
        padding-right: 0;

        a {
          color: $white;
        }
      }

      .product-card-title {
        font-size: rem-calc(14);
      }

      .product-card-price {
        color: $white;
      }
    }
  }
}

.no-results {
  text-align: center;
  padding-top: 80px;
  padding-bottom: 100px;

  @include breakpoint(large) {
    padding-top: 170px;
    padding-bottom: 220px;
  }

  &-image {
    background: url('@cpl_commerce/images/alert.svg') no-repeat center center;
    background-size: 120px 80px;
    width: 120px;
    height: 80px;
    margin: 0 auto 30px;
  }

  &-text {
    color: $dark-gray;
    margin-bottom: 30px;
    font-size: 18px;
    line-height: 1.2;
    @include breakpoint(large) {
      font-size: 30px;
    }
  }

  &-button {
    padding: 1em 3em;
  }
}
