.paragraph--type--icntxt {
  // Styles
  &.cpl-row-style-1,
  &.cpl-row-style-2 {
    &.cpl-row-max-1,
    &.cpl-row-max-2 {
      max-width: none !important;

      .content-grid {
        margin-left: auto;
        margin-right: auto;
      }
    }

    &.cpl-row-max-1 {
      .content-grid {
        max-width: $global-width !important;
      }
    }

    &.cpl-row-max-2 {
      .content-grid {
        max-width: $global-width / 2 !important;
      }
    }
  }

  .content-box {
    display: flex !important;
    padding: $global-padding;

    @include breakpoint(medium only) {
      flex-direction: column;
      text-align: center;
    }

    @include breakpoint(small only) {
      flex-direction: row;
      margin-bottom: $global-padding;
    }
  }

  .field-type-image {
    @include breakpoint(medium only) {
      margin: 0 auto;
    }
  }

  .medium-12 {
    .field-type-image {
      @include breakpoint(medium only) {
        margin: 0 auto;
      }
    }
  }

  .text-box-row {
    padding: 0 0 $global-padding $global-padding;
    @include breakpoint(medium) {
      padding: $global-padding $global-padding 0 $global-padding;
    }
  }

  .image-box-row {
    @include breakpoint(small only) {
      align-self: flex-start;
    }
  }

  // Alignment
  &.cpl-position-l,
  &.cpl-position-r {
    .text-box-row {
      padding-top: 0;
      display: flex;
      align-items: center;
    }
  }

  &.cpl-position-l {
    .content-box {
      @include breakpoint(large) {
        flex-direction: row-reverse;
      }
    }
  }

  &.cpl-position-t,
  &.cpl-position-b {
    .content-box {
      align-items: center;
    }

    .text-box-row {
      @include breakpoint(medium) {
        text-align: center;
      }
    }
  }

  &.cpl-position-t {
    .content-box {
      @include breakpoint(medium) {
        flex-direction: column-reverse;
      }
    }

    .text-box-row {
      @include breakpoint(medium) {
        padding-top: 0;
        padding-bottom: $global-padding;
      }
    }
  }

  &.cpl-position-b {
    .content-box {
      @include breakpoint(large) {
        flex-direction: column;
      }
    }
  }

  &.cpl-position-c {
    .content-box {
      // TODO: create this style using JS
    }
  }

  &.cpl-row-max-2 {
    .content-box {
      @include breakpoint(medium) {
        flex-direction: column;
      }
    }
  }

  &.cpl-row-style-1,
  &.cpl-row-style-3 {
    .columns {
      padding: 0;
    }

    .content-box {
      margin-bottom: 0;
    }

    .text-box-row {
      @include breakpoint(medium only) {
        padding-top: $global-padding;
      }
    }
  }

  // Row style 2
  &.cpl-row-style-2 {
    .content-box {
      @include breakpoint(medium) {
        margin-top: $global-padding;
        margin-bottom: $global-padding;
      }

      @include breakpoint(small only) {
        margin-left: $global-padding * 2;
        margin-right: $global-padding * 2;
        margin-bottom: $global-padding * 2;
      }
    }

    .text-box-row {
      @include breakpoint(medium only) {
        padding-top: $global-padding;
      }
    }

    .content-grid-item:nth-child(2) {
      .content-box {
        @include breakpoint(medium) {
          margin-top: 0;
          margin-bottom: 0;
          padding-top: $global-padding * 2;
          padding-bottom: $global-padding * 2;
        }

        @include breakpoint(small only) {
          margin-left: 0;
          margin-right: 0;
          padding-left: $global-padding * 3;
          padding-right: $global-padding * 3;
        }
      }
    }
  }

  // Row style 4
  &.cpl-row-style-4 {
    .content-grid-item {
      padding: 0;
    }

    .content-box {
      flex-direction: row;
      padding: 0;
      margin-bottom: 0;
    }

    h4 {
      font-family: $condensed-font-family;
      font-weight: normal;
    }

    .text-box-row,
    .image-box-row {
      @include grid-column(6, 0);
      display: flex;
      justify-content: center;
      align-items: center;
      padding: $text-block-padding $global-padding;
      @include breakpoint(xlarge) {
        padding-top: $text-block-padding * 2;
        padding-bottom: $text-block-padding * 2;
      }
    }

    .text-box-container {
      @include grid-column(10, 0);

      @include breakpoint(large) {
        @include grid-column(8, 0);
      }

      @include breakpoint(xlarge) {
        @include grid-column(5, 0);
      }
    }

    .large-4,
    .xlarge-4 {
      .text-box-container {
        @include breakpoint(xlarge) {
          @include grid-column(10, 0);
        }
      }
    }

    // Alternating
    &.cpl-alternating {
      @include breakpoint(small only) {
        .content-grid-item {
          &:nth-child(2n) {
            &.small-12 {
              .content-box {
                flex-direction: row-reverse;
              }
            }
          }
        }
      }

      @include breakpoint(medium only) {
        .content-grid-item {
          &:nth-child(2n) {
            &.medium-12 {
              .content-box {
                flex-direction: row-reverse;
              }
            }
          }

          &:nth-child(4n - 2),
          &:nth-child(4n - 3) {
            &.medium-6 {
              .content-box {
                flex-direction: row-reverse;
              }
            }
          }
        }
      }

      @include breakpoint(large) {
        .content-grid-item {
          &:nth-child(2n) {
            &.large-12 {
              .content-box {
                flex-direction: row-reverse;
              }
            }
          }

          &:nth-child(4n - 2),
          &:nth-child(4n - 3) {
            &.large-6 {
              .content-box {
                flex-direction: row-reverse;
              }
            }
          }
        }
      }
    }

    &.cpl-position-l {
      .content-box {
        flex-direction: row-reverse;
      }
    }

    &.cpl-position-r {
      .content-box {
        flex-direction: row;
      }
    }

    &.cpl-position-c {
      .text-box-row {
        text-align: center;
      }
    }
  }
}
