@if ($margin-quantity != 0) {
  @for $i from 1 through $margin-quantity + 1 {
    @each $side in $margin-sides {
      $size: '';
      $i-size: strip-unit($base-size) * $i;

      @if ($use-rem == true) {
        $size: rem-calc($i-size);
      } @else {
        $size: #{$i-size}px;
      }

      @if ($base-prefix != '') {
        .#{$base-prefix}-m-#{str-slice($side, 0, 1)}-#{$i} {
          margin-#{$side}: #{$size} !important;
        }
        .#{$base-prefix}-rp-#{$i} .content-grid > .field-item:not(:last-child) {
          margin-bottom: #{$size} !important;
        }
      } @else {
        .m-#{str-slice($side, 0, 1)}-#{$i} {
          margin-#{$side}: #{$size} !important;
        }
        .rp-#{$i} .content-grid > .field-item:not(:last-child) {
          margin-bottom: #{$size} !important;
        }
      }
    }
  }
}
@if ($padding-quantity != 0) {
  @for $i from 1 through $padding-quantity + 1 {
    @each $side in $padding-sides {
      $size: '';
      $i-size: strip-unit($base-size) * $i;

      @if ($use-rem == true) {
        $size: rem-calc($i-size);
      } @else {
        $size: #{$i-size}px;
      }

      @if ($base-prefix != '') {
        .#{$base-prefix}-p-#{str-slice($side, 0, 1)}-#{$i} {
          padding-#{$side}: #{$size} !important;
        }
      } @else {
        .p-#{str-slice($side, 0, 1)}-#{$i} {
          padding-#{$side}: #{$size} !important;
        }
      }
    }
  }
}
@if (length($text-colors) != 0) {
  @for $i from 1 through length($text-colors) {
    @if ($base-prefix != '') {
      .#{$base-prefix}-text-color-#{$i} {
        color: nth($text-colors, $i) !important;
      }
    } @else {
      .text-color-#{$i} {
        color: nth($text-colors, $i) !important;
      }
    }
  }
}
@if (length($bg-colors) != 0) {
  @for $i from 1 through length($bg-colors) {
    @if ($base-prefix != '') {
      .#{$base-prefix}-bg-color-#{$i} {
        background-color: nth($bg-colors, $i) !important;
      }
    } @else {
      .bg-color-#{$i} {
        background-color: nth($bg-colors, $i) !important;
      }
    }
  }
}

@each $i, $width in $row-widths {
  @if ($base-prefix != '') {
    .#{$base-prefix}-row-max-#{$i},
    .#{$base-prefix}-text-row-max-#{$i} .text-box-row {
      max-width: #{$width} !important;
      margin-left: auto !important;
      margin-right: auto !important;
    }
  } @else {
    .row-max-#{$i},
    .text-row-max-#{$i} .text-box-row {
      max-width: #{$width} !important;
      margin-left: auto !important;
      margin-right: auto !important;
    }
  }
}

// Icon sizes
@if (length($icon-sizes) != 0) {
  @for $i from 1 through length($icon-sizes) {
    @if ($base-prefix != '') {
      .#{$base-prefix}-icon-size-#{$i} {
        .image-box-row .field-type-image {
          width: nth($icon-sizes, $i) !important;
          flex: 0 0 auto;
        }
      }
    } @else {
      .icon-size-#{$i} {
        .image-box-row .field-type-image {
          width: nth($icon-sizes, $i) !important;
          flex: 0 0 auto;
        }
      }
    }
  }
}

// Masonry
@if (length($masonry-gap) != 0) {
  @for $i from 1 through length($masonry-gap) {
    @if ($base-prefix != '') {
      .#{$base-prefix}-masonry-gap-#{$i} {
        .content-grid-item {
          padding: nth($masonry-gap, $i) / 2 !important;
        }
      }
    } @else {
      .masonry-gap-#{$i} {
        .content-grid-item {
          padding: nth($masonry-gap, $i) / 2 !important;
        }
      }
    }
  }
}

// Predefined colors
.text-color-light-gray {
  color: $light-gray;
}

.text-color-medium-gray {
  color: $medium-gray;
}

.text-color-dark-gray {
  color: $dark-gray;
}

.text-color-black {
  color: $black;
}

.text-color-white {
  color: $white;
}

.bg-color-light-gray {
  background-color: $light-gray;
}

.bg-color-medium-gray {
  background-color: $medium-gray;
}

.bg-color-dark-gray {
  background-color: $dark-gray;
}

.bg-color-black {
  background-color: $black;
}

.bg-color-white {
  background-color: $white;
}
