.cpl-page-footer {
  &.cpl-footer-type-1 {
    .footer-wide-top {
      @include breakpoint(small only) {
        padding-top: 35px;
        padding-bottom: 35px;
      }
    }

    .footer-contact-block {
      text-align: center;

      .footer-title {
        margin-top: 18px;
      }

      .footer-title-links {
        margin-top: 35px;
      }

      @include breakpoint(small only) {
        & > * {
          display: none;
        }

        .footer-links {
          display: block;
        }
      }
    }

    .footer-menu-block {
      text-align: center;
    }

    .footer-contact-item {
      display: inline-block;
      text-align: center;

      a {
        &::before {
          content: none;
        }
      }

      .field-type-telephone {
        padding: 0;
      }
    }
  }
}
