body.age-check {
  overflow: hidden !important;
  position: fixed !important;
  height: 100%;
}

.prompt {
  background-color: rgba($white, 0.92);
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 1002;
}

.prompt-age-check {
  max-width: 650px;
  height: auto;
  margin: 0 auto;
  padding: 20px 15px 30px 15px;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1001;
  text-align: center;

  p {
    color: $black;
    padding-bottom: 1.75rem;
    font-size: 1.125rem;
  }

  h2 {
    color: $black;
    font-size: 28px;
    margin-top: 15px;
  }

  .field-name-field-block-logo {
    max-width: 120px;
    margin: 0 auto;
  }

  .prompt-buttons {
    text-align: center;

    .button {
      width: 240px;
      margin: 0.2rem 0.5rem;
    }
  }
}
