.cpl-page-footer {
  &.cpl-footer-type-2 {
    .footer-wide-top {
      @include breakpoint(small only) {
        padding-top: 35px;
        padding-bottom: 35px;
      }
    }

    .footer-contact-block {
      .footer-title {
        margin-top: 11px;
      }

      @include breakpoint(small only) {
        & > * {
          display: none;
        }

        .footer-links {
          display: block;
        }
      }
    }

    .footer-contact-item {
      @include breakpoint(medium only) {
        .field-type-telephone,
        .field-type-email {
          padding-left: 0;
        }

        a {
          &::before {
            content: none;
          }
        }
      }
    }

    .footer-links {
      @include breakpoint(small only) {
        text-align: center;
      }
    }

    .footer-title-links {
      margin-top: 20px;
    }
  }
}
