.cpl-contact-page-2 {
  .page-container {
    @include grid-row;
    @include grid-column-row(0);

    @include breakpoint(large) {
      display: flex;
    }
  }

  .main {
    padding-left: $global-padding !important;
    padding-right: $global-padding !important;
    @include breakpoint(large) {
      @include grid-col-size(7);
      margin: 0;
      left: 0;
      order: 1;
    }

    @include breakpoint(xlarge) {
      padding-left: 80px !important;
    }

    @include breakpoint(medium down) {
      padding-bottom: 50px;
    }
  }

  .sidebar {
    background-color: $contact-sidebar-bg-color;
    color: $contact-sidebar-text-color;

    @include breakpoint(large) {
      @include grid-col-size(5);
      margin: 0;
      left: 0;
      @if $contact-sidebar-shadow == true {
        box-shadow: 4px 0px 5px 0px rgba(0, 0, 0, 0.2);
      }
    }

    @include breakpoint(medium down) {
      padding-left: 0 !important;
      padding-right: 0 !important;
      text-align: center;
    }

    a {
      color: $contact-sidebar-link-color;

      &:hover {
        color: $contact-sidebar-link-color-h;
      }
    }

    &::before {
      @include breakpoint(large) {
        content: '';
        background-color: $contact-sidebar-bg-color;
        display: block;
        position: absolute;
        left: -50vw;
        width: 50vw;
        height: 100%;
      }
    }

    section {
      padding: 50px $global-padding;
    }
  }
}
