.cpl-contact-page-3 {
  .main {
    @include grid-row;
    padding-left: 20px;
    padding-right: 20px;
  }

  .webform-submission-form {
    padding-bottom: 50px;

    @include breakpoint(large) {
      .form-item-name,
      .form-item-email {
        width: 49%;
      }

      .form-item-name {
        float: left;
      }

      .form-item-email {
        float: right;
      }
    }

    .webform-button--submit {
      display: block;
      width: inherit;
      margin-left: auto;
      margin-right: auto;
    }
  }

  .cpl-contact-page-block {
    padding-bottom: 40px;
    padding-top: 40px;

    background-color: $contact-sidebar-bg-color;
    color: $contact-sidebar-text-color;

    margin-left: calc(-50vw + 50%);
    margin-right: calc(-50vw + 50%);
    max-width: none;

    a {
      color: $contact-sidebar-link-color;

      &:hover {
        color: $contact-sidebar-link-color-h;
      }
    }

    .field-name-body {
      @include grid-row;
      padding-left: 25px;
      padding-right: 25px;

      p:last-child {
        margin-bottom: 0;
      }
    }
  }
}
