.cpl-page-header {
  &.cpl-header-type-4 {
    a {
      critical: this;
      color: $header-text-color;
    }

    .header-main {
      @include grid-row;
      display: flex;
      justify-content: space-between;
      align-items: center;

      height: 60px;
      @include breakpoint(medium) {
        height: 80px;
      }
      @include breakpoint(large) {
        height: 130px;
      }

      @include breakpoint(large down) {
        padding-left: $global-margin;
        padding-right: $global-margin;
      }
    }

    .header-logo {
      img {
        width: auto;
        height: 100px;
        @include breakpoint(medium only) {
          height: 65px;
        }
        @include breakpoint(small only) {
          height: 50px;
        }
      }

      @include breakpoint(small only) {
        @include horizontal-center;
      }
    }

    .header-button {
      .button {
        font-size: rem-calc(20);
        text-transform: none;
        margin-bottom: 0;

        @include breakpoint(medium only) {
          padding: 0.3em 0.8em;
          font-size: rem-calc(16);
        }

        &.primary {
          .fas {
            border: 2px solid lighten($primary-color, 30%);
            border-radius: 50%;
            padding: 5px;
            color: lighten($primary-color, 30%);
            font-size: rem-calc(18);
            position: relative;
            left: -5px;
          }
        }
      }
    }

    .header-content {
      margin-right: auto;
      margin-left: 30px;
    }

    .header-nav-main {
      li {
        a {
          color: $header-text-color;
          font-size: rem-calc(18);

          &:hover {
            color: $header-text-color-h;
          }
        }
      }

      @include breakpoint(large down) {
        display: none;
      }
    }

    .header-hamburger {
      height: 60px;
      width: 60px;
      display: none;
      position: relative;
      top: 5px;

      @include breakpoint(large down) {
        display: block;
      }
    }

    .header-promo {
      background-color: $header-promo-bg-color;
      color: $header-promo-text-color;
      text-align: center;
      padding-top: 10px;
      padding-bottom: 10px;
      font-size: $header-font-size-promo;
      text-transform: uppercase;
      box-shadow: 0px 10px 20px 0px rgba(0, 0, 0, 0.25);
      position: relative;
      z-index: 1;

      @include breakpoint(small only) {
        padding-top: 5px;
        padding-bottom: 5px;
      }

      a {
        color: $header-promo-text-color;
        text-decoration: underline;
      }

      p {
        margin: 0;
      }
    }

    // Dropdown submenu
    .dropdown {
      .submenu {
        background-color: $header-submenu-bg-color;
        border: none;
        border-radius: 8px;
        filter: $header-submenu-shadow;
        z-index: 2;

        li {
          a {
            padding: 1.3rem 1.3rem;
            color: $header-submenu-text-color !important;
            transition: all ease 0.15s;

            &:hover {
              background-color: $header-submenu-bg-color-h;
              color: $header-submenu-text-color-h !important;
            }
          }

          &:first-child {
            a {
              &::before {
                @include css-triangle(16px, $header-submenu-bg-color, up);
                @include horizontal-center;
                transition: all ease 0.15s;
                top: -12px;
              }

              &:hover {
                border-top-left-radius: 8px;
                border-top-right-radius: 8px;

                &::before {
                  @include css-triangle(16px, $header-submenu-bg-color-h, up);
                }
              }
            }
          }

          &:last-child {
            a {
              &:hover {
                border-bottom-left-radius: 8px;
                border-bottom-right-radius: 8px;
              }
            }
          }
        }

        &.is-dropdown-submenu {
          top: 140% !important;
          left: -25% !important;
        }
      }
    }

    // Override default triangle that mark the submenu
    .dropdown.menu > li.is-dropdown-submenu-parent > a::after {
      border-color: $header-text-color transparent transparent !important;
    }

    // Hide bottom line
    .header-bottom-line {
      display: none;
    }
  }

  &.cpl-header-transparent {
    background-color: transparent !important;
    position: relative;

    .header-main {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      z-index: 1;
      width: 100%;

      &.not-sticking {
        position: absolute !important;
      }
    }

    .header-bottom-line {
      display: none;
    }
  }
}

// Sticky header
.has-sticky-header {
  .cpl-header-type-4 {
    .header-main {
      padding: 0 15px;
      z-index: 999;
    }

    .header-row-top {
      display: block;
    }

    .header-button {
      .button {
        font-size: 1rem;
        padding: 0.3em 0.8em;
      }
    }

    .header-nav-main li a {
      font-size: 17px;
    }
  }
}
