.product {
  &-wrapper {
    padding-bottom: 45px;

    @include breakpoint(small only) {
      padding-left: $global-padding;
      padding-right: $global-padding;
    }

    .field-name-title {
      font-family: $header-font-family;
      font-size: rem-calc(28);
      color: $black;
    }
  }

  &-tabs {
    padding-bottom: 60px;

    .vertical {
      border: none;
    }

    .tabs-title {
      position: relative;
      border-bottom: 2px solid $medium-gray;
      transition: all ease 0.3s;

      a {
        color: $dark-gray !important;
        font-size: 16px;
        font-family: $condensed-font-family;
        text-transform: uppercase;
        padding: 22px 24px 20px 0;
        background-color: transparent;

        &:focus,
        &:active {
          background-color: transparent;
        }
      }

      &.is-active {
        border-bottom: 2px solid $dark-gray;

        &::after {
          content: '\f054';
          position: absolute;
          right: 0;
          top: 50%;
          transform: translateY(-50%);
          display: block;
          color: $dark-gray;
          font-family: 'Font Awesome 5';
          font-weight: bold;
        }
      }
    }

    .tabs-panel {
      padding: 0;
    }

    .tabs-content {
      color: $dark-gray;
      font-size: rem-calc(16);
      border: none;
      min-height: 200px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding-top: 25px;
      padding-bottom: 25px;

      p:last-child {
        margin: 0;
      }

      a {
        color: $primary-color;

        &:hover {
          color: $black;
        }
      }
    }

    .accordion-item {
      border-top: 2px solid $light-gray;

      &:last-child {
        border-bottom: 2px solid $light-gray;

        .accordion-title {
          border: none;
        }

        .accordion-content {
          border: none;
        }
      }
    }

    // Table inside the acccordion tab.
    table {
      thead {
        background-color: $light-gray;
        border: 2px solid $medium-gray;
        color: $dark-gray;
      }

      tbody {
        border: 2px solid $medium-gray;
      }

      th,
      td {
        border: 2px solid $medium-gray;
        padding-top: 4px;
        padding-bottom: 4px;
        text-align: center;

        @include breakpoint(small only) {
          padding: 0.4rem;
          font-size: 14px;
        }
      }
    }
  }

  &-tabs-right {
    @include breakpoint(small only) {
      padding-left: 0;
      padding-right: 0;
    }
  }

  .product-gallery {
    display: flex;

    @include breakpoint(large up) {
      flex-direction: column;
    }

    @include breakpoint(large down) {
      margin-top: $global-margin;
      margin-bottom: $global-margin;
    }
  }

  .product-gallery-image {
    margin-right: -2px;
    @include breakpoint(large) {
      margin-bottom: -2px;
      margin-right: 0;
    }

    img {
      border: 2px solid $medium-gray;
      max-height: 190px;
    }
  }

  .cloudzoom {
    width: 100%;
  }

  .cloudzoom-gallery {
    position: relative;
  }

  hr {
    margin-bottom: 0;
    @include breakpoint(small only) {
      display: none;
    }
  }

  .field-name-field-media-video-embed-field {
    @include breakpoint(medium) {
      padding-top: 20px;
    }
  }

  .field-name-body {
    margin-bottom: 20px;
  }

  .field-name-field-cbd-brand,
  .field-name-field-product-type,
  .field-name-field-product-category,
  .field-name-field-product-volume,
  .field-name-field-product-quantity,
  .field-name-field-product-weight,
  .variation-strength {
    .field-label {
      display: inline-block;
      font-weight: bold;
      float: left;
      margin-right: 5px;
      margin-bottom: 10px;

      &::after {
        content: ':';
      }
    }

    .field-items {
      display: inline-block;
    }
  }
}

.product-price {
  h1 {
    margin-bottom: rem-calc(16);
  }

  .field-name-price {
    font-size: $lead-font-size;
    line-height: $lead-lineheight;
    color: $dark-gray;
    margin-bottom: 15px;
  }
}

// Add to cart field
.commerce-order-item-add-to-cart-form {
  label {
    display: none;
  }

  select {
    background-color: $light-gray;
    border: none;
    height: 44px;
    min-width: 137px;
  }

  select:disabled {
    display: none;
  }

  .form-submit {
    padding: 0.75em 1em;
    @include button-style($primary-color, $black, $white);
    font-size: 17px;
  }

  .field--name-purchased-entity {
    display: inline-block;
  }
}

// Breadcrumbs on product page
.path-product {
  .breadcrumbs {
    margin-left: 0;
  }
}

// Back in stock
#cpl-back-in-stock-form-wrapper {
  font-size: rem-calc(13);
  color: $dark-gray;
  line-height: 1.3;

  .button {
    display: block;
    background-color: $white;
    color: $dark-gray;
    border: 1px solid $medium-gray;
    padding: 5px 10px;
    font-size: rem-calc(13);
    margin-top: 5px;

    &:hover {
      background-color: $light-gray;
    }
  }
}

// Message after Ajax call
.back-in-stock-message {
  font-size: rem-calc(14);
  color: $dark-gray;
  line-height: 1.3;
}

// Lab results
.product-info-lab-results {
  .views-row:last-child {
    float: left;
  }

  .field-type-image {
    img {
      border: 2px solid $light-gray;
    }

    .field-item {
      a {
        display: block;
        position: relative;
        z-index: 0;

        &::before {
          content: '';
          background-color: rgba(0, 0, 0, 0);
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          z-index: 1;
          display: block;
          transition: all ease 0.3s;
        }

        &:hover {
          &::before {
            background-color: rgba(0, 0, 0, 0.5);
          }

          &::after {
            content: '\f00e';
            z-index: 2;
            @include absolute-center;
            font-family: 'Font Awesome 5';
            font-size: 28px;
            font-weight: bold;
            color: rgba(255, 255, 255, 0.8);
          }
        }
      }
    }
  }

  .field-media--name {
    font-size: 11px;
    line-height: 1.2;
    text-align: center;
    width: 80%;
    margin: 5px auto 20px;
  }
}

// Extends
%product-price-badge {
  background-color: $light-gray;
  color: $black;
  font-weight: bold;
  display: inline-block;
  position: relative;
  height: 40px;
  line-height: 40px;
  padding-left: 15px;
  padding-right: 30px;

  &::before {
    content: '';
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 20px 0 20px 15px;
    border-color: transparent transparent transparent $light-gray;
    position: absolute;
    right: -15px;
    top: 0;
    bottom: 0;
  }

  &::after {
    content: '';
    border-radius: 50%;
    background-color: $white;
    height: 10px;
    width: 10px;
    position: absolute;
    right: -2px;
    top: 15px;
  }
}

%product-review-one-line {
  @include breakpoint(xlarge) {
    display: flex;

    .stars {
      margin-right: 15px;
    }
  }
}

%product-active-gallery-left {
  &::after {
    position: absolute;
    z-index: 499;

    @include breakpoint(large) {
      @include css-triangle(12px, $medium-gray, left);
      left: -12px;
      top: 50%;
      transform: translateY(-50%);
    }

    @include breakpoint(medium down) {
      @include css-triangle(12px, $medium-gray, up);
      left: 50%;
      top: -12px;
      transform: translateX(-50%);
    }
  }
}

%product-active-gallery-right {
  &::after {
    position: absolute;
    z-index: 499;

    @include breakpoint(large) {
      @include css-triangle(12px, $medium-gray, right);
      right: -12px;
      top: 50%;
      transform: translateY(-50%);
    }

    @include breakpoint(medium down) {
      @include css-triangle(12px, $medium-gray, up);
      left: 50%;
      top: -12px;
      transform: translateX(-50%);
    }
  }
}

%product-select-light {
  select,
  option {
    border: 1px solid $medium-gray;
    background-color: $white;
  }
}

%product-field-number-light {
  input,
  .quantity-button {
    background-color: $white;
    border: 1px solid $medium-gray;
  }

  input {
    margin-left: -2px;
  }

  .quantity-up {
    right: 4px !important;
  }
}
