// Comments field
.comments {
  hr {
    margin-left: calc(-50vw + 50%);
    margin-right: calc(-50vw + 50%);
    max-width: none;
    border-color: $primary-color;
    margin-top: 25px;
    margin-bottom: 25px;
  }
}

.comments-content {
  @include breakpoint(medium) {
    padding-bottom: 40px;
    padding-top: 25px;
  }
}

.comments-article {
  .comments-header {
    margin-top: 70px;
    margin-bottom: 30px;

    h3 {
      text-align: center;
      color: $black;
    }
  }

  .form-actions {
    margin-top: $global-margin;
  }

  .filter-wrapper,
  .form-item-subject-0-value {
    display: none;
  }
}

// Single comment
.comment-form {
  #edit-preview {
    display: none;
  }

  .button {
    font-weight: 500;
    font-family: $body-font-family;
    text-transform: uppercase;
    font-size: rem-calc(17);
    @include button-style($primary-color, $black, $white);
    @include button-expand(true);
  }

  input[type='text'],
  textarea {
    box-shadow: none;
  }

  label {
    color: $dark-gray;
  }
}

.comment-article {
  border-top: 1px solid $light-gray;
  padding-top: $global-padding;
  padding-bottom: 5px;

  .comment-submitted {
    margin-bottom: $global-margin;
  }

  .comment-submitted-author {
    font-weight: bold;
    font-size: rem-calc(18);
    color: $black;

    a {
      color: $black;

      &:hover {
        color: $primary-color;
      }
    }
  }

  .comment-submitted-date {
    color: $dark-gray;
    font-size: rem-calc(14);
  }

  .more-link {
    color: $primary-color;
    position: relative;
    bottom: 5px;
  }

  .text-expand--summary {
    p:last-child {
      &::after {
        content: '...';
      }
    }
  }
}
