.cpl-mobile-menu {
  &.cpl-menu-type-3 {
    .mobile-menu {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      overflow-y: auto;
      background: $black;
      background: linear-gradient(
        to right,
        lighten($black, 5%) 0%,
        $black 100%
      );
    }

    .accordion-menu {
      margin-top: 30px;

      > li {
        text-indent: 20px;
      }
    }

    .menu {
      li {
        display: block;
      }

      a {
        color: $white;
        font-family: $body-font-family;

        &:hover {
          color: $white;
          background-color: lighten($black, 10%);
        }
      }
    }

    .is-accordion-submenu {
      margin-left: 40px;
    }

    .is-submenu-item {
      a {
        display: block;
        padding-top: 4px;
        padding-bottom: 4px;
      }
    }

    // Triangle marker
    .is-accordion-submenu-parent > a::after {
      border-color: $white transparent transparent;
      right: 24px;
    }

    .bordered {
      border-bottom: 1px solid lighten($black, 10%);
      padding-bottom: 15px;
      margin-bottom: 15px;
    }

    .close-button {
      right: 35px;
      top: 15px;
      color: $white;
    }
  }
}
