.search-popup-block {
  position: relative;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  overflow: hidden;
  z-index: 1001;
  height: 100%;
  width: 100%;

  &.is-active {
    position: fixed;
  }

  .search-container {
    background-color: $search-popup-background-color;
    color: $search-popup-text-color;
    position: absolute;
    overflow: hidden;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
  }

  // Slide from top animation
  &.animate-slide-top {
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.5s, visibility 0s 0.5s;

    .search-container {
      perspective: 1200px;
    }

    .search-inner {
      opacity: 0.4;
      transform: translateY(-25%) rotateX(35deg);
      transition: transform 0.5s, opacity 0.5s;
    }

    &.is-active {
      opacity: 1;
      visibility: visible;
      transition: opacity 0.5s;

      .search-inner {
        opacity: 1;
        transform: rotateX(0deg);
      }
    }

    &.is-closing {
      opacity: 0;
      visibility: hidden;
      transition: opacity 0.5s, visibility 0s 0.5s;

      .search-inner {
        transform: translateY(25%) rotateX(-35deg);
      }
    }
  }

  // Perspective animation
  &.animate-perspective {
    .search-container {
      transition: transform 300ms ease-in-out;
      transform: translateX(100%) rotateY(-100deg) translateZ(-50px);
      transform-origin: 50% 50%;
    }

    &.is-active {
      perspective: 1500px;

      .search-container {
        transform: translateX(0) rotateY(0deg) translateZ(0);
        transition: transform 500ms ease-in-out;
      }
    }

    &.is-closing {
      .search-container {
        transform: translateX(100%) rotateY(-100deg) translateZ(-50px);
      }
    }
  }

  .search-inner {
    margin-top: 25%;
    width: 90%;
    @include breakpoint(medium) {
      margin-top: 15%;
      width: 80%;
    }
    @include breakpoint(large) {
      width: 900px;
    }
  }

  input[type='search'] {
    border-left: none;
    border-right: none;
    border-top: none;
    border-bottom: 2px solid $search-popup-input-border-color;
    box-shadow: none;
    color: $search-popup-text-color;
    background-color: transparent;
    border-radius: 0;
    padding: 0;
    font-family: $header-font-family;
    @include placeholder {
      color: $search-popup-text-color;
    }
    font-size: rem-calc(22);
    height: rem-calc(40);
    @include breakpoint(medium) {
      font-size: rem-calc(36);
      height: rem-calc(60);
    }

    @include breakpoint(large) {
      font-size: rem-calc(44);
      height: rem-calc(80);
    }
  }

  .close-button {
    color: $search-popup-text-color;
    font-size: 40px;
    right: 13px;
    top: 7px;
    @include breakpoint(large) {
      font-size: 48px;
      right: 40px;
      top: 30px;
    }

    &:focus {
      outline: none;
    }

    &:hover {
      color: $search-popup-text-color-hover;
    }
  }

  .search-popular {
    text-align: center;
    margin-top: 30px;

    &-title {
      color: $search-popup-text-color;
      font-size: rem-calc(22);
      @include breakpoint(medium) {
        font-size: rem-calc(36);
      }
      @include breakpoint(large) {
        font-size: rem-calc(44);
      }
    }

    ul {
      margin: 0;
      padding: 0;
      font-size: rem-calc(22);
      @include breakpoint(medium) {
        font-size: rem-calc(32);
      }
    }

    li {
      list-style-type: none;
      margin-left: 0;
      font-family: $condensed-font-family;
      font-weight: 300;
    }
  }

  .search-result {
    a {
      display: flex;
      align-items: center;
      margin-bottom: 10px;
      background-color: $search-popup-result-background-color;
      color: $search-popup-result-text-color;
      border: 1px solid $search-popup-background-color;

      &:hover {
        border-color: $search-popup-text-color-hover;
      }
    }

    &-img {
      padding-top: 10px;
      padding-bottom: 10px;
      width: 110px;
      height: 110px;
      margin-right: 20px;
      object-fit: contain;

      @include breakpoint(small only) {
        padding-top: 5px;
        padding-bottom: 5px;
        width: 70px;
        height: 70px;
        margin-right: 10px;
      }
    }
  }

  .easy-autocomplete {
    width: initial !important;
  }

  .easy-autocomplete-container {
    ul {
      background-color: $search-popup-background-color;
      border: none;
      padding: 0;
      margin-left: 0;

      li {
        border: none;
        padding: 0;

        &:last-child {
          background-color: transparent;
        }
      }

      // Se all results text link
      .eac-item {
        & > a {
          color: $search-popup-link-color;

          &:hover {
            background: transparent;
            color: $search-popup-link-color-hover;
          }
        }
      }
    }
  }

  .search-view-all {
    text-align: center;
    font-size: 18px;
    position: absolute;
    bottom: 15px;
    left: 0;
    right: 0;
    transition: all ease 0.3s;

    a {
      color: $search-popup-link-color !important;

      &:hover {
        color: $search-popup-link-color-hover !important;
      }
    }
  }

  .form-container {
    position: relative;
  }

  .button-search,
  .button-clear {
    color: $search-popup-text-color;
    cursor: pointer;
    transition: all ease 0.3s;
    position: absolute;
    z-index: 1;

    &:hover {
      color: $search-popup-text-color-hover;
    }

    &:focus {
      outline: none;
    }
  }

  .button-search {
    top: 9px;
    right: 3px;
    font-size: 20px;
    @include breakpoint(medium) {
      font-size: 26px;
      top: 15px;
    }
    @include breakpoint(large) {
      font-size: 38px;
      top: 20px;
    }
  }

  .button-clear {
    left: -40px;
    font-size: 38px;
    top: 21px;

    @include breakpoint(medium only) {
      top: 11px;
    }

    @include breakpoint(small only) {
      display: none !important;
    }
  }
}
