.slick-slider {
  .slick-slide {
    outline: none;
  }
}

// Dots
.slick-dots {
  @include horizontal-center;
  margin: 0;

  button {
    position: relative;
    width: map-get($slideshow-dot, size) !important;
    height: map-get($slideshow-dot, size) !important;
    border: 2px solid map-get($slideshow-dot, color-dark);
    background-color: transparent !important;
    opacity: 1 !important;
    border-radius: 50%;
    font-size: 0;
    cursor: pointer;

    &:focus {
      outline: none;
    }
  }

  li {
    list-style-type: none;
    display: inline-block;
    margin-left: 7px;
    margin-right: 7px;

    &.slick-active {
      button {
        background-color: transparent !important;

        &::before {
          @include absolute-center;
          content: '';
          display: block;
          height: map-get($slideshow-dot, size) - 11px;
          width: map-get($slideshow-dot, size) - 11px;
          background-color: map-get($slideshow-dot, color-dark);
          border-radius: 50%;
        }
      }
    }
  }
}

// Arrows
.slick-arrow {
  @include vertical-center;
  cursor: pointer;
  font-size: 0;
  z-index: 5;
  outline: none;
  transition: opacity ease 0.2s;

  @if ($slideshow-arrow-container != 0) {
    @each $property, $value in $slideshow-arrow-container {
      #{$property}: $value;
    }
  }

  &::after {
    font-family: 'Font Awesome 5';
    color: map-get($slideshow-arrow, color-dark);
    font-weight: map-get($slideshow-arrow, font-weight);
    font-size: map-get($slideshow-arrow, font-size);
    position: relative;
  }

  &:hover {
    opacity: 0.8;
  }
}

.slick-prev {
  left: 50%;
  margin-left: -50vw;
  padding-left: map-get($slideshow-arrow-container, margin);
  padding-top: 20px;
  padding-bottom: 20px;
  padding-right: 20px;

  &::after {
    content: map-get($slideshow-arrow, prev-symbol);
  }
}

.slick-next {
  right: 50%;
  margin-right: -50vw;
  padding-right: map-get($slideshow-arrow-container, margin);
  padding-top: 20px;
  padding-bottom: 20px;
  padding-left: 20px;

  &::after {
    content: map-get($slideshow-arrow, next-symbol);
  }
}

// Arrows inside articles should not stick to viewport sides
.article-container {
  .slick-prev {
    left: -35px;
    margin-left: 0;

    @include breakpoint(small only) {
      left: -15px;
    }
  }

  .slick-next {
    right: -35px;
    margin-right: 0;

    @include breakpoint(small only) {
      right: -15px;
    }
  }
}

// Lighter colored dots on banner images
.paragraph--type--imgtxtbanner {
  .slick-dots {
    bottom: 20px;

    @include breakpoint(medium down) {
      display: none !important;
    }

    button {
      border-color: map-get($slideshow-dot, color-light);
      filter: map-get($slideshow-dot, color-light-shadow);
    }

    li {
      &.slick-active {
        button {
          &::before {
            background-color: map-get($slideshow-dot, color-light);
          }
        }
      }
    }
  }

  .slick-list {
    @include breakpoint(small only) {
      overflow: visible;
    }
  }
}

// User pages slideshow
.user-page-slideshow {
  position: relative;

  .slick-dots {
    left: 28px;
    transform: none;
    margin: 0;
    bottom: 60px;

    button {
      border-color: map-get($slideshow-dot, color-light);
      filter: map-get($slideshow-dot, color-light-shadow);
    }

    li {
      &.slick-active {
        button {
          &::before {
            background-color: map-get($slideshow-dot, color-light);
          }
        }
      }
    }
  }

  .slick-list {
    margin-bottom: -8px;
  }
}

// Fix alignment on slideshow when it has multiple rows.
.slick-has-rows {
  .text-box-row {
    max-height: 50px;
    @include breakpoint(small only) {
      max-height: 70px;
    }
  }

  .content-grid-item {
    margin-bottom: 20px;
  }
}
