.cpl-mobile-menu {
  &.cpl-menu-type-5 {
    .mobile-menu {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      overflow-y: auto;
      height: 100%;
      width: 100%;
      background-color: $primary-color;
    }

    .menu {
      @include absolute-center;
      text-align: center;

      li {
        display: block;
        font-size: 20px;
        margin-bottom: 25px;

        @include breakpoint(medium) {
          font-size: 24px;
        }

        @include breakpoint(xlarge) {
          font-size: 30px;
        }

        @include breakpoint(xxlarge) {
          font-size: 34px;
        }
      }

      a {
        color: $white;
        font-family: $body-font-family;

        &:hover {
          color: $white;
        }
      }
    }

    .close-button {
      right: 35px;
      top: 15px;
      color: $white;
    }
  }
}
