.is-vertical-menu {
  .region-vertical-menu {
    @include breakpoint(medium) {
      critical: this;
      position: fixed;
      top: 0;
      left: 0;
      bottom: 0;
      z-index: 1;
    }
  }

  .off-canvas-content {
    @include breakpoint(medium) {
      critical: this;
      position: absolute !important;
      top: 0;
      right: 0;
      left: 100px;
    }
  }

  &.menu-opened {
    .off-canvas-content {
      position: absolute !important;
      top: 0;
      left: 330px;
    }
  }
}

.cpl-mobile-menu {
  &.cpl-menu-type-4 {
    position: relative !important;
    left: 0;
    z-index: 100;
    overflow: initial;
    width: 85px;
    @include breakpoint(medium) {
      width: 100px;
    }

    @include breakpoint(small only) {
      position: fixed !important;
    }

    // TODO: add close button to expanded menu
    .close-button {
      display: none;
    }

    .mobile-menu-container {
      position: static !important;
      overflow: initial !important;
    }

    .is-dropdown-submenu {
      height: 100%;
      border: none;
      box-shadow: inset 20px 0px 20px -10px rgba(0, 0, 0, 0.1);
      filter: drop-shadow(10px 10px 10px rgba(0, 0, 0, 0.1));
    }

    .is-dropdown-submenu-parent {
      position: static;

      > a::after {
        content: none !important;
      }
    }

    .menu-logo,
    .mobile-menu {
      box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.1);
    }

    .menu-logo {
      background-color: $white;
      box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.2);
      z-index: 2;

      a {
        background-color: $primary-color;
      }

      @include breakpoint(small only) {
        z-index: 1;
        position: relative;

        &::after {
          content: '';
          @include css-triangle(20px, $primary-color, down);
          @include horizontal-center;
          bottom: -10px;
          z-index: 2;
        }
      }

      img {
        object-fit: cover;

        height: 85px;
        width: 85px;
        @include breakpoint(medium) {
          height: 100px;
          width: 100px;
        }
      }

      .menu-logo-close {
        display: none;

        position: absolute;
        top: 23px;
        right: 35px;

        @include breakpoint(medium) {
          top: 32px;
          right: 16px;
        }

        button {
          cursor: pointer;
          color: $primary-color;
          font-size: 18px;
          font-family: $condensed-font-family;
          text-transform: uppercase;
          transition: all ease 0.3s;
          display: flex;
          align-items: center;

          .close-icon {
            font-size: 39px;
            margin-left: 15px;
            font-family: $body-font-family;
          }

          &:hover {
            color: $black;
          }

          &:focus {
            outline: none;
          }
        }
      }
    }

    // Hidden for small screen
    .mobile-menu {
      @include breakpoint(small only) {
        transition: top ease 0.3s;
        z-index: 0;
        critical: this;
        top: -100vh;
      }
    }

    &.logo-menu-opened {
      .mobile-menu {
        @include breakpoint(small only) {
          top: 85px;
        }
      }
    }

    // Opened menu
    &.is-open {
      .menu-logo {
        position: relative;
        width: 100vw;
        @include breakpoint(medium) {
          width: 330px;
        }

        &::after {
          content: none;
        }

        a {
          display: block;
          width: 100px;
          @include breakpoint(small only) {
            width: 85px;
          }
        }

        img {
          margin: 0 auto;
          display: block;
        }
      }

      .menu-logo-close {
        display: block;
      }
    }

    .mobile-menu {
      position: absolute;
      overflow-y: initial;
      background-color: $white;

      width: 85px;
      @include breakpoint(medium) {
        width: 100px;
      }
    }

    // Menu items
    .menu {
      ul {
        margin-left: 0;
      }

      li {
        display: block;
        text-align: center;

        &:first-child {
          border-top: 1px solid $medium-gray;
        }

        &:last-child {
          a {
            border-bottom: none;
          }
        }

        a {
          color: $dark-gray;
          font-family: $condensed-font-family;
          font-size: 14px;
          text-transform: uppercase;
          line-height: 1.3;
          border-bottom: 1px solid $medium-gray;
          position: relative;
          transition: all ease 0.3s;
          height: 100%;
          width: 100%;
          display: flex;
          justify-content: center;
          align-items: center;

          &.is-active {
            &::after {
              @include css-triangle(12px, $white, right);
              @include vertical-center;
              right: -9px;
              z-index: 101;
              transition: all ease 0.3s;
              content: '' !important;
            }

            &:hover {
              &::after {
                @include css-triangle(12px, $light-gray, right);
              }
            }
          }

          &:hover {
            background-color: $light-gray;
          }
        }
      }

      &.vertical {
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        height: calc(100vh - 85px);
        @include breakpoint(medium) {
          height: calc(100vh - 100px);
        }

        > li {
          flex: 1;
        }
      }

      .submenu {
        li {
          flex: 1;

          &:first-child {
            border-top: none;
          }

          a {
            padding-left: 2rem;
            padding-right: 2rem;
            font-size: 18px;
            text-align: left;
            justify-content: flex-start;

            &::after {
              content: none !important;
            }

            &:hover {
              box-shadow: inset 10px 0px 20px 0px rgba(0, 0, 0, 0.08);
            }

            &.is-active {
              background-color: $light-gray;
              color: $primary-color;
              font-weight: bold;

              &::after {
                content: none !important;
              }
            }
          }
        }
      }
    }

    .opens-right {
      > a::after {
        content: none !important;
      }
    }

    // Submenu container
    .submenu {
      &.js-dropdown-active {
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        position: absolute;
        background-color: $white;

        width: calc(100vw - 85px);
        top: 85px;
        left: 85px;
        @include breakpoint(medium) {
          width: 230px;
          left: 100px;
          top: 100px;
        }
      }
    }
  }
}
