.cpl-mobile-menu {
  &.cpl-menu-type-2 {
    .mobile-menu {
      background-color: $white;
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      overflow-y: auto;
    }

    .menu-header {
      border-bottom: 1px solid $medium-gray;
      display: flex;
      align-items: center;
      margin-bottom: 5px;
    }

    .menu-title {
      font-weight: bold;
      font-family: $body-font-family;
      font-size: rem-calc(17);
      margin-left: $global-margin;
      margin-bottom: 20px;
      margin-top: 20px;
    }

    .accordion-menu {
      position: relative;
      width: 250px;
    }

    .is-accordion-submenu {
      margin-left: 0;

      &.is-active {
        background-color: $light-gray;

        a {
          &:hover {
            background-color: darken($light-gray, 5%);
          }
        }
      }
    }

    .is-submenu-item {
      a {
        display: block;
        padding-left: 30px;
        padding-top: 6px;
        padding-bottom: 6px;
      }
    }

    .is-accordion-submenu-parent[aria-expanded='true'] > a {
      background-color: $light-gray;
    }

    // Triangle marker
    .is-accordion-submenu-parent > a::after {
      border-color: $dark-gray transparent transparent;
      right: 24px;
    }

    // Menu items
    .menu {
      li {
        display: block;
      }

      a {
        color: $black;
        font-family: $condensed-font-family;

        &:hover {
          color: $black;
          background-color: $light-gray;
        }
      }
    }

    .bordered {
      border-bottom: 1px solid $medium-gray;
      padding-bottom: 10px;
      margin-bottom: 10px;
    }

    .close-button {
      right: 25px;
      top: 20px;
      color: $white;
      z-index: 1;

      &:hover {
        color: $light-gray;
      }

      &:focus {
        outline: none;
      }
    }
  }
}
