// Override Popup cart width
// TODO: add color overlay different from the mobile menu by adding extra class to body
.right-off-canvas-menu {
  background-color: $white;
  overflow-x: hidden;
  width: 100%;
  transform: translateX(100%);
  @include breakpoint(medium) {
    width: $cart-popup-width;
    transform: translateX($cart-popup-width);
  }

  &.is-open {
    overflow-y: auto;
  }

  .off-canvas-container {
    width: 100%;
    height: 100%;
    position: relative;
  }
}

// Global Cart item
.cart-item {
  display: flex;
  color: $black;
  border-bottom: 1px solid $medium-gray;
  padding-top: $global-padding;
  padding-bottom: $global-padding;

  &:last-child {
    border: none;
  }

  &-first {
    margin-right: $global-margin;
    padding-left: $global-padding;
    position: relative;

    .button {
      background: url('@cpl_commerce/images/close.svg') no-repeat center center;
      background-size: 20px 20px;
      width: 20px;
      height: 20px;
      font-size: 0;
      margin: 0;
      position: absolute;
      top: 0;
      left: 0;

      &:hover {
        opacity: 0.8;
        background-color: transparent;
      }

      &:focus {
        outline: none;
      }
    }

    img {
      width: 50px;
      height: 50px;
      max-width: inherit;
      @include breakpoint(medium) {
        width: 80px;
        height: 80px;
      }
    }
  }

  &-content {
    flex: 1 1 auto;
  }

  &-title {
    font-weight: bold;
    @include breakpoint(small only) {
      font-size: rem-calc(14);
    }
  }

  &-unit {
    color: $dark-gray;
    margin-bottom: 10px;
  }

  &-last {
    margin-left: auto;
    padding-left: 20px;
    font-weight: bold;
    align-self: flex-end;
  }

  .badge {
    position: absolute;
    top: -6px;
    right: -6px;
  }
}

// Popup cart
.block-cart-popup {
  position: static;
  background-color: $white;
  color: $dark-gray;
  padding: 1.5rem 1rem 0;
  @include breakpoint(medium) {
    padding: 1.5rem 1.5rem 0;
  }

  .close-button {
    font-size: 48px;
    color: $black;
    top: 19px;

    &:hover {
      color: $primary-color;
    }

    &:focus {
      outline: none;
    }
  }

  .svg-close {
    height: 30px;
    width: 30px;
    fill: $dark-gray;

    &:hover {
      fill: $primary-color;
    }
  }

  .block-title {
    color: $black;
    border-bottom: 1px solid $medium-gray;
    padding-bottom: 12px;
    margin-bottom: 0;
    font-size: rem-calc(38);
    line-height: 1;
  }

  .cart-block {
    &--contents {
      display: block;
      position: static;
      overflow: initial;
      width: 100%;
    }

    &--contents__items {
      overflow: initial;
      max-height: none;
    }

    &--contents__inner {
      padding: 0;
    }

    &--buttons {
      background-color: $light-gray;
      margin-left: -24px;
      margin-right: -24px;
      padding-left: 24px;
      padding-right: 24px;
      @include breakpoint(small only) {
        padding-bottom: $global-padding;
      }

      a {
        color: $white;
        margin-bottom: 0;
      }
    }

    .views-form {
      form {
        display: flex;
        flex-direction: column;
        min-height: calc(100vh - 76px);
        @include breakpoint(small only) {
          min-height: calc(100vh - 132px);
        }
      }
    }
  }

  .js-form-type-number {
    margin-bottom: 0;
  }

  .form-actions {
    display: flex;
    justify-content: space-between;
    background-color: $light-gray;
    padding: 20px 24px 24px;
    margin-left: -24px;
    margin-right: -24px;
    margin-top: 0;

    @include breakpoint(small only) {
      padding-bottom: 20px;
    }

    & > input {
      flex: 1;
      margin-top: 0;
      margin-bottom: 0;
    }

    .cpl-commerce-cart-edit-submit {
      display: none;
    }

    .form-submit {
      background-color: $primary-color;
      font-weight: 500;
      font-family: $body-font-family;
      text-transform: uppercase;
      font-size: 17px;

      &:hover {
        background-color: $black;
      }
    }
  }

  .dcom-cart-wholesale-message {
    background-color: $light-gray;
    padding: 20px 24px 24px;
    margin-left: -24px;
    margin-right: -24px;
    margin-top: 0;
  }

  .cart-free-products,
  .field-name-total-price {
    background-color: $light-gray;
    margin-left: -1rem;
    margin-right: -1rem;
    padding-left: 1rem;
    padding-right: 1rem;
    @include breakpoint(medium) {
      margin-left: -1.5rem;
      margin-right: -1.5rem;
      padding-left: 1.5rem;
      padding-right: 1.5rem;
    }

    h3 {
      color: $black;
      font-size: rem-calc(24);
      padding-top: 15px;
      margin-bottom: 0;
    }

    .cart-item {
      border: none;
      align-items: center;
    }

    .cart-item-last {
      align-self: center;
    }
  }

  .cart-free-products {
    .header {
      padding-top: 10px;
    }
  }

  .views-row {
    padding-top: 5px;
    padding-bottom: 5px;
  }

  .field-name-total-price {
    padding: 30px 0 5px;
    border-top: 1px solid $medium-gray;
    @include breakpoint(small only) {
      background-color: transparent;
      padding-bottom: 20px;
      border-top: none;
    }
  }

  .order-total-line {
    padding: 0 20px;
  }

  .order-total-line__subtotal,
  .order-total-line__adjustment {
    .order-total-line-label {
      float: left;
    }
  }

  .order-total-line__total {
    font-size: rem-calc(24);
    text-transform: uppercase;
    color: $black;
    display: flex;
    justify-content: space-between;
    margin: 0;

    .order-total-line-label {
      font-weight: 300;
    }

    .order-total-line-value {
      font-weight: bold;
      width: inherit;
    }
  }

  .cpl-commerce-cart-order-total {
    margin-top: auto !important;
  }
}

// Global cart empty
.cart-empty {
  text-align: center;
  padding: 100px 20px 0;
  @include breakpoint(medium) {
    padding: 100px 50px 0;
  }
  margin: 0 auto;
  max-width: 360px;

  h2 {
    margin-top: 15px;
    margin-bottom: 60px;
    color: $dark-gray;
  }
}

.cart-empty-page {
  padding-top: 45px;
  padding-bottom: 75px;
  max-width: 400px;
}

.cart-empty-image {
  height: 110px;
  width: 110px;
}

// Cart page
.path-cart {
  h1 {
    text-align: center;
    margin-top: 80px;
    margin-bottom: 30px;
    @include breakpoint(small only) {
      margin-top: 40px;
    }
  }

  .cart-form {
    @include breakpoint(large down) {
      padding-left: $global-padding;
      padding-right: $global-padding;
    }

    .form-actions {
      text-align: right;
      margin-top: 20px;
      margin-bottom: 60px;

      .button {
        padding-left: 20px;
        padding-right: 20px;
        background-color: $primary-color;
        font-weight: 500;
        font-family: $body-font-family;
        text-transform: uppercase;
        font-size: 17px;

        &:hover {
          background-color: $black;
        }
      }

      .cpl-commerce-cart-edit-submit {
        display: none;
      }
    }
  }

  .breadcrumbs {
    display: none;
  }

  .order-total-line__total {
    text-transform: uppercase;
    font-size: 22px;
    text-align: right;

    .order-total-line-label {
      color: $dark-gray;
    }

    .order-total-line-value {
      width: initial;
      margin-left: 35px;
    }
  }

  .order-total-line__subtotal,
  .order-total-line__adjustment {
    .order-total-line-value {
      width: 115px;
    }
  }

  .cart-free-products {
    background-color: $light-gray;
    margin-bottom: 23px;

    h3 {
      color: $black;
      font-size: rem-calc(20);
      padding-left: 20px;
      padding-top: 13px;
      padding-bottom: 10px;
      border-bottom: 1px solid darken($light-gray, 10%);
    }
  }

  .cart-item {
    align-items: center;
    padding-top: 0.8rem;
    padding-bottom: 0.8rem;

    &-first {
      img {
        border-radius: 5px;
      }
    }

    &-last {
      align-self: center;
      padding-right: 20px;
      flex-shrink: 0;

      @include breakpoint(small only) {
        display: none;
      }
    }
  }

  .cart-item-price {
    color: $dark-gray;
    margin-top: 10px;
  }

  // Table
  table {
    border-collapse: collapse;
    border: none;

    thead {
      background: transparent;
      font-weight: normal;
      text-transform: uppercase;
      color: $dark-gray;
      font-size: rem-calc(14);
      border-bottom: 1px solid $medium-gray;
      border-top: none;
      border-left: none;
      border-right: none;

      th {
        @include breakpoint(small only) {
          display: none;
        }
      }
    }

    tbody {
      border: none;
    }

    tr {
      border-bottom: 1px solid $medium-gray;
      background: transparent !important;
    }

    .views-field-remove-button {
      position: relative;
      @include breakpoint(small only) {
        padding: 0;
        margin: 0;
      }

      .button {
        background: url('@cpl_commerce/images/close.svg') no-repeat center
          center;
        background-size: 20px 20px;
        width: 20px;
        height: 20px;
        font-size: 0;
        margin: 0;
        @include vertical-center;

        &:hover {
          opacity: 0.8;
          background-color: transparent;
        }

        &:focus {
          outline: none;
        }

        @include breakpoint(small only) {
          top: 6px;
          transform: none;
        }
      }
    }

    .views-field-field-product-image {
      width: 120px;

      @include breakpoint(small only) {
        width: inherit;

        img {
          height: 50px;
          width: 50px;
          max-width: inherit;
        }
      }
    }

    .views-field-edit-quantity {
      @include breakpoint(small only) {
        position: relative;
      }
    }

    .js-form-type-number {
      margin: 0;

      @include breakpoint(small only) {
        position: absolute;
        top: 10px;
        right: -70px;
      }
    }

    .views-field-title {
      @include breakpoint(small only) {
        padding-right: 75px;
      }
    }

    .views-field-total-price__number {
      text-align: right;
      font-weight: bold;

      @include breakpoint(small only) {
        vertical-align: bottom;
        height: 100px;
      }
    }
  }
}
