.cpl-page-footer {
  &.cpl-footer-type-3 {
    .field-name-field-block-logo {
      margin-bottom: $global-margin;

      @include breakpoint(small only) {
        margin-left: auto;
        margin-right: auto;
      }
    }

    .footer-links {
      margin-top: 20px;
    }

    @include breakpoint(small only) {
      .footer-contact-block {
        text-align: center;

        & > * {
          display: none;
        }

        .footer-links {
          display: block;
        }

        .field-name-field-block-logo {
          display: block;
        }
      }
    }
  }
}
