.cpl-product-page-style-3 {
  .product {
    .product-wrapper {
      @include grid-row;
    }

    .product-image:not(.is-not-gallery) {
      @include breakpoint(medium) {
        @include grid-column(6);
      }

      @include breakpoint(large) {
        @include grid-column-gutter(0);
      }

      .product-image-zoom {
        @include breakpoint(large) {
          @include grid-column(9);
          @include grid-column-position(3);
        }
      }

      .product-gallery {
        @include breakpoint(large) {
          @include grid-column(3);
          @include grid-column-position(-9);
        }
      }
    }

    .product-image.is-not-gallery {
      @include breakpoint(medium) {
        @include grid-column(6);
      }

      @include breakpoint(large) {
        @include grid-column-gutter(0);
      }

      .product-image-zoom {
        @include breakpoint(large) {
          @include grid-column(12);
        }
      }
    }

    .product-price:not(.is-not-gallery) {
      @include breakpoint(medium) {
        @include grid-column(6);
      }
    }

    .product-price.is-not-gallery {
      @include breakpoint(medium) {
        @include grid-column(6);
      }
    }

    .product-tabs {
      @include breakpoint(medium) {
        @include grid-column(6);
      }

      @include breakpoint(large) {
        @include grid-column-gutter($global-padding * 2);
      }
    }

    .cloudzoom-gallery-active {
      @extend %product-active-gallery-right;
    }

    .cloudzoom-gallery {
      @include breakpoint(medium down) {
        display: block;
      }
    }
  }

  hr {
    display: none;
  }

  .product-tabs {
    padding-top: 30px;

    .product-tabs-left {
      display: none !important;
    }

    .product-tabs-right {
      width: 100%;
      padding-left: 0;
      padding-right: 0;
    }

    .accordion-title {
      display: block !important;

      &:hover,
      &:focus,
      &:active {
        background-color: transparent;
      }
    }

    .accordion-content,
    .accordion-title {
      padding-left: 0;
      padding-right: 0;
    }
  }
}
