.field--type-integer {
  height: 85px;
  padding-top: 22px;

  label {
    position: absolute;
    top: -26px;
  }
}

.js-form-type-number {
  input[type='number']::-webkit-inner-spin-button,
  input[type='number']::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  input[type='number'] {
    -moz-appearance: textfield;
  }
  float: left;
  position: relative;
  height: 45px;
  width: 137px;
  margin-bottom: $global-margin;
  margin-right: $global-margin;

  input {
    width: 45px;
    height: 45px;
    float: left;
    display: block;
    padding: 0;
    margin: 0;
    border: none;
    background-color: $light-gray;
    transform: translateX(46px);
    text-align: center;
  }

  input:focus {
    outline: 0;
  }

  .quantity-button {
    cursor: pointer;
    width: 45px;
    text-align: center;
    font-size: rem-calc(30);
    color: $dark-gray;
    background-color: $light-gray;
    user-select: none;
    position: absolute;
    top: 0;
    height: 100%;

    &.quantity-down {
      left: 0;
    }

    &.quantity-up {
      right: 0;
    }
  }
}
