// TODO: remove .page-global-contact class
.page-global-contact,
[class^='cpl-contact-page-'] {
  .main,
  .sidebar {
    height: 100%;
  }

  .webform-submission-form {
    margin-top: 20px;
    margin-bottom: 100px;
  }

  h1,
  h2,
  h3 {
    text-align: left;
  }

  h1 {
    color: $black;
    font-size: rem-calc(42);
    padding-top: 30px;
  }

  .fl-wrap {
    margin-bottom: $global-margin;

    input {
      height: 56px;
      font-size: 18px;
    }

    label {
      font-size: 17px;
      top: 13px;
    }

    &.fl-has-focus,
    &.fl-is-active {
      label {
        font-size: 11px;
        top: 5px;
      }
    }
  }

  .js-form-type-select {
    label {
      font-size: 12px;
      color: $dark-gray;
    }

    select {
      height: 56px;
      border-radius: 3px;
      font-size: 18px;
    }
  }

  .form-submit {
    height: 56px;
    border-radius: 3px;
  }
}
