.cpl-trigger {
  position: fixed;
  z-index: 1000;

  &-position {
    &-br {
      bottom: $global-padding * 2;
      right: $global-padding * 2;
    }
    &-bl {
      bottom: $global-padding * 2;
      left: $global-padding * 2;
    }
    &-tl {
      top: $global-padding * 2;
      left: $global-padding * 2;
    }
    &-tr {
      top: $global-padding * 2;
      right: $global-padding * 2;
    }
  }

  &-wrapper-inner {
    background-color: $trigger-bg-color;
    border-radius: 50%;
    transition: all ease 0.3s;
    width: $trigger-icon-size;

    &:hover {
      background-color: darken($trigger-bg-color, 5%);
    }
  }

  .field-name-field-block-image {
    padding: $trigger-inner-padding;
  }
}
