// Place any Drupal-related quirks here.
.region-highlighted {
  border: none;

  &:empty {
    display: none;
  }
}

// Fix popups and sticky header height with admin_menu
.adminimal-admin-toolbar {
  padding-top: 38px !important;

  .position-right,
  .position-left {
    top: 0 !important;
    z-index: 1000;
  }

  &.is-vertical-menu {
    .region-vertical-menu {
      top: 38px !important;
    }
  }

  .cpl-menu-type-4 {
    .menu.vertical {
      height: calc(100vh - 85px - 38px);
      @include breakpoint(medium) {
        height: calc(100vh - 100px - 38px);
      }
    }
  }
}

.toolbar-horizontal.toolbar-tray-open {
  padding-top: 76px !important;

  &.is-vertical-menu {
    .region-vertical-menu {
      top: 76px !important;
    }
  }

  .cpl-menu-type-4 {
    .menu.vertical {
      height: calc(100vh - 85px - 76px);
      @include breakpoint(medium) {
        height: calc(100vh - 100px - 76px);
      }
    }
  }
}

// Local tasks
[id$='local-tasks'] {
  position: fixed;
  bottom: 0;
  right: 0;
  z-index: 10;

  .button-group {
    margin: 0;
  }

  .button {
    padding: 0.5rem;
    font-size: rem-calc(13);
  }
}
