// TODO: move this to cbd_base theme.
.table-lab-results {
  .views-field-field-media-image-1 {
    width: 130px;
  }

  td.views-field-name {
    @include breakpoint(medium) {
      min-width: 320px;
    }
  }

  td.views-field-field-media-image-1 {
    text-align: center;

    a {
      font-size: 0;

      &::after {
        content: '\f019';
        font-family: 'Font Awesome 5';
        font-size: 28px;
        font-weight: bold;
        color: $dark-gray;
        transition: color ease 0.3s;
      }

      &:hover {
        &::after {
          color: $primary-color;
        }
      }
    }
  }
}
