.pager {
  .pager__item {
    font-family: $body-font-family;
    font-weight: 500;
    font-size: rem-calc(18);
    padding-left: 6px;
    padding-right: 6px;

    a {
      background: $white;
      border-radius: 50%;
      transition: all ease 0.25s;
      padding-left: 12px;
      padding-right: 12px;

      &:hover {
        background: $pager-current-background;
        color: $white;
      }
    }

    &.current {
      background: transparent;

      a {
        background: $pager-current-background;
        color: $white;
      }
    }
  }

  &__item--next,
  &__item--previous,
  &__item--last,
  &__item--first {
    .fas {
      font-size: 14px;
      position: relative;
      bottom: 1px;
    }
  }

  &__item--next {
    right: 2px;
  }

  &__item--previous {
    left: 2px;
  }

  &__item--first,
  &__item--last {
    a {
      padding-left: 6px !important;
      padding-right: 6px !important;
    }

    .fas:first-of-type {
      position: relative;
      left: 3px;
    }

    .fas:last-of-type {
      position: relative;
      right: 3px;
    }
  }
}

.pagination {
  margin-top: 20px;
  margin-bottom: 60px;
}

.cpl-blog-page-block {
  .pagination {
    margin-top: 20px;
    margin-bottom: 20px;
  }
}
