// TODO: this will be moved to new paragraph type
.paragraph--type--parallax {
  overflow: hidden;

  h4,
  .field-name-field-parc-body {
    color: $white;
  }

  .content-box {
    display: flex;
    flex-direction: column;
    @include breakpoint(medium) {
      flex-direction: row;
    }
  }

  .image-box-row,
  .text-box-row {
    min-height: 500px;

    @include breakpoint(medium) {
      width: 50%;
    }

    @include breakpoint(large) {
      min-height: 700px;
    }
  }

  .background-image {
    background-attachment: fixed;
    backface-visibility: hidden;

    @include breakpoint(medium) {
      height: 120%;
    }
  }

  .image-box-row {
    display: flex;
    justify-content: center;
    position: relative;

    @include breakpoint(small only) {
      height: 400px;
    }
  }

  .parallax-item-number {
    @include absolute-center;
    background-color: $white;
    border-radius: 50%;
    height: 120px;
    width: 120px;
    z-index: 1;
    font-size: 4rem;
    line-height: 120px;
    text-align: center;
    font-family: 'Times New Roman', serif;
    font-weight: bold;
  }

  .parallax-vertical-line {
    @include horizontal-center;
    width: 5px;
    height: 100%;
    background-color: $white;
    display: block;
  }

  .text-box-row {
    display: flex;
    align-items: center;
    padding: 30px;
    @include breakpoint(large) {
      padding: 80px;
    }
  }

  h4 {
    font-family: $body-font-family;
    font-weight: bold;
    margin-bottom: 25px;
    font-size: rem-calc(32);
  }

  .field-name-field-parc-body {
    border-top: 2px solid $white;
    padding-top: 20px;
    font-size: rem-calc(18);
  }
}
