.cpl-shop-page-style-1 {
  // Filters
  .facets-widget-links {
    padding-left: 0.5rem;

    .facet-item {
      list-style-type: none;
      padding-top: 3px;
      padding-bottom: 3px;

      a {
        position: relative;
        color: $dark-gray;
        transition: color ease 0.3s;

        &:hover {
          color: darken($dark-gray, 20%);
        }

        &::before {
          content: '\f0c8';
          font-family: 'Font Awesome 5';
          font-weight: normal;
          position: absolute;
          left: -27px;
        }

        &.is-active {
          font-weight: bold;

          &::before {
            content: '\f14a';
          }

          .facet-item__status {
            display: none;
          }
        }
      }
    }

    .item-list__links {
      padding-bottom: 0;
    }
  }

  .block-facet--links {
    &:first-child {
      border-top: 1px solid $light-gray;
    }
  }
}
