.paragraph {
  article,
  .content-box {
    height: 100%;
  }

  .content-box {
    p {
      margin-bottom: 0;
    }

    .button {
      margin-top: $global-padding;
      margin-bottom: 0;
    }
  }

  // Slideshow
  &.cpl-slideshow {
    .field-name-field-par-content-items {
      margin-bottom: -7px !important;
    }

    .image-box-bg {
      @include breakpoint(small only) {
        margin-top: -20px;
      }
    }
  }

  // Paragraphs with background images
  .image-box-bg {
    background-position: center;
    background-size: cover;

    // Default height of background image
    max-height: 800px;
    min-height: 350px;
    @include breakpoint(medium) {
      min-height: 450px;
    }

    @include breakpoint(large) {
      min-height: 600px;
    }
  }
}

// Views reference
// TODO: make the title enable/disable in gui
.viewsreference--view-title {
  line-height: 1.4;
  margin-bottom: 40px;
  margin-top: 30px;
  text-align: center;

  font-size: 1.25rem;
  @include breakpoint(medium) {
    font-size: 2.5rem;
  }
}

// Paragraphs with video
.video-modal {
  padding: 0;
  border: none;
  overflow-y: initial;

  @include breakpoint(small only) {
    overflow: hidden;
    background-color: rgba(0, 0, 0, 0.7);
  }

  > .field-wrapper {
    @include breakpoint(small only) {
      height: 100%;
      width: 90%;
      margin: 12vh auto 0;
    }
  }

  &:active,
  &:focus {
    border: none;
  }

  .close-button {
    z-index: 100;
    top: -30px;
    right: -30px;
    color: $white;

    @include breakpoint(small only) {
      top: 10px;
      right: 10px;
    }
  }
}
