.cpl-mobile-menu {
  position: relative;
  top: 0;
  bottom: 0;
  right: 0;
  left: -1150px;
  z-index: 1001;
  height: 100%;
  width: 100%;
  overflow: hidden;

  transition: left ease-in-out 0.5s;

  &.is-active {
    position: fixed;
    left: 0;

    .close-button {
      position: fixed;
    }
  }

  &.is-closing {
    left: -1150px;

    .close-button {
      position: static;
    }
  }

  .mobile-menu-container {
    position: absolute;
    overflow-x: auto;
    width: 100%;
    height: 100%;

    -webkit-overflow-scrolling: touch;
    -ms-overflow-style: -ms-autohiding-scrollbar;

    &::-webkit-scrollbar {
      display: none;
    }
  }

  .mobile-menu-slideshow {
    min-width: 1200px;
    display: flex;
    align-content: center;
    align-items: center;
    background-color: rgba($black, 0.95);
    position: absolute;
    bottom: 0;
    top: 0;
    left: 250px;
    right: 0;
    overflow: auto;
    transition: left ease-in-out 0.5s;
    padding-left: 30px;

    h4 {
      color: $white;
      font-family: $body-font-family;
      font-size: rem-calc(16);
      margin-top: 10px;
    }

    p {
      font-size: rem-calc(14);
    }

    .views-row {
      margin-right: 30px;
    }

    .text-box-row {
      min-height: 50px;
    }
  }
}

.off-canvas-wrapper {
  overflow: initial !important;
}

.left-off-canvas-menu {
  &.is-open {
    overflow: initial !important;
  }
}

.js-off-canvas-overlay {
  &.is-visible {
    overflow-x: scroll !important;
  }
}

// COLORS

// Text colors
@if (length($text-colors) != 0) {
  @for $i from 1 through length($text-colors) {
    .cpl-menu-type-5.cpl-menu-text-#{$i} {
      .menu a,
      .close-button {
        color: nth($text-colors, $i) !important;
      }
    }
  }
}

.cpl-menu-type-5.cpl-menu-text-w {
  .menu a,
  .close-button {
    color: $white !important;
  }
}

.cpl-menu-type-5.cpl-menu-text-b {
  .menu a,
  .close-button {
    color: $black !important;
  }
}

.cpl-menu-type-5.cpl-menu-text-l {
  .menu a,
  .close-button {
    color: $light-gray !important;
  }
}

.cpl-menu-type-5.cpl-menu-text-m {
  .menu a,
  .close-button {
    color: $medium-gray !important;
  }
}

.cpl-menu-type-5.cpl-menu-text-d {
  .menu a,
  .close-button {
    color: $dark-gray !important;
  }
}

// Background colors
@if (length($bg-colors) != 0) {
  @for $i from 1 through length($bg-colors) {
    .cpl-menu-type-5.cpl-menu-bg-#{$i} {
      .mobile-menu {
        background: nth($bg-colors, $i) !important;
      }
    }
  }
}

.cpl-menu-type-5.cpl-menu-bg-b {
  .mobile-menu {
    background: $black !important;
  }
}

.cpl-menu-type-5.cpl-menu-bg-w {
  .mobile-menu {
    background: $white !important;
  }
}
