.cpl-page-header {
  &.cpl-header-type-2 {
    a {
      critical: this;
      color: $header-text-color;
    }

    .header-main {
      @include grid-row;
      position: relative;
      display: flex;
      justify-content: space-between;
      color: $header-text-color;

      height: 60px;
      @include breakpoint(medium) {
        height: 80px;
      }
      @include breakpoint(large) {
        height: 128px;
      }

      @include breakpoint(medium only) {
        display: flex;
        align-items: center;
        justify-content: flex-start;
      }

      a {
        color: $header-text-color;
        transition: color ease 0.3s;

        &:hover {
          color: $header-text-color-h;
        }
      }

      .row {
        @include breakpoint(medium) {
          margin-left: 0;
          margin-right: 0;
        }
      }
    }

    .header-logo {
      flex: 0 0 auto;

      @include breakpoint(medium) {
        margin-right: 15px;
        margin-left: 15px;
      }

      @include breakpoint(large) {
        margin-top: 5px;
        float: left;
      }

      @include breakpoint(medium only) {
        flex-shrink: 0;
      }

      @include breakpoint(small only) {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
      }

      img {
        width: auto;
        height: 120px;
        @include breakpoint(medium only) {
          height: 70px;
        }
        @include breakpoint(small only) {
          height: 60px;
        }
      }
    }

    .header-content {
      @include breakpoint(medium) {
        display: flex;
        flex-direction: column;
        padding-left: 15px;
        padding-right: 15px;
        width: 100%;
      }

      @include breakpoint(medium only) {
        margin-left: auto;
      }
    }

    .header-contacts {
      padding-top: 18px;
      display: flex;

      a {
        margin-right: 13px;
      }

      .fas {
        margin-right: 2px;
      }

      @include breakpoint(large only) {
        float: left;
      }

      @include breakpoint(medium down) {
        display: none;
      }
    }

    .header-nav-main {
      text-transform: uppercase;
      margin-right: -0.8rem;
      float: right;

      @include breakpoint(medium down) {
        display: none;
      }

      li {
        a {
          padding: 1.3rem 0.8rem;
          @include breakpoint(large only) {
            padding: 1.3rem 0.6rem;
          }

          &.is-active {
            color: $header-text-color-h !important;
          }
        }
      }
    }

    .header-nav-shop,
    .header-nav-commerce {
      li {
        a {
          @include breakpoint(medium) {
            padding-top: 1rem;
            padding-bottom: 1rem;
          }

          @include breakpoint(large) {
            padding-top: 1.7rem;
            padding-bottom: 1.7rem;
          }
        }
      }
    }

    .header-nav-shop {
      margin-left: -0.9rem;
      margin-right: -0.9rem;

      @include breakpoint(medium down) {
        display: none;
      }

      li {
        &:first-child {
          a {
            padding-left: 0;
          }
        }

        &.menu-item--active-trail {
          > a {
            color: $header-active-menu-color;
            position: relative;

            &::after {
              @include css-triangle(10px, $header-active-menu-color, up);
              position: absolute;
              bottom: -2px;
              left: 50%;
              transform: translateX(-50%);
            }
          }
        }
      }
    }

    .header-nav-commerce {
      font-weight: 300;
      float: right;
      margin-left: auto;

      @include breakpoint(small only) {
        position: absolute;
        right: 15px;
        top: 50%;
        transform: translateY(-50%);
      }

      .menu {
        float: right;
        margin-right: -15px;

        @include breakpoint(xlarge only) {
          margin-right: -30px;
        }

        @include breakpoint(large only) {
          margin-right: -35px;
        }

        @include breakpoint(medium only) {
          margin-right: -35px;
        }
      }

      li {
        position: relative;

        &:last-child {
          a {
            padding-right: 0;
          }
        }

        @include breakpoint(medium down) {
          width: 55px;
        }

        @include breakpoint(small down) {
          width: 40px;
        }

        a {
          @include breakpoint(medium) {
            margin-left: 25px;
          }

          @include breakpoint(xlarge only) {
            padding-top: 2rem;
            padding-bottom: 2rem;
            padding-left: 20px;
            font-size: 0;
          }

          @include breakpoint(large only) {
            padding-top: 2rem;
            padding-bottom: 2rem;
            padding-left: 20px;
            font-size: 0;
          }
        }
      }
    }

    .header-row-top {
      font-family: $condensed-font-family;
      font-size: $header-font-size-first;

      @include breakpoint(medium) {
        padding-left: 0;
        padding-right: 0;
      }

      @include breakpoint(large) {
        border-bottom: 2px solid $header-line-color;
      }
    }

    .header-row-bottom {
      font-size: $header-font-size-second;
      font-family: $condensed-font-family;
      text-transform: uppercase;

      @include breakpoint(medium) {
        display: flex;
        justify-content: flex-start;
        padding-left: 1rem;
        padding-right: 1rem;
      }

      @include breakpoint(medium only) {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        flex-grow: 1;
        margin-right: 35px;
      }
    }

    .header-promo {
      background-color: $header-promo-bg-color;
      color: $header-promo-text-color;
      text-align: center;
      padding-top: 10px;
      padding-bottom: 10px;
      font-size: $header-font-size-promo;
      text-transform: uppercase;
      box-shadow: 0px 10px 20px 0px rgba(0, 0, 0, 0.25);
      position: relative;
      z-index: 1;
      border-top: 2px solid $header-line-color;

      @include breakpoint(small only) {
        padding-top: 5px;
        padding-bottom: 5px;
      }

      a {
        color: $header-promo-text-color;
        text-decoration: underline;
      }

      p {
        margin: 0;
      }
    }

    .field-name-field-block-logo {
      display: inline-block;
    }

    .field-name-field-block-phone {
      margin-right: 20px;
    }

    .header-hamburger {
      @include breakpoint(large) {
        display: none;
      }

      @include breakpoint(medium only) {
        margin-left: 15px;
      }

      @include breakpoint(small only) {
        position: absolute;
        left: 15px;
        top: 50%;
        transform: translateY(-45%);
      }
    }
  }
}

// Sticky header
.has-sticky-header {
  .cpl-header-type-2 {
    .header-logo {
      margin-top: 0;
    }

    .header-row-bottom {
      max-width: none;
    }

    .header-nav-commerce {
      .menu {
        @include breakpoint(large down) {
          margin-right: -35px !important;
        }

        li {
          a {
            @include breakpoint(xlarge only) {
              padding-top: 1.7rem !important;
              padding-bottom: 1.7rem !important;
            }

            @include breakpoint(large only) {
              padding-top: 1.7rem !important;
              padding-bottom: 1.7rem !important;
            }
          }
        }
      }
    }
  }
}
