// Page styles.
.cpl-blog-page-style-1 {
  .views-row {
    margin-bottom: 40px;
  }
}

// Article styles.
.cpl-article-row-style-1 {
  .posted {
    font-size: $blog-teaser-font-size;
    color: $dark-gray;
    margin-top: 10px;
    margin-bottom: 5px;
    font-family: $body-font-family;
  }

  .node-title {
    font-size: $blog-teaser-heading-font-size;
    font-family: $blog-teaser-heading-font-family;
    line-height: 1.2;

    a {
      color: $black;

      &:hover {
        color: $primary-color;
      }
    }
  }

  .node-readmore {
    margin-top: 10px;

    a {
      color: $primary-color;

      &:hover {
        color: $black;
      }
    }
  }

  .field-name-field-blog-description {
    font-size: $blog-teaser-font-size;
    font-family: $blog-teaser-font-family;
  }

  &.cpl-embed-in-paragraph {
    .text-box-container {
      padding: 0 !important;
    }
  }
}
