.cpl-mobile-menu {
  &.cpl-menu-type-1 {
    .mobile-menu {
      background-color: $white;
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      overflow-y: auto;

      .menu-header {
        border-bottom: 1px solid $medium-gray;
        display: flex;
        align-items: center;
      }

      .menu-title {
        font-weight: bold;
        font-family: $body-font-family;
        font-size: rem-calc(17);
        margin-left: $global-margin;
        margin-bottom: 25px;
        margin-top: 20px;
      }

      // Drilldown
      .drilldown {
        position: relative;
        width: 250px;
      }

      .is-drilldown-submenu {
        height: 100%;
        min-height: 100vh !important;
        margin-left: 0;
        padding-top: 10px;
        overflow: auto;

        -webkit-overflow-scrolling: touch;
        -ms-overflow-style: -ms-autohiding-scrollbar;

        &::-webkit-scrollbar {
          display: none;
        }
      }

      .is-drilldown-submenu-item {
        line-height: 1.2;

        a {
          display: block;
          padding: 0.7rem 1rem;
        }
      }

      .is-drilldown-submenu-parent {
        position: static;

        > a {
          &::after {
            border: none;
            content: '\f054';
            font-family: 'Font Awesome 5';
            font-weight: bold;
            position: absolute;
            right: 30px;
            top: 16px;
          }

          &:focus,
          &:active {
            outline: none;
          }
        }
      }

      .js-drilldown-back {
        margin-bottom: 15px;
        margin-top: 5px;
        padding-left: 22px;

        a {
          color: $primary-color !important;
          padding-left: 1rem;
          text-transform: uppercase;

          &::before {
            border: none;
            content: '\f060';
            font-family: 'Font Awesome 5';
            font-weight: bold;
            position: absolute;
            left: 16px;
            top: 15px;
            color: $primary-color;
          }

          &:hover {
            background-color: transparent !important;
          }
        }
      }

      .nested {
        margin-left: 0;
        padding-left: 1rem;
      }

      // Menu items
      .menu {
        li {
          display: block;
        }

        a {
          color: $black;
          font-family: $condensed-font-family;

          &:hover {
            color: $black;
            background-color: $light-gray;
          }
        }
      }

      .bordered {
        border-bottom: 1px solid $medium-gray;
      }
    }

    .close-button {
      right: 25px;
      top: 20px;
      color: $white;
      z-index: 1;

      &:hover {
        color: $light-gray;
      }

      &:focus {
        outline: none;
      }
    }
  }
}
