.account {
  .js-form-type-select {
    label {
      color: $dark-gray;
      font-size: rem-calc(13);
    }

    select {
      border-radius: 3px;
      background-color: $white;
    }
  }

  // Form elements
  .js-form-type-textfield,
  .js-form-type-email,
  .js-form-type-password,
  .js-form-type-tel {
    position: relative;

    input[type='text'],
    input[type='email'],
    input[type='password'],
    input[type='tel'] {
      height: 44px;
      border-radius: 3px;
      outline: inherit;
      padding-left: 1rem;
      padding-top: 22px;
      margin-bottom: 10px;
      border-color: $medium-gray;

      &:focus,
      &:active {
        border: 2px solid $primary-color;
        outline: none;
      }

      @include placeholder {
        visibility: hidden;
        opacity: 0;
      }
    }

    label {
      transition: all 0.2s ease-in-out;
      position: absolute;
      color: $dark-gray;
      top: 10px;
      left: 18px;
      font-size: 14px;
    }

    .fl-is-active,
    .fl-has-focus {
      label {
        font-size: 11px;
        top: 2px;
        left: 18px;
      }
    }

    .fl-wrap {
      margin-bottom: 25px;
    }

    .fl-label {
      // Remove visually hidden
      clip: auto;
      width: auto;
      height: auto;
    }
  }

  .user-form {
    @include breakpoint(large) {
      padding-right: 150px;
    }

    @include breakpoint(small only) {
      padding-right: $global-padding;
    }

    .description {
      display: none;
    }

    summary {
      display: none;
    }

    // File input
    .form-file-input {
      color: $dark-gray;
      background-color: $white;
      border: 2px solid $medium-gray;
      display: inline-block;
      cursor: pointer;
      position: relative;
      height: 44px;
      width: 150px;
      line-height: 41px;
      padding-left: 8px;
      transition: all ease 0.3s;

      &:hover {
        background-color: $light-gray;
      }

      &::after {
        content: '\f093';
        font-family: 'Font Awesome 5';
        font-size: 24px;
        color: $dark-gray;
        position: absolute;
        right: 11px;
        top: 0;
        font-weight: bold;
        z-index: 1;
      }
    }

    .form-managed-file {
      position: relative;

      [id^='edit-field-ein-file-0-remove-button'] {
        background: url('@cpl_base/images/close-bold.svg') no-repeat center
          center;
        background-size: 16px 16px;
        width: 16px !important;
        height: 16px !important;
        font-size: 0;
        margin: 0;
        position: absolute;
        top: 12px;
        left: -3px;

        &:hover {
          opacity: 0.8;
          background-color: transparent;
        }

        &:focus {
          outline: none;
        }
      }

      .file--image {
        padding-left: 24px;
        font-family: $condensed-font-family;
        font-size: 22px;

        a {
          color: $dark-gray;
          position: relative;
          top: 4px;
        }
      }
    }

    .form-item-field-ein-file-0 {
      display: flex;
      justify-content: space-between;
      margin-top: $global-margin * 1.5;
      margin-bottom: $global-margin * 1.5;

      > label {
        border-bottom: 2px solid $medium-gray;
        width: 55%;
        color: $medium-gray;
        font-size: 13px;
        font-weight: 500;
        text-transform: uppercase;
      }
    }
  }

  .password-strength {
    display: none;
  }

  .form-item-field-address-0-address-administrative-area {
    position: relative;
    bottom: 22px;
  }
}

.account-header {
  background-color: $light-gray;

  &-container {
    @include breakpoint(medium) {
      display: flex;
      justify-content: space-between;
    }
    padding-top: $global-padding;
    padding-bottom: $global-padding;

    @include breakpoint(xlarge down) {
      padding-left: $global-padding;
      padding-right: $global-padding;
    }
  }

  &-name {
    align-self: center;
    font-family: $header-font-family;
    font-size: rem-calc(20);

    @include breakpoint(small only) {
      width: 100%;
      clear: both;
      margin-bottom: 20px;
    }
  }

  &-year,
  &-orders {
    text-align: right;
    margin-left: 40px;
    color: $dark-gray;
    text-transform: uppercase;
    font-size: rem-calc(13);

    @include breakpoint(small only) {
      width: 40%;
      float: left;
      text-align: left;
    }

    span {
      display: block;
      font-weight: bold;
      color: $black;
      text-transform: none;
      font-size: rem-calc(16);
      margin-top: 3px;
      margin-bottom: 3px;
    }
  }

  &-orders {
    margin-left: auto;

    a {
      color: $primary-color;

      &:hover {
        color: $black;
      }
    }
  }
}

// Content
.account-content {
  h1 {
    text-align: center;
    color: $black;
    font-size: rem-calc(28);
    margin-top: 35px;
    margin-bottom: 35px;
    font-weight: bold;
    @include breakpoint(medium down) {
      padding-right: $global-padding;
    }

    .field-name-order-number {
      text-align: left;
      margin-bottom: -30px;
      font-family: $body-font-family;
      font-size: rem-calc(25);

      .field-label {
        margin-right: 15px;
      }

      .field-items {
        font-weight: normal;
      }
    }
  }

  .field-name-placed {
    margin-bottom: 20px;
  }

  .field-label-inline {
    .field-label {
      font-weight: bold;
      float: left;
      margin-right: 10px;

      &::after {
        content: ': ';
      }
    }
  }

  .field-name-order-items {
    margin-top: $global-margin;
    margin-bottom: $global-margin;
    margin-left: -#{$global-padding};
  }

  .order-header {
    position: relative;
  }

  .field-name-state {
    display: flex;
    position: absolute;
    right: $global-padding;
    bottom: 27px;
    background-color: $order-completed;
    border-radius: 20px;
    padding: 3px 16px;
    font-size: rem-calc(14);

    @include breakpoint(medium) {
      right: 0;
    }

    @include breakpoint(small only) {
      bottom: 70px;
    }

    .field-label {
      font-weight: normal;
    }

    .field-items {
      font-weight: bold;
    }
  }

  .order-summary {
    text-align: right;

    @include breakpoint(small only) {
      padding-right: $global-padding;
    }

    .order-total-line {
      padding-right: 0;
    }
  }

  .order-item-title {
    line-height: 1.2;
  }

  .order-item-unit {
    @include breakpoint(medium down) {
      float: none !important;
    }
  }

  .field-name-total-price {
    margin-top: 10px;
    margin-bottom: 80px;
  }

  // Order items
  .order-item {
    display: flex;
    border-bottom: 2px solid $light-gray;
    padding-top: 15px;
    padding-bottom: 15px;

    &-first {
      @include breakpoint(small only) {
        padding-left: $global-padding;
        flex-shrink: 0;
      }

      img {
        height: 160px;
        width: 160px;
        @include breakpoint(small only) {
          height: 75px;
          width: 75px;
          margin-right: 15px;
        }
      }
    }

    &-last {
      align-self: center;
      margin-left: auto;
      margin-bottom: 5px;
      @include breakpoint(small only) {
        padding-right: $global-padding;
      }
    }

    &-title {
      font-weight: bold;
      margin-bottom: 5px;
    }

    &-weight {
      margin-bottom: 12px;
    }

    &-unit,
    &-quantity {
      float: left;
      margin-bottom: 7px;
      @include breakpoint(large) {
        margin-bottom: 20px;
      }
    }

    &-quantity {
      @include breakpoint(medium down) {
        float: none;
      }
    }

    &-unit {
      margin-right: 30px;
    }

    &-button {
      clear: both;

      @include breakpoint(medium down) {
        margin-top: 15px;
      }

      a {
        text-transform: uppercase;
        padding: 0.6rem 1.2rem;
        font-size: rem-calc(13);
        font-weight: 500;
        @include button-style($primary-color, $black, $white);
        transition: all ease 0.3s;

        @include breakpoint(small only) {
          font-size: rem-calc(12);
          padding-left: 0.7rem;
          padding-right: 0.7rem;
        }
      }
    }
  }
}

// Main content
.account-content-right {
  padding-right: $global-padding !important;

  @include breakpoint(medium) {
    padding-left: 50px !important;
  }

  @include breakpoint(small only) {
    margin-right: -#{$global-padding};
    margin-left: -#{$global-padding};
  }
}

// Sidebar menu
.account-menu {
  @include breakpoint(medium) {
    padding-bottom: 50px;
  }

  // Override local tasks
  [class$='local-tasks'] {
    position: static;
  }

  .button-group {
    .button {
      display: block;
      margin: 0;
      border-bottom: 2px solid $light-gray;
      transition: all ease 0.3s;
      position: relative;
      padding-left: 33px;
      padding-top: $global-padding;
      padding-bottom: $global-padding;
      color: $dark-gray;
      font-size: rem-calc(15);
      text-align: left;
      background-color: $white !important;

      @include breakpoint(large down) {
        padding-left: $global-padding;
      }

      &:hover {
        color: $black;
      }

      &.active {
        color: $black;

        @include breakpoint(medium) {
          &::after {
            content: '\f054';
            position: absolute;
            font-size: 12px;
            font-family: 'Font Awesome 5';
            font-weight: bold;
            color: $black;
            right: 0;
            top: 50%;
            transform: translateY(-50%);
          }
        }

        @include breakpoint(small only) {
          background-color: $light-gray;
        }
      }
    }
  }
}

// Your Orders
.account-orderlist {
  @include breakpoint(small only) {
    margin-left: -#{$global-padding};
  }

  &-empty {
    padding-top: 50px;
    max-width: 360px;
    margin: 0 auto;

    .order-empty-image {
      margin-bottom: 15px;
      background: url('@cpl_commerce/images/cart.svg') center no-repeat;
      height: 90px;
    }

    h2 {
      font-size: rem-calc(28);
    }

    .button {
      margin-top: 35px;

      background-color: $primary-color;

      &:hover {
        background-color: $black;
      }
    }
  }

  table {
    border-collapse: collapse;
    border: none;
    @include breakpoint(medium only) {
      font-size: 15px;
    }
    @include breakpoint(small only) {
      font-size: 14px;
    }

    thead {
      background: transparent;
      font-weight: normal;
      color: $black;
      font-size: rem-calc(16);
      border-bottom: 1px solid $light-gray;
      border-top: none;
      border-left: none;
      border-right: none;

      a {
        color: $black;

        &:hover {
          color: $primary-color;
        }
      }

      th {
        padding: 1rem 0;

        @include breakpoint(medium down) {
          font-size: 14px;
        }

        @include breakpoint(small only) {
          display: none;
        }
      }
    }

    tbody {
      border: none;

      td {
        padding: 1rem 0;
        @include breakpoint(small only) {
          padding-right: 10px;
        }

        &:first-child {
          @include breakpoint(small only) {
            padding-left: $global-padding;
          }
        }

        &:last-child {
          @include breakpoint(small only) {
            padding-right: 0;
          }
        }
      }
    }

    tr {
      border-bottom: 1px solid $light-gray;
      background: transparent !important;
    }
  }
}

.profile-type-listing,
.profile-user-listing {
  .views-row {
    position: relative;
    margin-bottom: 20px;
    @include grid-column-gutter(20);

    &:last-child {
      float: left;
    }
  }

  .views-field-rendered-entity {
    border: 1px solid $medium-gray;
    padding: $global-padding;
    border-radius: 5px;
    height: 100%;
  }

  .views-field-operations {
    position: absolute;
    bottom: 15px;
    right: 15px;

    a {
      color: $primary-color;
      font-size: 13px;

      &:hover {
        color: $black;
      }
    }

    .dropbutton-wrapper {
      &.open {
        .dropbutton {
          background-color: $white;
          padding: 10px 20px;
          border: 1px solid $light-gray;
          border-radius: 3px;

          li {
            margin-top: 8px;
            margin-bottom: 8px;
          }
        }
      }
    }

    .dropbutton-arrow {
      border-width: 5px 5px 0;
      color: $primary-color;
      right: 50%;
    }
  }

  .address {
    font-size: 14px;

    .family-name,
    .given-name {
      font-weight: bold;
      font-size: 16px;
    }
  }

  .field-name-field-phone-number {
    font-size: 14px;

    .field-label {
      font-weight: bold;
      font-size: 16px;
    }
  }
}

.profile-type-listing {
  @include breakpoint(small only) {
    margin-left: -#{$global-padding};
  }
}

// Add New button
.add-new-address,
.add-new-payment {
  border: 1px solid $medium-gray;
  padding-top: 45px;
  border-radius: 5px;
  margin-bottom: 20px;
  background-color: $light-gray;
  color: $dark-gray;
  position: relative !important;
  text-align: center;
  transition: all ease 0.3s;

  @include breakpoint(medium) {
    width: calc((100% / 2) - 20px);
    margin-left: 10px;
  }

  @include breakpoint(large) {
    width: calc((100% / 3) - 20px);
    margin-left: 10px;
  }

  &:hover,
  &:active,
  &:focus {
    background-color: darken($light-gray, 3%);
    color: $dark-gray;
  }

  &::before {
    content: '';
    background: url('@cpl_commerce/images/plus-gray.svg');
    background-size: 50px 50px;
    display: block;
    @include horizontal-center;
    height: 50px;
    width: 50px;
  }
}

.add-new-address {
  line-height: 200px;

  @include breakpoint(small only) {
    line-height: 120px;
    width: calc(100% - 20px);
    margin-left: 10px;
  }

  &::before {
    top: 75px;

    @include breakpoint(small only) {
      top: 35px;
    }
  }
}

.add-new-payment {
  line-height: 150px;

  @include breakpoint(small only) {
    line-height: 120px;
    width: calc(100% - 20px);
    margin-left: 10px;
  }

  &::before {
    top: 50px;

    @include breakpoint(small only) {
      top: 35px;
    }
  }
}

// Payment method listing
.payment-method-listing {
  @include breakpoint(small only) {
    margin-left: -#{$global-padding};
  }

  .views-row {
    position: relative;
    margin-bottom: 20px;
    @include grid-column-gutter(20);

    &:last-child {
      float: left;
    }

    .payment-method-wrapper {
      border: 1px solid $medium-gray;
      padding: $global-padding;
      border-radius: 5px;
      height: 100%;
      padding-bottom: 35px;
    }
  }

  .views-field-nothing {
    height: 100%;
  }

  .views-field-expires {
    font-size: 13px;

    .views-label {
      font-weight: bold;
      display: block;
    }
  }

  .views-field-card-number-value {
    font-weight: bold;
    font-size: 18px;
    line-height: 1.2;
    margin-bottom: 10px;
  }

  .payment-method-actions {
    text-align: right;
    position: absolute;
    bottom: 15px;
    right: 25px;
    font-size: 12px;
    text-transform: uppercase;

    a {
      color: $primary-color;

      &:hover {
        color: $black;
      }
    }

    a:not(:last-child) {
      border-right: 1px solid $medium-gray;
      padding-right: 7px;
      margin-right: 4px;
    }
  }

  .payment-card-default {
    position: absolute;
    top: 10px;
    right: 25px;
    font-size: 11px;
    color: $dark-gray;
  }

  .payment-card {
    // We have classes with uppercase because these classes taken from view field value.
    &.payment-card-Visa {
      display: block;
      background: url('@cpl_commerce/images/card-visa.svg') no-repeat center;
      height: 20px;
      width: 33px;
      margin-bottom: 9px;
    }

    &.payment-card-Mastercard {
      display: block;
      background: url('@cpl_commerce/images/card-mastercard.svg') no-repeat
        center;
      height: 20px;
      width: 33px;
      margin-bottom: 9px;
    }
  }
}

// Individual order
.customer-information {
  background-color: $light-gray;
  padding: $global-padding;
  display: flex;
  font-size: rem-calc(14);
  @include breakpoint(small only) {
    margin-right: -#{$global-padding};
    margin-left: -#{$global-padding};
  }

  & > div {
    flex: 1;
    padding-right: 10px;
  }

  .address {
    margin-bottom: 0;
  }

  .field-label {
    font-weight: bold;
    margin-top: 10px;
  }
}

// Logout modal
.reveal-logout {
  padding: $global-padding * 2;

  @include breakpoint(small only) {
    padding-top: 20%;
  }

  h3 {
    color: $black;
    font-family: $body-font-family;
    font-weight: 500;
    margin-bottom: 15px;
    margin-top: 10px;
  }

  .svg-logout {
    height: 60px;
    width: 60px;
    fill: $medium-gray;
  }

  .button {
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 15px;
  }
}

// Add address form
.profile-customer-add-form {
  position: fixed;
  // Hide box shadow that's why -105%
  right: -105%;
  top: 0;
  bottom: 0;
  z-index: 503;
  width: 460px;
  background-color: $white;
  padding: 2rem $global-padding 0;
  box-shadow: -2px 0px 10px 0px rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: column;
  transition: right ease 0.7s;
  overflow: auto;

  @include breakpoint(small only) {
    width: 100%;
  }

  &.is-active {
    right: 0;
  }

  .close-button {
    &:active,
    &:focus {
      outline: none;
    }
  }

  summary,
  .form-item-address-0-address-address-line2 {
    display: none;
  }

  .form-actions {
    margin-top: auto;
    margin-left: -#{$global-padding};
    margin-right: -#{$global-padding};
    padding: $global-padding;
    background-color: $light-gray;
    display: flex;
    justify-content: space-between;
    flex: 0 0 auto;

    input {
      flex: 1;
      margin-bottom: 0;
    }

    .primary {
      background-color: $secondary-color;

      &:hover {
        background-color: $black;
      }
    }
  }

  select {
    margin-bottom: 25px;
  }
}

// Profile delete form
.profile-customer-delete-form {
  text-align: center;
  padding-top: 15px;
  font-size: 18px;

  .form-actions {
    margin-top: 30px;
  }
}

// Profile delete form
.profile-customer-edit-form {
  max-width: map-get($row-widths, 2);
  margin: 0 auto;
  padding-left: $global-padding;
  padding-right: $global-padding;

  .form-actions {
    margin-top: 30px;
  }

  summary {
    display: none;
  }
}

// Profile delete payment
.commerce-payment-method-nmi-3sr-cc-delete-form {
  .form-actions {
    margin-top: 30px;
  }
}
