.paragraph--type--imgtxtbanner {
  &.cpl-row-style-1 {
    .content-box {
      @include breakpoint(small only) {
        display: flex;
        flex-direction: column;
      }
    }

    .text-box-row {
      @include vertical-center;

      @include grid-col-size(11);
      @include breakpoint(medium) {
        @include grid-col-size(5);
      }
      @include breakpoint(large) {
        @include grid-col-size(4);
      }
    }

    &.cpl-mobile-layout-1 {
      .text-box-row {
        @include breakpoint(small only) {
          @include grid-col-size(12);
          position: relative !important;
          transform: none !important;
          left: 0 !important;
        }
      }
    }

    .text-box-container {
      @include breakpoint(xlarge) {
        @include grid-col-size(9);
      }
    }

    .image-box-row {
      @include grid-col-size(9);

      @include breakpoint(small only) {
        @include grid-col-size(12);
      }
    }

    &.cpl-position-l {
      .image-box-row {
        @include grid-column-offset(3);

        @include breakpoint(small only) {
          @include grid-column-offset(0);
        }
      }

      .text-box-row {
        left: 0;

        @include breakpoint(small only) {
          @include absolute-center;
        }
      }

      .text-box-container {
        @include breakpoint(xlarge) {
          @include grid-col-size(9);
          @include grid-column-offset(3);
        }
      }
    }

    &.cpl-position-r {
      .text-box-row {
        right: 0;

        @include breakpoint(small only) {
          @include absolute-center;
        }
      }
    }

    &.cpl-position-c {
      .text-box-row {
        @include breakpoint(medium) {
          @include absolute-center;
        }

        @include breakpoint(small only) {
          left: 50%;
          transform: translate(-50%, -50%);
        }
      }

      .image-box-row {
        @include grid-col-size(12);
      }

      .text-box-container {
        @include grid-col-size(12);
        text-align: center;
      }
    }

    // Alternating.
    &.cpl-alternating {
      .content-grid > .field-item:nth-child(odd),
      .slick-track > .slick-slide:nth-child(odd) {
        .image-box-row {
          @include breakpoint(medium) {
            @include grid-column-offset(3);
          }
        }

        .text-box-row {
          @include breakpoint(medium) {
            left: 0;
            right: auto;
          }

          @include breakpoint(small only) {
            @include absolute-center;
          }
        }
      }

      .content-grid > .field-item:nth-child(even),
      .slick-track > .slick-slide:nth-child(even) {
        .image-box-row {
          @include breakpoint(medium) {
            @include grid-column-offset(0);
          }
        }

        .text-box-row {
          @include breakpoint(medium) {
            left: auto;
            right: 0;
          }

          @include breakpoint(small only) {
            @include absolute-center;
          }
        }
      }
    }
  }
}
