.accordion-title {
  font-weight: bold;
  transition: color ease 0.3s;
  border: none;

  &:hover,
  &:focus,
  &:active {
    color: darken($dark-gray, 20%);
    background-color: rgba($primary-color, 0.1);
  }

  &::before {
    content: '\f107' !important;
    font-family: 'Font Awesome 5';
    font-weight: bold;
  }
}

.accordion-item {
  &.is-active {
    > .accordion-title {
      color: darken($dark-gray, 20%);

      &::before {
        content: '\f106' !important;
      }
    }
  }
}

.accordion-content {
  border: none;
}

// Filter block for small screen
.sidebar-inner {
  background-color: $accordion-background;

  @include breakpoint(medium down) {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top: calc(100vh - 50px);
    transition: all ease 0.4s;
    box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.25);
    z-index: 100;
  }

  .reset-filter {
    margin: 9px 16px 0 0;
    padding: 10px;

    @include breakpoint(medium down) {
      opacity: 0;
      transition: opacity ease 0.3s;
    }
  }

  &.is-active {
    @include breakpoint(medium down) {
      top: 0;
      z-index: 1000;
      overflow-y: scroll;

      .reset-filter {
        opacity: 1;
        margin-top: 47px;
        position: relative;
        z-index: 1000;
      }

      .sidebar-inner-title {
        padding-top: 55px;
        position: relative;

        &::before {
          content: '';
          display: block;
          width: 100%;
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          height: 36px;
          box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.25);
        }

        &::after {
          content: '\f0d7';
          font-family: 'Font Awesome 5';
          position: absolute;
          top: 2px;
          left: 50%;
          transform: translateX(-50%);
          display: block;
          color: $dark-gray;
          font-size: 24px;
          font-weight: 900;
        }
      }
    }

    @include breakpoint(small only) {
      .reset-filter {
        margin-top: 44px;
      }
    }
  }
}

.sidebar-inner-title {
  padding: $accordion-item-padding;
  margin-bottom: 0;
  line-height: 1;

  a {
    cursor: default;
    pointer-events: none;
    width: 100%;
    height: 100%;
    color: $dark-gray;
    font-weight: bold;
    font-family: $body-font-family;
    font-size: $accordion-title-font-size;
    line-height: $accordion-title-font-size;
    @include breakpoint(medium down) {
      cursor: pointer;
    }
  }

  @include breakpoint(medium only) {
    a {
      position: relative;
      bottom: 5px;
    }
  }

  @include breakpoint(medium down) {
    cursor: pointer;
    pointer-events: auto;
  }
}

.is-fixed {
  @include breakpoint(medium down) {
    overflow-y: hidden;
  }
}

// Override mobile iOS bottom panel
@supports (-webkit-overflow-scrolling: touch) {
  @media (orientation: portrait) {
    .sidebar-inner {
      top: calc(100vh - 80px);
    }

    .sidebar-inner-title {
      padding-bottom: 3rem;
    }
  }
}
