// Payment methods icons
.card-visa-gray,
.card-mastercard-gray,
.card-amex-gray,
.card-discover-gray,
.card-visa-white,
.card-mastercard-white,
.card-amex-white,
.card-discover-white {
  display: inline-block;
  float: left;
  background-repeat: no-repeat;
  background-position: center;
  height: 30px;
  margin-right: 8px;
}

.card-visa-gray {
  background-image: url('@cpl_commerce/images/payment-visa-g.svg');
  width: 44px;
}

.card-mastercard-gray {
  background-image: url('@cpl_commerce/images/payment-mastercard-g.svg');
  width: 38px;
}

.card-amex-gray {
  background-image: url('@cpl_commerce/images/payment-amex-g.svg');
  width: 45px;
}

.card-discover-gray {
  background-image: url('@cpl_commerce/images/payment-discover-g.svg');
  width: 57px;
}

.card-visa-white {
  background-image: url('@cpl_commerce/images/payment-visa-w.svg');
  width: 44px;
}

.card-mastercard-white {
  background-image: url('@cpl_commerce/images/payment-mastercard-w.svg');
  width: 38px;
}

.card-amex-white {
  background-image: url('@cpl_commerce/images/payment-amex-w.svg');
  width: 45px;
}

.card-discover-white {
  background-image: url('@cpl_commerce/images/payment-discover-w.svg');
  width: 57px;
}
