.paragraph--type--imgtxtbanner {
  // Banner overlay
  &.cpl-overlay-dark,
  &.cpl-overlay-light {
    .image-box-row {
      position: relative;

      &::after {
        content: '';
        height: 100%;
        width: 100%;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
      }
    }
  }

  // Fix displaying all slides when slideshow started
  &.cpl-slideshow {
    .content-grid {
      .content-grid-item:not(:first-child),
      .field-item:not(:first-child) {
        display: none;
      }
    }
  }

  // Overlays over background image
  &.cpl-overlay-light {
    .image-box-row {
      &::after {
        background-color: $imgbnr-opacity-light;
      }
    }
  }

  &.cpl-overlay-dark {
    .image-box-row {
      &::after {
        background-color: $imgbnr-opacity-dark;
      }
    }
  }

  .content-grid > .field-item {
    @include clearfix;
  }

  .content-box {
    @include clearfix;
    position: relative;
  }

  .text-box-row {
    z-index: 1;

    padding: $text-block-padding-rem !important;
    @include breakpoint(medium only) {
      padding: $text-block-padding-rem / 1.5 !important;
    }
    @include breakpoint(small only) {
      padding: $text-block-padding-rem / 2 !important;
    }
  }

  &.cpl-text-row-max-1,
  &.cpl-text-row-max-2 {
    .text-box-row {
      @include breakpoint(small only) {
        padding: 0 !important;
      }
    }
  }

  .image-box-row {
    img {
      width: 100%;
    }
  }

  // Responsive video maintaining aspect ratio
  .video-embed-field-responsive-video {
    overflow: hidden;
    height: 100%;
    padding: 0;
    position: relative !important;
    background-color: $primary-color;

    iframe {
      box-sizing: border-box;
      min-height: 100%;
      min-width: 100%;
      height: 100vw;
      width: 300vh;
      left: 50%;
      transform: translate(-50%, -50%);
      position: absolute !important;
      top: 50%;
    }
  }

  // Slideshow fixes
  &.cpl-mobile-layout-1 {
    &.cpl-slideshow {
      @include breakpoint(small only) {
        .slick-arrow {
          top: 30%;
        }
      }
    }
  }
}
