.cpl-contact-page-4 {
  .main {
    @include grid-row;
    padding-left: 20px;
    padding-right: 20px;
  }

  .paragraph--type--imgtxtbanner {
    margin-left: calc(-50vw + 50%);
    margin-right: calc(-50vw + 50%);
    max-width: none;
  }

  .webform-submission-form {
    padding: 30px;
    box-shadow: 0px 0px 22px 0px rgba(0, 0, 0, 0.3);
    background-color: $white;

    @include breakpoint(medium) {
      @include grid-col-size(10);
      @include grid-column-offset(1);
      position: relative;
      top: -100px;
    }

    @include breakpoint(large) {
      @include grid-col-size(6);
      @include grid-column-offset(0);
      float: right;
    }

    @include breakpoint(small down) {
      margin-left: -20px;
      margin-right: -20px;
    }
  }

  .cpl-contact-page-block {
    padding-top: 30px;
    padding-bottom: 30px;
    background-color: $contact-sidebar-bg-color;
    color: $contact-sidebar-text-color;

    @include breakpoint(large) {
      @include grid-col-size(6);
      float: left;
    }

    @include breakpoint(medium down) {
      text-align: center;
    }

    @include breakpoint(medium only) {
      padding-top: 0;
      margin-top: -30px;
    }

    a {
      color: $contact-sidebar-link-color;

      &:hover {
        color: $contact-sidebar-link-color-h;
      }
    }
  }
}
