.cpl-form-reference {
  background-color: transparent;

  &.cpl-form-type-1 {
    text-align: center;
    padding-top: 40px;
    padding-bottom: 30px;
    color: $form-reference-text-color;

    .block-form-container {
      @include breakpoint(large) {
        width: 70%;
        margin-left: auto !important;
        margin-right: auto !important;
        float: none;
      }
    }

    .block-form-image {
      margin-bottom: 20px;
    }

    .block-form-title {
      font-family: map-get($form-reference-heading, font-family);
      color: map-get($form-reference-heading, color);
      font-weight: map-get($form-reference-heading, font-weight);
      margin-bottom: 10px;
    }

    .block-form-description {
      margin-bottom: 30px;
    }

    a {
      color: $form-reference-link-color;

      &:hover {
        text-decoration: underline;
      }
    }
  }
}

// Custom forms styles
.form-subscribe-signup {
  position: relative;

  input[type='email'] {
    height: 44px;
    @if $form-reference-display-shadow == true {
      box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.2) !important;
    }
    border: none;
    padding-left: 15px;
  }

  label {
    display: none;
  }

  .form-actions {
    position: absolute;
    top: 0;
    right: 0;
  }

  .button {
    background-color: map-get($form-reference-button, background-color);
    color: map-get($form-reference-button, color);
    margin: 0;

    &:hover {
      background-color: map-get($form-reference-button, background-color-h);
      color: map-get($form-reference-button, color-h);
    }
  }
}
