.cpl-page-footer {
  .footer-wide-top {
    background-color: $footer-bg-color;
    color: $footer-text-color;
    font-family: $footer-font-family;
    font-size: $footer-font-size - 2px;
    padding-top: 15px;
    padding-bottom: 15px;
    @include breakpoint(large) {
      font-size: $footer-font-size;
      padding-top: 20px;
      padding-bottom: 25px;
    }

    a {
      color: $footer-text-color;
      transition: color ease 0.2s;
      padding: 0.375rem 0;

      &:hover {
        color: $footer-text-color-h;
      }

      @include breakpoint(medium down) {
        padding: 0.6rem 0;
      }
    }

    .menu {
      li {
        display: block;
      }
    }

    .footer-title {
      font-size: $footer-title-size;
      font-family: $footer-title-font-family;
      font-weight: $footer-title-font-weight;
      color: $footer-title-color;
      margin-top: 0.5rem;
      margin-bottom: 0.7rem;
      text-transform: $footer-title-text-transform;
    }
  }

  .footer-menu-block {
    margin-top: 12px;

    @include breakpoint(small only) {
      display: none;
    }
  }

  &.has-title {
    .footer-menu-block {
      margin-top: 0;
    }
  }

  .footer-contact-block {
    .footer-title {
      text-transform: $footer-title-text-transform;
      font-size: $footer-title-size;
      margin-bottom: 10px;
    }
  }

  .footer-contact-item {
    @include breakpoint(medium down) {
      margin-bottom: 10px;
    }

    a {
      padding: 0;
      margin-bottom: 5px;
    }

    @include breakpoint(small only) {
      display: none !important;
    }
  }

  .field-type-telephone,
  .field-type-email {
    padding-left: 21px;
    position: relative;

    a {
      &::before {
        font-family: 'Font Awesome 5';
        font-weight: bold;
        position: absolute;
        left: 0;
      }
    }
  }

  .field-type-telephone {
    a {
      &::before {
        content: '\f095';
      }
    }
  }

  .field-type-email {
    a {
      &::before {
        content: '\f0e0';
      }
    }
  }

  .field-name-field-block-logo {
    img {
      width: 100px;
    }
  }

  .footer-links {
    margin-left: -4px;

    a {
      @include breakpoint(small only) {
        color: $dark-gray;
      }
    }

    @include breakpoint(small only) {
      margin-bottom: 0;
    }

    li {
      display: inline-block;
      margin: 0 4px;

      i {
        font-size: 26px;

        @include breakpoint(small only) {
          font-size: 36px;
        }
      }
    }
  }
}

.bottom-bar {
  background-color: $footer-bar-bg-color;
  color: $footer-bar-text-color;
  font-size: $footer-bar-font-size;
  font-family: $footer-bar-font-family;
  padding-top: rem-calc(12);
  padding-bottom: rem-calc(12);

  p {
    margin: 0;
  }

  &-content {
    @include breakpoint(small only) {
      display: flex;
      flex-direction: column-reverse;
      align-items: center;
    }
  }

  &-copyright {
    @include breakpoint(small only) {
      margin-top: 10px;
    }
  }

  &-privacy {
    ul {
      text-align: right;
      margin: 0;
      padding: 0;

      @include breakpoint(small only) {
        text-align: center;
      }
    }

    li {
      display: inline-block;
      margin-left: 1rem;

      a {
        color: $footer-bar-text-color;
        padding: 0;

        &:hover {
          text-decoration: underline;
        }
      }
    }
  }
}

.bottom-bar-extra {
  background-color: darken($footer-bar-bg-color, 5%);
  color: $footer-bar-text-color;
  font-size: $footer-bar-font-size - 2px;
  font-family: $footer-bar-font-family;
  padding-top: 0.8rem;
  padding-bottom: 0.8rem;

  p {
    margin: 0;
    line-height: 1.3;
  }
}

// Padding on shop page because of fixed filter bar
.path-collections {
  .bottom-bar-extra {
    @include breakpoint(medium down) {
      padding-bottom: 70px;
    }
  }
}
