.menu--simple-mega-menu {
  position: static !important;
}

.mega-menu-wrapper {
  padding-top: 30px;
  padding-bottom: 30px;
  background-color: $white;
  box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.15);
  width: 100vw;
  position: absolute;
  left: 0;
  // Slick assigns elements width 0px when elements are hidden
  // so that's why we are hidding megamenu offscreen.
  top: -9999px;
  // Admin menu has z-index 502
  // so megamenu should be lower.
  z-index: 500;

  .content-box {
    a {
      padding-left: 0 !important;
      padding-right: 0 !important;
    }
  }

  .field-name-title,
  .node-title,
  .taxonomy-title {
    text-align: center;
    font-size: rem-calc(14);
    font-weight: bold;
  }

  .taxonomy-title {
    margin-top: -7px;
  }

  .node-title {
    margin-top: 10px;
  }

  .field-name-field-megamenu-item > .field-items {
    width: 100vw;
    display: flex;
    flex-flow: wrap;
  }

  // Set image full width
  .field-name-field-image,
  .field-name-field-product-image {
    img {
      width: 100% !important;
      height: auto !important;
    }

    .field-item {
      text-align: center;
    }
  }
}

.is-megamenu {
  &:hover {
    .mega-menu-wrapper {
      top: 128px;
      border-top: 2px solid $primary-color;
    }
  }
}

// When constraint is set use row-max-X classes
.cpl-row-max-1,
.cpl-row-max-2 {
  .field-name-field-megamenu-item > .field-items {
    width: auto !important;
  }
}

// When sticky header
.has-sticky-header {
  .is-megamenu {
    &:hover {
      .mega-menu-wrapper {
        top: 60px;
      }
    }
  }
}

.cpl-mega-menu-slideshow {
  .field-name-field-megamenu-item > .field-items {
    display: block;
    width: 100%;
  }
}

// Megamenu Style 2 (3 columns)

.cpl-mega-menu-style-2 {
  &.cpl-mega-menu-slideshow {
    display: flex;
    justify-content: space-between;

    .megamenu-slideshow-block {
      width: 34vw !important;
      margin-top: -30px;
      margin-bottom: -30px;
      margin-right: -30px;

      .slick-dots {
        bottom: 25px;
        left: 20px;
        transform: none;

        button {
          border-color: map-get($slideshow-dot, color-light);
          filter: map-get($slideshow-dot, color-light-shadow);
        }

        li {
          &.slick-active {
            button {
              &::before {
                background-color: map-get($slideshow-dot, color-light);
              }
            }
          }
        }
      }

      .slick-list {
        margin-bottom: -7px !important;
      }

      .field-name-field-parc-image {
        overflow: hidden;

        img {
          object-fit: cover;
          width: 100%;
          height: 100%;
          max-height: 450px;
          margin: 0;
        }
      }
    }

    .megamenu-ad-slideshow-item {
      position: relative;
    }

    h4 {
      color: $black;
      text-transform: none;
      font-weight: bold;
      font-size: 18px;
    }

    .field-name-field-parc-link {
      margin-top: 20px;

      a {
        padding: 12px 25px !important;
        background-color: $primary-color;
        color: $white;

        &:hover {
          background-color: $black;
          color: $white;
        }
      }
    }

    .text-box-row {
      position: absolute;
      top: 25px;
      left: 25px;
      padding: 20px 25px 30px 25px;
      background-color: rgba($white, 0.9);
      width: 60%;
    }

    .field-name-field-parc-body {
      text-transform: none;
      font-family: $body-font-family;
    }

    .field-name-field-megamenu-item > .field-items {
      width: 66vw !important;
    }
  }

  .field-name-field-image {
    padding-left: 30px;
    padding-right: 30px;
  }

  // Product and content reference (4 columns)
  .field-name-field-megamenu-item > .field-items > .field-item {
    width: 25%;
    padding-left: 30px;
    padding-right: 30px;
  }
}

// Megamenu Style 3 (4 columns)
.cpl-mega-menu-style-3 {
  // Product and content reference
  .field-name-field-megamenu-item > .field-items > .field-item {
    width: 25%;
    padding-left: 30px;
    padding-right: 30px;
  }

  .slick-slide {
    .field-item {
      padding-left: 10px;
      padding-right: 10px;
    }
  }

  .slick-dots {
    bottom: -15px;
  }
}

// Megamenu Style 4 (5 columns)
.cpl-mega-menu-style-4 {
  // Product and content reference
  .field-name-field-megamenu-item > .field-items > .field-item {
    width: 20%;
    padding-left: 30px;
    padding-right: 30px;
  }

  .slick-slide {
    .field-item {
      padding-left: 10px;
      padding-right: 10px;
    }
  }

  .slick-dots {
    bottom: -15px;
  }
}

// Megamenu width with fixed header container
.cpl-header-type-2,
.cpl-header-type-3 {
  .mega-menu-wrapper {
    left: 50%;
    margin-left: -50vw;
  }
}
