.cpl-page-footer {
  &.cpl-footer-type-5 {
    .footer-wide-top {
      padding-top: 25px;
      padding-bottom: 25px;
      background-color: $footer-bg-color;
      color: $footer-text-color;

      .footer-logo-block {
        padding-top: 9px;
        padding-right: 0;

        .field-name-field-block-logo {
          img {
            width: auto;
            max-height: 80px;
          }
        }
      }

      .footer-menu-block {
        margin-top: 20px;

        .menu {
          text-align: left;
          padding-left: 32px;
          font-size: 0%;

          li {
            display: inline-block;

            a {
              padding: 20px 0;
              color: $footer-text-color;
              font-size: $footer-font-size;

              &:hover,
              &.is-active {
                text-decoration: underline;
              }
            }

            & + li {
              padding-left: 40px;
            }
          }
        }
      }

      .footer-contact-block {
        padding-left: 0;
        margin-top: 3px;

        .footer-contact-item {
          display: block;
          text-align: right;
          margin-bottom: 0;
        }

        .field-name-body {
          p {
            margin-bottom: 0;
          }
        }

        .field-type-telephone {
          line-height: 1;
          color: $footer-text-color;
          font-size: 26px;
          font-weight: bold;
          background: none;
          margin-bottom: 10px;

          a {
            position: relative;

            &:hover {
              color: $footer-text-color;
            }

            &::before {
              left: -30px;
              margin-top: 2px;
              margin-right: 6px;
              border: 1px solid $primary-color;
              font-size: 12px;
              color: $primary-color;
              padding: 4px;
              border-radius: 50%;
            }
          }
        }
      }

      @include breakpoint(medium down) {
        padding-top: 40px;
        padding-bottom: 35px;

        .footer-logo-block {
          padding-left: 0;

          .field-name-field-block-logo {
            img {
              display: block;
              margin: 0 auto;
            }
          }
        }

        .footer-menu-block {
          display: none;
        }

        .footer-contact-block {
          padding-right: 0;
          text-align: center;

          .footer-contact-item {
            text-align: center;
          }
        }
      }

      @include breakpoint(small only) {
        .footer-logo-block {
          padding-bottom: 35px;
        }

        .footer-contact-block {
          .footer-contact-item {
            display: block !important;
          }
        }
      }
    }

    .footer-wide-bottom {
      background-color: $footer-bg-color-bottom;

      .footer-links {
        padding-top: 30px;
        padding-bottom: 30px;
        margin-bottom: 0;
        font-size: 0%;

        li {
          margin: 0 13px;
          transition: opacity ease 0.3s;
          opacity: 0.85;

          a {
            display: block;
            width: 40px;
            height: 40px;
            padding: 6px;

            // TODO: all colors into variables
            background-color: #626262;
            border-radius: 50%;

            i {
              font-size: 28px;

              &::before {
                color: #323232;
              }
            }
          }

          &:hover {
            opacity: 1;
          }

          @include breakpoint(small only) {
            margin: 0 10px;
          }
        }
      }
    }
  }
}
