// FAQs on Product page
.faqs-product {
  .accordion-title {
    font-size: 1rem !important;
  }

  .accordion-content {
    font-size: 1rem !important;
  }

  .views-row {
    .accordion-item {
      border-bottom-width: 0 !important;
    }

    &:first-child {
      .accordion-item {
        border-top-width: 2px !important;
      }
    }

    &:last-child {
      .accordion-item {
        border-bottom-width: 2px !important;
      }
    }
  }
}
