// Selects
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  text-indent: 1px;
  text-overflow: '';
  height: auto;
  box-shadow: none !important;
  padding: 0.7rem 3.6rem 0.7rem 0.8rem;
  // Foundation triangle
  background-position: right -2.7rem center;

  &:active,
  &:focus {
    box-shadow: none !important;
  }
}

select::-ms-expand {
  display: none;
}

// Global inputs
input[type='text'],
input[type='email'],
input[type='tel'],
input[type='number'],
input[type='password'],
textarea {
  box-shadow: none !important;
}

// Remove Chrome autofill background
input:-webkit-autofill,
input:-webkit-autofill:active,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus {
  -webkit-box-shadow: 0 0 0 1000px $white inset !important;
  -webkit-text-fill-color: $black !important;
}

// Buttons
.button {
  font-family: $button-font-family;
  font-weight: $button-font-weight;
  text-transform: $button-text-transform;
  font-size: $button-font-size;

  &.accent {
    @include button-style($primary-color, $black, $white);
  }

  &.rounded {
    border-radius: 40px;
    padding: 0.5em 0.8em;
  }

  &.gradient {
    &.primary {
      transition: all ease 0.25s;
      color: $white;

      background: linear-gradient(
        to bottom,
        lighten($primary-color, 10%) 0%,
        $primary-color 100%
      );

      &:hover {
        background: linear-gradient(
          to bottom,
          lighten($primary-color, 5%) 0%,
          $primary-color 100%
        );
      }
    }
  }
}

// Common floating labels
.floating-labels {
  .js-form-item {
    position: relative;
  }

  .fl-wrap {
    input,
    textarea {
      border-radius: 3px;
      outline: inherit;
      border-color: $medium-gray;

      &:focus,
      &:active {
        border: 2px solid $primary-color;
        outline: none;
      }

      @include placeholder {
        visibility: hidden;
        opacity: 0;
      }
    }

    input {
      height: 44px;
      padding-left: 1rem;
      padding-top: 22px;
      margin-bottom: 10px;
    }

    textarea {
      padding-top: 22px;
      padding-left: 1rem;
      padding-right: 1rem;
    }

    label {
      transition: all 0.2s ease-in-out;
      position: absolute;
      color: $dark-gray;
      top: 10px;
      left: 18px;
      font-size: 14px;
    }
  }

  .fl-is-active,
  .fl-has-focus {
    label {
      font-size: 11px;
      top: 2px;
      left: 18px;
    }
  }
}

// Remove blue border around reveal popup
.reval {
  &:focus {
    outline: none;
  }
}
