.callout {
  font-size: rem-calc(14);
  border: none;
  position: relative;
  background-color: transparent;
  z-index: 1;
  padding: 1.3rem 2.5rem 1.3rem 1.6rem;
  border-radius: 2px;

  p {
    margin-bottom: 0.5rem;
  }

  > :nth-last-child(2) {
    margin-bottom: 0 !important;
  }

  a {
    color: $primary-color;
    transition: color ease 0.3s;

    &:hover {
      color: $black;
    }
  }

  .close-button {
    right: 10px;
    top: 4px;
  }

  &.success {
    .close-button {
      color: darken($primary-color, 10%);

      &:focus {
        outline: none;
      }
    }
  }
}

.region-highlighted {
  .callout {
    margin-bottom: 0;
    padding-right: 30px;
    padding-left: 20px;
  }
}
