.paragraph--type--imgtxt {
  // Styles
  .content-grid {
    .content-grid-item:last-child {
      float: left;
    }
  }

  img {
    width: 100%;
  }

  .button {
    margin: 1rem 0 0;
  }

  // Row style 1
  &.cpl-row-style-1 {
    .content-grid-item {
      margin: 0;
      padding: 0;
    }

    .text-box-row {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      opacity: 0;
      transition: all 0.5s ease-in-out;
    }

    .content-box {
      position: relative;

      &:hover {
        .text-box-row {
          opacity: 1;

          @include breakpoint(small only) {
            display: none;
          }
        }
      }
    }

    .text-box-container {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      align-content: center;
      padding: $global-padding;
      text-align: center;
    }
  }

  // Row Style 2
  &.cpl-row-style-2 {
    .content-grid,
    .content-grid-item {
      @include grid-column-gutter(20);
    }

    &.cpl-position-c {
      .text-box-container {
        text-align: center;
      }
    }

    &.cpl-position-r {
      .text-box-container {
        text-align: right;
      }
    }

    .content-box {
      margin-bottom: $global-margin;
    }

    .image-box-row {
      padding-bottom: 3px;
    }

    .text-box-row {
      position: relative;

      h4,
      p {
        margin-bottom: 0;
      }
    }

    .text-box-container {
      padding: 0.7rem;
    }
  }

  // Row style 3
  &.cpl-row-style-3 {
    &.cpl-slideshow-bt,
    &.cpl-slideshow-ar {
      .image-box-row {
        @include breakpoint(small only) {
          height: inherit;
        }
      }
    }

    .content-grid,
    .content-grid-item {
      padding: 0;
      margin: 0;
    }

    .content-box {
      @include breakpoint(medium) {
        display: flex;
        flex-direction: row;
      }
    }

    .text-box-row,
    .image-box-row {
      @include breakpoint(medium) {
        @include grid-column(6, 0);
      }
    }

    .image-box-row {
      position: relative;

      overflow: hidden;

      .field-name-field-parc-image {
        height: 100%;

        .field-items,
        .field-item {
          height: 100%;
        }

        img {
          // TODO: add fallback for IE11-
          height: 100%;
          width: 100%;
          object-fit: cover;
        }
      }

      @include breakpoint(medium only) {
        height: map-get($imgtxt-rs3-image-height, medium);
        overflow: hidden;
      }

      @include breakpoint(small only) {
        height: map-get($imgtxt-rs3-image-height, small);
        overflow: hidden;
      }
    }

    .text-box-row {
      padding: 0;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    .text-box-container {
      padding: $global-padding * 3;
      @include breakpoint(small only) {
        padding: 1.5rem 2rem 3rem;
      }
    }

    &.cpl-position-c {
      .text-box-container {
        text-align: center;
      }
    }

    &.cpl-position-r {
      .text-box-container {
        text-align: right;
      }
    }

    &.cpl-alternating {
      @include breakpoint(small only) {
        .content-grid-item {
          &:nth-child(2n) {
            &.small-12 {
              .content-box {
                flex-direction: row-reverse;
              }
            }
          }
        }
      }

      @include breakpoint(medium only) {
        .content-grid-item {
          &:nth-child(2n) {
            &.medium-12 {
              .content-box {
                flex-direction: row-reverse;
              }
            }
          }

          &:nth-child(4n - 2),
          &:nth-child(4n - 3) {
            &.medium-6 {
              .content-box {
                flex-direction: row-reverse;
              }
            }
          }
        }
      }

      @include breakpoint(large) {
        .content-grid-item {
          &:nth-child(2n) {
            &.large-12 {
              .content-box {
                flex-direction: row-reverse;
              }
            }
          }

          &:nth-child(4n - 2),
          &:nth-child(4n - 3) {
            &.large-6 {
              .content-box {
                flex-direction: row-reverse;
              }
            }
          }
        }
      }
    }
  }

  // Row style 4
  &.cpl-row-style-4 {
    .content-grid-item {
      margin: 0;
      padding: 0;
    }

    .text-box-row {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      transition: all 0.5s ease-in-out;
    }

    .content-box {
      position: relative;
    }

    .text-box-container {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      align-content: center;
      padding: $global-padding;
      text-align: center;
    }
  }

  // Slideshow fixes
  &.cpl-slideshow-pg,
  &.cpl-slideshow-bt {
    padding-bottom: 100px !important;

    .slick-dots {
      bottom: -50px;
    }
  }

  // Masonry
  &.cpl-masonry {
    // Reset foundation paddings
    .content-grid {
      padding: 0;
    }

    .content-grid-item {
      padding: 0;
    }
  }
}
