// CPL settings

// Helpers generator settings
$base-size: 16px;
$base-prefix: 'cpl';
$use-rem: false;

$condensed-font-family: 'Roboto Condensed', sans-serif;

$text-block-padding: 40px;
$text-block-padding-rem: rem-calc(strip-unit($text-block-padding));

$text-colors: (#1779ba, #767676);
$bg-colors: (#3adb76, #ffae00);

// Row max widths
$row-widths: (
  1: $global-width,
  2: calc(#{$global-width} / 2)
);

// Margins and paddings for style generator
$margin-quantity: 5;
$margin-sides: (top, bottom, left, right);

$padding-quantity: 5;
$padding-sides: (top, bottom, left, right);

// Masonry gaps
$masonry-gap: (5px, 15px, 30px);

// Icon sizes
$icon-sizes: (125px, 90px);

// Blog articles
$blog-teaser-heading-font-size: 20px;
$blog-teaser-heading-font-family: $header-font-family;
$blog-teaser-font-size: 14px;
$blog-teaser-font-family: $body-font-family;
$blog-article-font-size: 17px;
$blog-article-font-family: $body-font-family;

// Trigger button
$trigger-bg-color: $primary-color;
$trigger-inner-padding: $global-padding;
$trigger-icon-size: 75px;

// ImgTxtBanner
$imgbnr-opacity-dark: rgba(0, 0, 0, 0.3);
$imgbnr-opacity-light: rgba(255, 255, 255, 0.3);

// Header
$header-font-size-first: 13px;
$header-font-size-second: 17px;
$header-text-color: #555;
$header-text-color-h: $primary-color;
$header-bg-color: $white;
$header-line-color: $primary-color;
$header-active-menu-color: $primary-color;

// Header hamburger menu button
$header-hamburger-color: $dark-gray;
$header-hamburger-color-h: $primary-color;

// Header promo block
$header-font-size-promo: 13px;
$header-promo-bg-color: $primary-color;
$header-promo-text-color: $white;

// Header submenu
$header-submenu-bg-color: rgba($white, 0.93);
$header-submenu-text-color: $black;
$header-submenu-text-color-h: $white;
$header-submenu-bg-color-h: $primary-color;
$header-submenu-shadow: drop-shadow(0px 0px 10px rgba(0, 0, 0, 0.25));

// Footer
$footer-font-size: 16px;
$footer-title-size: 16px;
$footer-title-color: #aaa;
$footer-title-font-weight: bold;
$footer-title-text-transform: none;
$footer-title-font-family: $condensed-font-family;
$footer-text-color: $black;
$footer-text-color-h: $primary-color;
$footer-bg-color: #ececec;
$footer-bg-color-bottom: #323232;
$footer-font-family: $condensed-font-family;
// Footer bottom bar color
$footer-bar-bg-color: $primary-color;
$footer-bar-text-color: $white;
$footer-bar-font-size: 13px;
$footer-bar-font-family: $condensed-font-family;
// Footer on checkout page
$footer-checkout-text-color: #a2a2a2;
$footer-checkout-text-size: 13px;

// Contact page
$contact-sidebar-bg-color: $white;
$contact-sidebar-text-color: $black;
$contact-sidebar-link-color: $primary-color;
$contact-sidebar-link-color-h: darken($primary-color, 10%);
$contact-sidebar-shadow: true;

// Cart
$cart-popup-width: 600px;

// Search
$search-popup-background-color: $light-gray;
$search-popup-text-color: $dark-gray;
$search-popup-text-color-hover: $primary-color;
$search-popup-link-color: $primary-color;
$search-popup-link-color-hover: $black;
$search-popup-input-border-color: $primary-color;
$search-popup-result-background-color: $white;
$search-popup-result-text-color: $black;

// Buttons
$button-font-family: $body-font-family;
$button-font-weight: 500;
$button-text-transform: uppercase;
$button-font-size: 1rem;

// FAQ
$faq-active-title-bg: $primary-color;
$faq-active-title-text: $white;
$faq-active-content-bg: #d9e9e8;

// Form reference block
$form-reference-text-color: $black;
$form-reference-link-color: $primary-color;
$form-reference-display-shadow: true;
$form-reference-heading: (
  color: $black,
  font-weight: 300,
  font-family: $condensed-font-family
);
$form-reference-button: (
  background-color: transparent,
  color: $dark-gray,
  background-color-h: transparent,
  color-h: $black
);

// Pager
$pager-current-background: $primary-color;

// Slideshow controls
$slideshow-arrow-container: (
  background-color: transparent,
  box-shadow: none,
  height: auto,
  width: auto,
  border: none,
  margin: 16px
);

// We have two variants: for dark and light background
// Arrows use Font Awesome 5
$slideshow-arrow: (
  next-symbol: '\f0da',
  prev-symbol: '\f0d9',
  color-dark: rgba(205, 205, 205, 0.5),
  color-light: rgba(255, 255, 255, 0.5),
  font-size: 70px,
  font-weight: bold
);

// We have two variants: for dark and light background
$slideshow-dot: (
  color-dark: #8f8f8f,
  color-light: $white,
  color-light-shadow: drop-shadow(0 0 2px rgba(0, 0, 0, 0.15)),
  size: 16px
);

// Ajax throbber
$ajax-throbber-overlay-color: rgba(255, 255, 255, 0.95);
$ajax-throbber-color: $primary-color;

// Image Text Images
$imgtxt-rs3-image-height: (
  small: 300px,
  medium: 400px
);

// Image Banner size
$paragraph-image-banner-height: (
  full: 100vh,
  1: 500px,
  2: 700px
);

// Webforms embeded in paragraphs
$paragraph-webform-button: (
  background: $primary-color,
  color: $white
);

$paragraph-webform-button-h: (
  background: $black,
  color: $white
);

// Paragraph heading size
$paragraph-heading-text-size: (
  small: (
    xs: 12px,
    sm: 14px,
    md: 16px,
    lg: 24px,
    xl: 30px
  ),
  large: (
    xs: 12px,
    sm: 14px,
    md: 20px,
    lg: 28px,
    xl: 36px
  )
);

