// IMAGE TEXT
@each $number, $map in $imgtxt {
  @each $type, $style in $map {
    @if $type == 'container' {
      .imgtxt-tb#{$number}-container {
        @each $property, $value in $style {
          #{$property}: $value !important;
        }
      }
    }
    @if $type == 'heading' {
      .imgtxt-tb#{$number}-heading {
        @each $property, $value in $style {
          #{$property}: $value !important;
        }
      }
    }
    @if $type == 'heading-h' {
      .imgtxt-tb#{$number}-heading:hover {
        @each $property, $value in $style {
          #{$property}: $value !important;
        }
      }
    }
    @if $type == 'subheading' {
      .imgtxt-tb#{$number}-subheading {
        @each $property, $value in $style {
          #{$property}: $value !important;
        }

        a {
          color: map-get($style, color) !important;

          &:hover {
            text-decoration: underline;
          }
        }
      }
    }
    @if $type == 'link' {
      .imgtxt-tb#{$number}-link .button {
        @each $property, $value in $style {
          #{$property}: $value !important;
        }
      }
    }
    @if $type == 'link-h' {
      .imgtxt-tb#{$number}-link .button:hover {
        @each $property, $value in $style {
          #{$property}: $value !important;
        }
      }
    }
    @if $type == 'overlay' {
      .paragraph--type--imgtxt.cpl-row-style-1 .imgtxt-tb#{$number}-overlay,
      .paragraph--type--imgtxt.cpl-row-style-4 .imgtxt-tb#{$number}-overlay {
        top: map-get($style, padding) !important;
        left: map-get($style, padding) !important;
        right: map-get($style, padding) !important;
        bottom: map-get($style, padding) !important;

        .text-box-container {
          background-color: map-get($style, background-color) !important;
        }
      }
    }
  }
}

