// ICON TEXT
@each $number, $map in $icntxt {
  @each $type, $style in $map {
    @if $type == 'container' {
      .paragraph--type--icntxt {
        &.cpl-text-box-#{$number} {
          &.cpl-row-style-2 .content-box,
          &.cpl-row-style-3 .content-box {
            @each $property, $value in $style {
              #{$property}: $value !important;
            }
          }
        }
      }

      .cpl-row-style-1,
      .cpl-row-style-2,
      .cpl-row-style-3 {
        .icntxt-tb#{$number}-container {
          background-color: transparent;
        }
      }

      .cpl-row-style-4 {
        .icntxt-tb#{$number}-container {
          @each $property, $value in $style {
            #{$property}: $value !important;
          }
        }
      }
    }
    @if $type == 'heading' {
      .icntxt-tb#{$number}-heading {
        @each $property, $value in $style {
          #{$property}: $value !important;
        }
      }
    }
    @if $type == 'heading-h' {
      .icntxt-tb#{$number}-heading:hover {
        //
      }
    }
    @if $type == 'subheading' {
      .icntxt-tb#{$number}-subheading {
        @each $property, $value in $style {
          #{$property}: $value !important;
        }

        a {
          color: map-get($style, color) !important;

          &:hover {
            text-decoration: underline;
          }
        }
      }
