    }
    @if $type == 'link-h' {
      .icntxt-tb#{$number}-link .button:hover {
        @each $property, $value in $style {
          #{$property}: $value !important;
        }
      }
    }
  }
}

// TEXT
.paragraph--type--txt {
  .field-name-field-par-text-1 {
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      color: inherit;
    }
  }

  // Heading color field
  &.cpl-heading-color-b {
    .field-name-field-par-text-1 {
      color: $black !important;
    }
  }

  &.cpl-heading-color-w {
    .field-name-field-par-text-1 {
      color: $white !important;
    }
  }

  &.cpl-heading-color-l {
    .field-name-field-par-text-1 {
      color: $light-gray !important;
    }
  }

  &.cpl-heading-color-m {
    .field-name-field-par-text-1 {
      color: $medium-gray !important;
    }
  }

  &.cpl-heading-color-d {
    .field-name-field-par-text-1 {
      color: $dark-gray !important;
    }
  }
}

@if (length($text-colors) != 0) {
  @for $i from 1 through length($text-colors) {
    .paragraph--type--txt.cpl-heading-color-#{$i} {
      .field-name-field-par-text-1 {
        color: nth($text-colors, $i) !important;
      }
    }
  }
}

@each $number, $map in $txt {
  @each $type, $style in $map {
    @if $type == 'subheading' {
      .paragraph--type--txt.cpl-text-box-#{$number} {
        .field-name-field-par-text-2 {
          @each $property, $value in $style {
            #{$property}: $value !important;
          }
        }
      }
    }
    @if $type == 'link' {
      .paragraph--type--txt.cpl-text-box-#{$number} {
        .button {
          @each $property, $value in $style {
            #{$property}: $value !important;
          }
        }
      }
    }
    @if $type == 'link-h' {
      .paragraph--type--txt.cpl-text-box-#{$number} {
        .button {
          &:hover {
            @each $property, $value in $style {
              #{$property}: $value !important;
            }
          }
        }
      }
    }
  }
}

// HEADING TEXT SIZE
@each $screen, $map in $paragraph-heading-text-size {
  @each $size, $value in $map {
    @if $screen == 'small' {
      .cpl-heading-text-size-#{$size} {
        font-size: $value !important;
      }
    }
    @if $screen == 'large' {
      @include breakpoint(#{$screen}) {
        .cpl-heading-text-size-#{$size} {
          font-size: $value !important;
        }
      }
    }
  }
}

// PARAGRAPH WEBFORMS
@if ($paragraph-webform-button != 0) {
  .paragraph .webform-button--submit {
    @each $property, $value in $paragraph-webform-button {
      #{$property}: $value !important;
    }
  }
}
@if ($paragraph-webform-button != 0) {
  .paragraph .webform-button--submit:hover {
    @each $property, $value in $paragraph-webform-button-h {
      #{$property}: $value !important;
    }
  }
}

// IMGTXTBANNER HEIGHT
@each $key, $value in $paragraph-image-banner-height {
  .cpl-banner-height-#{$key} .image-box-bg {
    @if $key == 'full' {
      min-height: calc(#{$value} - 60px) !important;

      @include breakpoint(medium) {
        min-height: calc(#{$value} - 80px) !important;
      }

      @include breakpoint(large) {
        min-height: calc(#{$value} - 130px) !important;
      }
    } @else {
      min-height: ceil($value / 1.7) !important;

      @include breakpoint(medium) {
        min-height: ceil($value / 1.3) !important;
      }

      @include breakpoint(large) {
        min-height: $value !important;
      }
    }
  }
  .cpl-banner-height-#{$key} .video-embed-field-responsive-video {
    @if $key == 'full' {
      height: calc(#{$value} - 60px) !important;

      @include breakpoint(medium) {
        height: calc(#{$value} - 80px) !important;
      }

      @include breakpoint(large) {
        height: calc(#{$value} - 130px) !important;
      }
    } @else {
      height: ceil($value / 1.7) !important;

      @include breakpoint(medium) {
        height: ceil($value / 1.3) !important;
      }

      @include breakpoint(large) {
        height: $value !important;
      }
    }
  }
}
