// Checkout
$checkout-font-family: Helvetica Neue, sans-serif;
$checkout-primary-color: #1990c6;
$checkout-white: #fefefe;
$checkout-black: #535353;
$checkout-dark-gray: #545454;
$checkout-light-gray: #fafafa;
$checkout-medium-gray: #e4e4e4;
$checkout-form-border: #d9d9d9;
$checkout-coupon-button: #c8c8c8;
$checkout-cart-item-weight: #717171;
$checkout-credit-card-border: #eaeaea;
$checkout-credit-card-background: #f7f7f7;
// Round badge on top right of the cart item.
$checkout-cart-badge-bg-color: #a3a3a3;
$checkout-cart-badge-text-color: $white;

// Reviews
$review-star-color: #d9b400;

// Order statuses
$order-pending: #f7d849;
$order-shipped: #5ae5a2;
// TODO: what color should be on completed?
$order-completed: #5ae5a2;
