// Add review page
.path-commerce-review {
  .page-title,
  .breadcrumbs {
    display: none;
  }

  .form-item-greetings {
    text-align: center;
    font-size: 28px;
    font-weight: bold;
    margin-top: 30px;
    margin-bottom: 25px;
    padding-left: $global-padding;
    padding-right: $global-padding;
  }

  .form-item-product-label {
    font-size: 18px;

    a {
      font-weight: bold;
      color: $primary-color;

      &:hover {
        color: $black;
      }
    }
  }

  .add-klaviyo-review-form-wrapper {
    max-width: map-get($row-widths, 2);
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.3);
    padding: 40px;
    border-radius: 5px;

    @include breakpoint(small only) {
      border-radius: 0;
      padding: $global-padding;
    }

    img {
      max-width: 300px;
      margin-top: 25px;
      margin-bottom: 30px;

      @include breakpoint(small only) {
        max-width: 275px;
      }
    }

    .form-item-field-review-vote {
      label {
        font-size: 20px;
        font-weight: bold;
        margin-bottom: 5px;

        @include breakpoint(small only) {
          margin-bottom: 10px;
        }
      }
    }

    .br-widget a::after {
      font-size: 28px;
    }

    .br-theme-fontawesome-stars .br-widget a {
      margin-right: 10px !important;
      @include breakpoint(small only) {
        margin-right: 15px !important;
      }
    }

    .form-item-field-review-body-0-value {
      margin-top: 30px;

      label {
        text-align: left;
        line-height: 1.3;
        margin-bottom: 7px;
      }
    }

    .button {
      display: inline-block;
      width: initial;
      margin-top: 20px;
      margin-bottom: 0;
    }
  }
}

// Thank you page
.commerce-klaviyo-review.thank-you-wrapper {
  text-align: center;
  padding: 40px $global-padding;

  .tick {
    &::before {
      content: '\f00c';
      color: $medium-gray;
      font-size: 60px;
      font-weight: bold;
      font-family: 'Font Awesome 5';
    }
  }

  .ty {
    font-weight: bold;
    font-size: 22px;
  }

  .ty-message {
    font-size: 18px;
    margin-bottom: 25px;
    margin-top: 7px;
  }

  .gohome-link {
    display: inline-block;
    background-color: $primary-color;
    color: $white;
    padding: 0.7rem 3rem;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 15px;
    transition: all ease 0.3s;

    &:hover {
      background-color: $black;
    }
  }
}
