.hamburger {
  font: inherit;
  color: inherit;
  text-transform: none;
  background-color: transparent;
  border: 0;
  margin: 0;
  overflow: visible;
  outline: 0;
  cursor: pointer;
  transition: opacity, filter 0.15s linear;
  height: 50px;
  width: 50px;

  &:hover {
    .hamburger-inner,
    .hamburger-inner::after,
    .hamburger-inner::before {
      background-color: $header-hamburger-color-h;
    }
  }
}

.hamburger-box {
  position: relative;
}

.hamburger-inner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.hamburger-inner,
.hamburger-inner::after,
.hamburger-inner::before {
  width: 40px;
  height: 4px;
  background-color: $header-hamburger-color;
  border-radius: 4px;
  position: absolute;
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
}

.hamburger-inner::after,
.hamburger-inner::before {
  content: '';
  display: block;
}

.hamburger-inner::before {
  top: -10px;
}

.hamburger-inner::after {
  bottom: -10px;
}
