.cpl-shop-page-style-2 {
  // Filters
  .facets-widget-links {
    .facet-item {
      list-style-type: none;
      a {
        position: relative;
        color: $dark-gray;
        transition: color ease 0.3s;
        width: 100%;
        display: block;
        padding: 8px 16px;

        &:hover {
          color: darken($dark-gray, 20%);
        }

        &.is-active {
          font-weight: bold;
          background-color: $light-gray;
          color: $black;

          &::after {
            content: '';
            width: 14px;
            height: 14px;
            background: url('@cpl_commerce/images/close-bold.svg') center
              no-repeat;
            background-size: 14px 14px;
            position: absolute;
            right: 15px;
            top: 13px;
          }

          .facet-item__status {
            display: none;
          }
        }
      }
    }

    .item-list__links {
      padding: 0;
      margin: 0;
    }
  }

  .block-facet--links {
    &:first-child {
      border-top: 1px solid $light-gray;
    }
  }

  .accordion-content {
    padding: 0;
  }
}
