// Input placeholder style
//
// Usage:
// textarea {
//   @include placeholder {
//     color: $grey;
//   }
// }
@mixin placeholder {
  &:-moz-placeholder {
    @content;
  }

  &::-moz-placeholder {
    @content;
  }

  &:-ms-input-placeholder {
    @content;
  }

  &::-webkit-input-placeholder {
    @content;
  }
}

// Truncates the text and replace overflow with ellipsis
//
// Usage:
// @include truncate(300px);
@mixin truncate($truncation-boundary) {
  max-width: $truncation-boundary;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
