.node--type-article {
  .breadcrumbs {
    margin: 0;
    padding-left: 0;
    padding-right: 0;

    @include breakpoint(small only) {
      display: none;
    }
  }

  .article-container {
    background-color: $white;

    @include breakpoint(medium) {
      box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
      padding: $global-padding $global-padding * 2 $global-padding * 2;
      margin-top: -145px;
    }

    @include breakpoint(small only) {
      padding-top: 30px;
    }
  }

  .article-title {
    font-size: rem-calc(36);
    line-height: 1.2;

    @include breakpoint(small only) {
      font-size: rem-calc(28);
    }
  }

  .posted {
    color: $dark-gray;
    margin-top: rem-calc(12);
    margin-bottom: rem-calc(36);
  }

  // TODO: add fallback for IE11
  .field-name-field-image {
    img {
      width: 100%;
      max-height: 550px;
      object-fit: cover;
    }
  }

  article {
    padding-bottom: 20px;

    h2,
    h3,
    h4,
    h5 {
      margin-top: $global-margin * 2;
    }

    h2 {
      font-size: rem-calc(24);
      @include breakpoint(small only) {
        font-size: rem-calc(22);
      }
    }

    h3 {
      font-size: rem-calc(20);
      @include breakpoint(small only) {
        font-size: rem-calc(18);
      }
    }

    h4 {
      font-size: rem-calc(16);
      @include breakpoint(small only) {
        font-size: rem-calc(16);
      }
    }

    h5 {
      font-size: rem-calc(14);
      @include breakpoint(small only) {
        font-size: rem-calc(14);
      }
    }

    ul,
    ol {
      color: lighten($black, 30%);
      padding: 0.5rem 1rem;
      line-height: 1.6;

      font-size: $blog-article-font-size - 2px;
      @include breakpoint(large) {
        font-size: $blog-article-font-size - 1px;
      }

      li:not(:last-child) {
        margin-bottom: 0;
      }
    }

    p {
      line-height: 1.75;
      font-size: $blog-article-font-size;
      font-family: $blog-article-font-family;

      @include breakpoint(medium only) {
        font-size: $blog-article-font-size - 1px;
      }

      @include breakpoint(small only) {
        font-size: $blog-article-font-size - 2px;
      }

      a {
        color: $primary-color;
      }
    }

    blockquote {
      position: relative;
      text-align: center;
      font-style: italic;
      font-size: $blog-article-font-size + 1px;
      line-height: 1.2;
      color: $dark-gray;
      border-left: none;
      border-top: 2px solid $medium-gray;
      border-bottom: 2px solid $medium-gray;
      width: 80%;
      margin: 1.6rem auto 1.8rem;
      padding: 1rem 1rem 0.2rem;

      &::before,
      &::after {
        @include vertical-center;
        font-family: 'Font Awesome 5';
        font-weight: bold;
        font-style: normal;
        color: $medium-gray;
      }

      &::before {
        content: '\f10d';
        left: -1rem;
      }

      &::after {
        content: '\f10e';
        right: -1rem;
      }
    }

    .paragraph--type--blogimage,
    .paragraph--type--blogvideo {
      margin-top: 2rem;
      margin-bottom: 3rem;
    }

    .field-name-field-par-desc {
      color: $dark-gray;
      font-size: rem-calc(13);
      border-bottom: 1px solid $medium-gray;
      padding-top: 4px;
      padding-bottom: 4px;
    }
  }
}
