// Reviews field
.comments-add {
  text-transform: uppercase;
  margin-bottom: 15px;
  font-size: 18px;

  a {
    color: $primary-color;
    text-decoration: underline;
    transition: color ease 0.3s;

    &:hover {
      color: $black;
    }
  }
}

// Single review
.comment-product {
  border-bottom: 1px solid $light-gray;
  margin-bottom: 18px;
  padding-bottom: 10px;
  font-size: rem-calc(14);

  h5 {
    font-size: rem-calc(14);
    font-weight: bold;
    color: $black;
    margin-bottom: 5px;
    font-family: $body-font-family;

    a {
      color: $black;
    }
  }

  .comment-submitted {
    font-weight: bold;
    margin-bottom: 0;
  }

  .comment-vote {
    margin-bottom: 7px;
  }
}

// Product info
.field-name-field-review-body {
  margin-top: -5px;
  margin-bottom: 10px;
}

.field-name-field-average-review,
.field-name-field-review-vote {
  font-size: rem-calc(18);
  color: $review-star-color;
}

.stars {
  margin: 0;

  li {
    text-indent: -9999px;
    width: 16px;
    height: 16px;
    background: url('@cpl_commerce/images/star-empty.svg');
    background-size: 16px 16px;
    margin-right: 3px;
    display: inline-block;
  }
}

.stars .full {
  background: url('@cpl_commerce/images/star-full.svg');
  background-size: 16px 16px;
}

.stars .half {
  background: url('@cpl_commerce/images/star-half.svg');
  background-size: 16px 16px;
}

// Top stars widget
.product-price {
  p {
    font-size: 13px;
    color: $dark-gray;

    a {
      color: $dark-gray;
      margin-left: 15px;
      text-decoration: underline;
      transition: color ease 0.3s;

      &:hover {
        color: $black;
      }
    }
  }
}

// Bottom stars widget
.product-reviews-header {
  margin-top: 25px;

  p {
    font-size: 18px;
    color: $black;
    margin-bottom: 10px;

    a {
      display: none;
    }
  }
}

.br-theme-fontawesome-stars {
  .br-widget {
    a {
      margin-right: 5px !important;

      &::after {
        color: $review-star-color !important;
        font-family: 'Font Awesome 5';
        font-size: 18px;
      }
    }

    .br-selected,
    .br-active {
      font-weight: bold;
    }
  }
}
