// Overlay settings
.ajax-progress.ajax-progress-fullscreen {
  z-index: 11000;
  position: fixed;
  height: 100vh;
  width: 100vw;
  overflow: visible;
  margin: auto;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  border-radius: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: $ajax-throbber-overlay-color !important;
}

// Display throbber overlay only in cart popup block
.path-product {
  &.is-off-canvas-open {
    .ajax-progress.ajax-progress-fullscreen {
      @include breakpoint(medium) {
        width: 600px;
        left: auto;
      }
    }
  }
}

// Custom color
.ajax-throbber {
  &.sk-spinner-pulse,
  &.sk-rotating-plane,
  &.sk-chasing-dots .sk-child,
  &.sk-double-bounce .sk-child,
  &.sk-three-bounce .sk-child,
  &.sk-cube-grid .sk-cube,
  &.sk-wandering-cubes .sk-cube,
  &.sk-double-bounce .sk-child,
  &.sk-wave .sk-rect,
  &.sk-circle .sk-child::before,
  &.sk-folding-cube .sk-cube::before,
  &.sk-fading-circle .sk-circle::before {
    background-color: $ajax-throbber-color !important;
  }
}
