.cpl-page-header {
  &.cpl-header-type-3 {
    a {
      critical: this;
      color: $header-text-color;
    }

    .header-main {
      position: relative;
      color: $header-text-color;
      max-width: map-get($row-widths, 1);
      margin-left: auto;
      margin-right: auto;

      height: 60px;
      @include breakpoint(medium) {
        height: 80px;
      }
      @include breakpoint(xlarge) {
        height: 128px;
      }

      @include breakpoint(medium only) {
        display: flex;
        align-items: center;
        justify-content: flex-start;
      }

      a {
        color: $header-text-color;
        transition: color ease 0.3s;

        &:hover {
          color: $header-text-color-h;
        }
      }

      .row {
        max-width: none;
        float: none;
        margin-left: $global-padding;
        margin-right: $global-padding;
      }
    }

    .header-logo {
      @include absolute-center;
      @include breakpoint(xlarge) {
        background-color: $header-bg-color;
        padding-left: 50px;
        padding-right: 50px;
      }

      img {
        width: auto;
        height: 120px;
        @include breakpoint(medium large) {
          height: 70px;
        }
        @include breakpoint(small only) {
          height: 60px;
        }
      }
    }

    .header-content {
      @include breakpoint(medium) {
        display: flex;
        flex-direction: column;
        padding-left: 15px;
        padding-right: 15px;
      }
      @include breakpoint(xlarge) {
        display: block;
        padding-left: 0;
        padding-right: 0;
      }

      @include breakpoint(medium only) {
        margin-left: auto;
      }
    }

    .header-contacts {
      padding-top: 17px;
      display: flex;

      a {
        margin-right: 13px;
      }

      .fas {
        margin-right: 2px;
      }

      @include breakpoint(large) {
        float: left;
      }

      @include breakpoint(large down) {
        display: none;
      }
    }

    .header-nav-main {
      text-transform: uppercase;
      margin-right: -0.8rem;
      float: right;

      @include breakpoint(large down) {
        display: none;
      }

      li {
        a {
          padding: 1.3rem 0.8rem;
          @include breakpoint(large) {
            padding: 1.3rem 0.6rem;
          }

          &.is-active {
            color: $header-text-color-h !important;
          }
        }
      }
    }

    .header-nav-shop,
    .header-nav-commerce {
      li {
        a {
          @include breakpoint(medium) {
            padding-top: 1rem;
            padding-bottom: 1rem;
          }

          @include breakpoint(large) {
            padding-top: 1.7rem;
            padding-bottom: 1.7rem;
          }

          @include breakpoint(large only) {
            padding-left: 1.3rem;
          }

          @include breakpoint(xlarge only) {
            padding-left: 1.3rem;
          }
        }
      }
    }

    .header-nav-shop {
      @include breakpoint(large down) {
        display: none;
      }

      li {
        &:first-child {
          a {
            padding-left: 0;
          }
        }

        &.menu-item--active-trail {
          > a {
            color: $header-active-menu-color;
            position: relative;

            &::after {
              @include css-triangle(10px, $header-active-menu-color, up);
              position: absolute;
              bottom: -2px;
              left: 50%;
              transform: translateX(-50%);
            }
          }
        }
      }
    }

    .header-nav-commerce {
      font-weight: 300;
      float: right;

      @include breakpoint(large) {
        border-bottom: none;
        width: inherit;
      }

      @include breakpoint(large down) {
        @include vertical-center;
        right: 15px;
      }

      .menu {
        float: right;

        @include breakpoint(xxlarge) {
          margin-right: -#{$global-padding};
        }
      }

      li {
        position: relative;

        @include breakpoint(large) {
          margin-right: 0;
        }

        @include breakpoint(medium down) {
          width: 55px;
        }

        @include breakpoint(small down) {
          width: 40px;
        }

        a {
          @include breakpoint(medium) {
            margin-left: 25px;
          }

          @include breakpoint(xlarge) {
            text-indent: 0;
          }

          @include breakpoint(large only) {
            padding-bottom: 1rem !important;
          }

          @include breakpoint(xlarge only) {
            padding-bottom: 1rem !important;
          }

          @include breakpoint(large down) {
            font-size: 0;
          }
        }
      }

      .js-cart-items {
        @include breakpoint(large down) {
          top: 3px;
        }

        @include breakpoint(medium down) {
          top: -3px;
        }

        @include breakpoint(small only) {
          top: -8px;
          left: 20px;
        }
      }
    }

    .header-row-top {
      font-family: $condensed-font-family;
      font-size: $header-font-size-first;

      @include breakpoint(medium) {
        padding-left: 0;
        padding-right: 0;
      }

      @include breakpoint(xlarge) {
        border-bottom: 2px solid $header-line-color;
      }
    }

    .header-row-bottom {
      font-size: $header-font-size-second;
      font-family: $condensed-font-family;
      text-transform: uppercase;

      @include breakpoint(medium) {
        display: flex;
        justify-content: flex-start;
      }

      @include breakpoint(xlarge) {
        display: block;
      }

      @include breakpoint(medium only) {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        flex-grow: 1;
        margin-right: 35px;
      }
    }

    .header-promo {
      background-color: $header-promo-bg-color;
      color: $header-promo-text-color;
      text-align: center;
      padding-top: 10px;
      padding-bottom: 10px;
      font-size: $header-font-size-promo;
      text-transform: uppercase;
      box-shadow: 0px 10px 20px 0px rgba(0, 0, 0, 0.25);
      position: relative;
      z-index: 1;

      a {
        color: $header-promo-text-color;
        text-decoration: underline;
      }

      p {
        margin: 0;
      }
    }

    .field-name-field-block-logo {
      display: inline-block;
    }

    .field-name-field-block-phone {
      margin-right: 20px;
    }

    .header-hamburger {
      position: absolute;
      top: 50%;
      transform: translateY(-45%);
      left: 15px;

      @include breakpoint(xlarge) {
        display: none;
      }
    }
  }
}

.has-sticky-header {
  .header-row-bottom {
    @include breakpoint(medium) {
      padding-left: 1rem;
      padding-right: 1rem;
    }
  }
}
