.collection-listing {
  .views-row:last-child {
    float: left;
  }
}

.cpl-term-collection-style-1 {
  padding-left: 20px;
  padding-right: 20px;
  margin-bottom: 35px;

  .field-name-field-image {
    img {
      width: 100% !important;
      height: auto !important;
    }

    .field-item {
      text-align: center;
    }
  }

  .taxonomy-title {
    text-align: center;
    font-size: 14px;
    font-weight: bold;
    color: $primary-color;
    margin-top: -7px;

    a {
      color: $primary-color;

      &:hover {
        color: $black;
      }
    }
  }
}
