.paragraph--type--imgtxtbanner {
  &.cpl-row-style-2 {
    .content-box {
      @include breakpoint(small only) {
        display: flex;
        flex-direction: column;
      }
    }

    .text-box-row {
      @include vertical-center;

      @include grid-col-size(11);
      @include breakpoint(medium) {
        @include grid-col-size(5);
      }
      @include breakpoint(large) {
        @include grid-col-size(4);
      }

      @include breakpoint(small only) {
        @include absolute-center;
      }
    }

    &.cpl-mobile-layout-1 {
      .text-box-row {
        @include breakpoint(small only) {
          position: relative !important;
          transform: none !important;
          width: 100% !important;
          left: 0 !important;
        }
      }
    }

    .text-box-container {
      @include grid-col-size(12);

      @include breakpoint(xlarge) {
        @include grid-col-size(10);
      }
    }

    .image-box-container {
      @include grid-col-size(12);
    }

    // Diferrent style for text box 2
    &.cpl-text-box-2 {
      @include grid-col-size(12);
      margin-bottom: 0 !important;

      .content-box {
        margin-bottom: 0 !important;
      }
    }

    &.cpl-row-max-1 {
      .text-box-row {
        @include grid-col-size(11);
        @include breakpoint(medium) {
          @include grid-col-size(5);
        }
        @include breakpoint(large) {
          @include grid-col-size(4);
        }
      }
    }

    // With text constraint
    &.cpl-text-row-max-1,
    &.cpl-text-row-max-2 {
      .text-box-row {
        @include grid-col-size(12);

        @include breakpoint(medium) {
          @include absolute-center;
        }

        @include breakpoint(small only) {
          @include grid-col-size(11);
          @include absolute-center;
        }
      }

      .text-box-container {
        padding: $text-block-padding !important;

        @include breakpoint(small only) {
          padding: $text-block-padding / 2 !important;
        }
      }

      &.cpl-position-l {
        .text-box-container {
          @include breakpoint(medium) {
            float: left;
          }
        }
      }

      &.cpl-position-r {
        .text-box-row {
          right: inherit;
        }

        .text-box-container {
          @include breakpoint(medium) {
            float: right;
          }
        }
      }

      &.cpl-position-c {
        .text-box-container {
          text-align: center;
          float: none;
        }
      }

      &.cpl-position-tl,
      &.cpl-position-tr,
      &.cpl-position-bl,
      &.cpl-position-br {
        .text-box-row {
          left: 50%;
          transform: translateX(-50%);
        }
      }

      &.cpl-position-tr {
        .text-box-container {
          float: right;
        }
      }

      &.cpl-position-br {
        .text-box-container {
          float: right;
        }
      }
    }

    // Text constraint
    &.cpl-text-row-max-1 {
      .text-box-row {
        max-width: map-get($row-widths, 1) !important;
        background-color: transparent !important;
        box-shadow: none !important;
      }

      .text-box-container {
        @include grid-col-size(12);

        @include breakpoint(medium) {
          width: 45%;
        }

        @include breakpoint(large) {
          @include grid-col-size(5);
        }
      }
    }

    &.cpl-text-row-max-2 {
      .text-box-row {
        max-width: map-get($row-widths, 2) / 2 !important;
        background-color: transparent !important;
        box-shadow: none !important;
      }

      .text-box-container {
        @include grid-col-size(12);
      }
    }

    // No constraint
    &.cpl-position-l {
      .text-box-row {
        left: 0;

        @include breakpoint(small only) {
          @include absolute-center;
        }
      }
    }

    &.cpl-position-r {
      .text-box-row {
        right: 0;

        @include breakpoint(small only) {
          @include absolute-center;
        }
      }
    }

    &.cpl-position-c {
      .text-box-row {
        @include absolute-center;
      }

      .text-box-container {
        @include grid-col-size(12);
        text-align: center;
      }
    }

    @include breakpoint(medium) {
      &.cpl-position-t {
        .text-box-row {
          top: 0;
          left: 50%;
          transform: translate(-50%, 0);
        }
      }

      &.cpl-position-b {
        .text-box-row {
          top: initial;
          left: 50%;
          bottom: 0;
          transform: translate(-50%, 0);
        }
      }

      &.cpl-position-tl {
        .text-box-row {
          top: 0;
          left: 0;
          transform: none;
        }
      }

      &.cpl-position-tr {
        .text-box-row {
          top: 0;
          right: 0;
          transform: none;
        }
      }

      &.cpl-position-bl {
        .text-box-row {
          top: initial;
          bottom: 0;
          left: 0;
          transform: none;
        }
      }

      &.cpl-position-br {
        .text-box-row {
          top: initial;
          bottom: 0;
          right: 0;
          transform: none;
        }
      }

      &.cpl-text-row-max-1,
      &.cpl-text-row-max-2 {
        &.cpl-position-t,
        &.cpl-position-b {
          .text-box-container {
            margin: 0 auto;
          }
        }
      }
    }

    // Alternating.
    &.cpl-alternating {
      .content-grid > .field-item:nth-child(odd),
      .slick-track > .slick-slide:nth-child(odd) {
        .text-box-row {
          @include breakpoint(medium) {
            left: 0;
            right: auto;
          }

          @include breakpoint(small only) {
            @include absolute-center;
          }
        }
      }

      .content-grid > .field-item:nth-child(even),
      .slick-track > .slick-slide:nth-child(even) {
        .text-box-row {
          @include breakpoint(medium) {
            left: auto;
            right: 0;
          }

          @include breakpoint(small only) {
            @include absolute-center;
          }
        }
      }
    }
  }
}
