.view-faqs {
  padding-bottom: 50px;
  position: relative;

  h3 {
    font-family: $body-font-family;
    color: $black;
    font-weight: bold;
    margin-top: 50px;
    padding-bottom: 15px;
    margin-bottom: 0;
    padding-left: $global-padding;
    border-bottom: 1px solid $light-gray;

    font-size: rem-calc(22);
    @include breakpoint(large) {
      font-size: rem-calc(28);
    }
  }

  .views-exposed-form {
    display: flex;
    justify-content: center;
    margin-top: 15px;
    width: 100%;
    @include breakpoint(large) {
      width: 60%;
    }

    @include horizontal-center;
    top: -170px;

    > div {
      margin-left: 7px;
      margin-right: 7px;
    }

    input[type='text'] {
      height: 44px;
      border: none;
      width: 100%;
    }

    select {
      border: none;
      height: 44px;
      width: 220px;
      margin-left: 10px;

      @include breakpoint(small only) {
        width: 88vw;
      }
    }

    label {
      display: none;
    }

    .js-form-type-textfield {
      @include breakpoint(small only) {
        display: none;
      }
    }

    .form-item-search-faq {
      width: 100%;
    }
  }
}

.path-faqs {
  .cpl-layout-paragraphs {
    .columns {
      @include breakpoint(small only) {
        padding: 0 !important;
      }
    }
  }

  .paragraph--type--imgtxtbanner.cpl-row-style-2 {
    .text-box-row {
      top: 50% !important;
    }
  }
}

// FAQ page
.cpl-faqs-page {
  .image-box-bg {
    min-height: 500px !important;
  }

  .text-box-row {
    background-color: transparent !important;

    @include breakpoint(small only) {
      // overrides default imgtxtbanner settings
      position: absolute !important;
      top: 35px !important;
    }
  }

  // TODO: add a field to wrap heading with H1
  // H4 displays by default
  .h1,
  .h4,
  .field-name-field-parc-header,
  .text-box-container {
    color: $white !important;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
  }

  .text-box-container {
    font-size: rem-calc(20);
    width: 100% !important;
  }

  .h4,
  .field-name-field-parc-header {
    font-size: rem-calc(42);
  }
}

// FAQ style 1
.cpl-faq-style-1 {
  .accordion-item {
    .accordion-title {
      transition: all ease 0.2s;
      line-height: 1.5;
      padding: 0.8rem 2.5rem 0.8rem 1rem;

      font-size: rem-calc(16);
      @include breakpoint(large) {
        font-size: rem-calc(20);
      }

      &[aria-expanded='false'] {
        background-color: transparent !important;
        color: $dark-gray !important;
      }

      &:hover,
      &:active,
      &:focus {
        background-color: $faq-active-title-bg;
        color: $faq-active-title-text;
      }

      &::before {
        top: 40% !important;
      }
    }

    .accordion-content {
      padding: 30px 30px 15px;
      position: relative;

      font-size: rem-calc(15);
      @include breakpoint(large) {
        font-size: rem-calc(17);
      }

      p {
        line-height: 1.75;
      }
    }

    // Active item
    &.is-active {
      .accordion-title {
        background-color: $faq-active-title-bg;
        color: $faq-active-title-text;
      }

      .accordion-content {
        background-color: $faq-active-content-bg;
        border-bottom-color: $faq-active-content-bg;

        &::before {
          @include css-triangle(14px, $faq-active-title-bg, right);
          position: absolute;
          left: 30px;
          top: -14px;
        }
      }
    }
  }
}

// Don't change tabs colors inside product info accordion.
.accordion-product {
  .accordion-faq {
    .cpl-faq-style-1 {
      .accordion-item {
        .accordion-title {
          &[aria-expanded='false'] {
            background-color: $faq-active-title-bg !important;
            color: $faq-active-title-text !important;
          }
        }
      }
    }
  }
}
