.paragraph--type--imgtxtbanner {
  &.cpl-row-style-3 {
    .content-box {
      display: flex;
      flex-wrap: nowrap;
      position: relative;

      @include breakpoint(small only) {
        flex-direction: column !important;
      }
    }

    .text-box-row {
      display: flex;
      align-items: center;

      @include grid-col-size(11);
      @include breakpoint(medium) {
        @include grid-col-size(5);
      }
      @include breakpoint(large) {
        @include grid-col-size(4);
      }

      @include breakpoint(small only) {
        position: absolute;
        transform: translateY(-50%);
      }
    }

    &.cpl-mobile-layout-1 {
      .text-box-row {
        @include breakpoint(small only) {
          @include grid-col-size(12);
          position: relative !important;
          transform: none !important;
          left: 0 !important;
        }
      }
    }

    .image-box-row {
      @include grid-col-size(8);
      @include breakpoint(small only) {
        @include grid-col-size(12);
      }
    }

    &.cpl-position-l {
      .content-box {
        flex-direction: row-reverse;
      }

      .text-box-row {
        left: 0;

        @include breakpoint(small only) {
          @include absolute-center;
        }
      }
    }

    &.cpl-position-r {
      .content-box {
        flex-direction: row;
      }

      .text-box-row {
        right: 0;

        @include breakpoint(small only) {
          @include absolute-center;
        }
      }
    }

    &.cpl-position-c {
      .text-box-container {
        text-align: center;
      }

      .text-box-row {
        @include breakpoint(small only) {
          left: 50%;
          transform: translate(-50%, -50%);
        }
      }
    }

    // Alternating.
    &.cpl-alternating {
      .content-grid > .field-item:nth-child(odd),
      .slick-track > .slick-slide:nth-child(odd) {
        .content-box {
          flex-direction: row;
        }
      }

      .content-grid > .field-item:nth-child(even),
      .slick-track > .slick-slide:nth-child(even) {
        .content-box {
          flex-direction: row-reverse;
        }
      }
    }
  }
}
