.cpl-page-footer {
  &.cpl-footer-type-4 {
    .footer-wide-top {
      @include breakpoint(small only) {
        display: none;
      }
    }

    .footer-wide-bottom {
      background-color: $primary-color;
      color: $white;

      a {
        color: $white;
      }
    }

    .footer-links {
      padding-top: 35px;
      padding-bottom: 35px;
      margin-bottom: 0;

      li {
        margin: 0 15px;
        transition: opacity ease 0.3s;
        opacity: 0.85;

        &:hover {
          opacity: 1;
        }

        @include breakpoint(small only) {
          margin: 0 10px;
        }

        i {
          font-size: 40px;
        }
      }
    }
  }
}
