.upper {
  text-transform: uppercase;
}

.bold {
  font-weight: bold;
}

.is-fixed {
  position: fixed;
  right: 0;
  left: 0;
  top: 0;
  bottom: 0;
}

.background-image {
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
  width: 100%;
}

// Fixed width <main> container on the page.
.cpl-page-row-max-1,
.cpl-page-row-max-2 {
  .main {
    margin-left: auto !important;
    margin-right: auto !important;
  }
}

.cpl-page-row-max-1 {
  .main {
    max-width: map-get($row-widths, 1) !important;
  }
}

.cpl-page-row-max-2 {
  .main {
    max-width: map-get($row-widths, 2) !important;
  }
}
