.cpl-page-footer {
  &.cpl-footer-type-6 {
    .footer-wide-top {
      padding: 25px;
      background-color: $footer-bg-color !important;
      color: $footer-text-color !important;
    }

    .field-type-email,
    .field-type-telephone {
      a {
        &::before {
          content: none;
        }
      }
    }

    .footer-contact-block {
      margin-top: 23px;

      @include breakpoint(medium down) {
        display: none;
      }
    }

    .footer-contact-item {
      text-align: center;
      padding-top: 5px;
      padding-bottom: 5px;

      @include breakpoint(large) {
        text-align: right;
      }
    }

    .footer-logo-block {
      display: flex;
      align-items: center;

      @include breakpoint(small only) {
        flex-direction: column;
        justify-content: center;
        text-align: center;
      }
    }

    .field-name-body {
      font-size: 20px;

      h3 {
        font-size: $footer-title-size;
        font-weight: bold;
        font-family: $footer-title-font-family;
      }

      p {
        margin-bottom: 0;
        font-size: $footer-font-size;
      }
    }

    .field-name-field-block-logo {
      @include breakpoint(medium) {
        border-right: 1px solid $dark-gray;
        padding-right: 20px;
        margin-right: 20px;
      }
    }
  }
}
