// Page styles.
.cpl-blog-page-style-2 {
  &.row,
  .columns {
    padding: 0 !important;
    margin: 0 !important;
  }

  .views-row {
    @include breakpoint(medium) {
      // 12 cols
      &:nth-child(odd) {
        .content-box {
          flex-direction: row;
        }

        .text-box-row {
          &::after {
            @include css-triangle(26px, $light-gray, left);
            @include vertical-center;
            left: -23px;
          }
        }
      }

      &:nth-child(even) {
        .content-box {
          flex-direction: row-reverse !important;
        }

        .text-box-row {
          &::after {
            @include css-triangle(26px, $light-gray, right);
            @include vertical-center;
            right: -23px;
          }
        }
      }
    }
  }
}

// Article styles.
.cpl-article-row-style-2 {
  .content-box {
    display: flex;

    @include breakpoint(small only) {
      flex-direction: column;
    }
  }

  .text-box-row,
  .image-box-row {
    @include breakpoint(medium) {
      width: 50%;
      flex: 0 0 50%;
    }
  }

  .text-box-row {
    background-color: $light-gray;
    padding: 30px;
    position: relative;
    display: flex;
    justify-content: center;
  }

  .text-box-container {
    @include breakpoint(xlarge) {
      @include absolute-center;
      width: 75%;
    }
  }

  .image-box-row {
    overflow: hidden;

    .field-type-image,
    .field-items,
    .field-item,
    a {
      height: 100%;
      display: block;
    }

    img {
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }

  .posted {
    font-size: $blog-teaser-font-size;
    color: $dark-gray;
    margin-top: 10px;
    margin-bottom: 5px;

    @include breakpoint(xlarge) {
      font-size: $blog-teaser-font-size + 2px;
    }
  }

  .node-title {
    font-size: $blog-teaser-font-size + 2px;
    line-height: 1.2;
    font-weight: bold;

    @include breakpoint(xlarge) {
      font-size: $blog-teaser-font-size + 6px;
    }

    a {
      color: $black;

      &:hover {
        color: $primary-color;
      }
    }
  }

  .node-readmore {
    margin-top: 10px;
    font-weight: bold;
    font-family: $blog-teaser-font-family;
    font-size: $blog-teaser-font-size;

    @include breakpoint(xlarge) {
      font-size: $blog-teaser-font-size + 2px;
    }

    a {
      color: $primary-color;

      &:hover {
        color: $black;
      }
    }
  }

  .field-name-field-blog-description {
    font-family: $blog-teaser-font-family;

    font-size: $blog-teaser-font-size;
    @include breakpoint(xlarge) {
      font-size: $blog-teaser-font-size + 4px;
    }
  }
}
