.user-page {
  &-first {
    overflow: auto;
    position: relative;

    @include breakpoint(large) {
      height: calc(100vh - 132px);
    }
  }

  &-container {
    width: 100%;
    padding-left: $global-padding * 2;
    padding-right: $global-padding * 2;

    @include breakpoint(medium) {
      width: 80%;
      margin: 0 auto;
    }

    @include breakpoint(large) {
      width: 70%;
      padding-left: 0;
      padding-right: 0;
    }
  }

  &-links {
    text-align: center;
    text-transform: uppercase;
    margin: 0 auto;
    margin-bottom: 50px;
    font-size: rem-calc(12);
    color: $primary-color;

    a {
      color: $primary-color;
      font-weight: 500;
      transition: all ease 0.3s;

      &:hover {
        color: $black;
      }

      &:not(:last-child) {
        &::after {
          content: '/';
          margin-left: 10px;
          margin-right: 7px;
        }
      }
    }
  }

  .form-centered {
    @include breakpoint(large) {
      position: absolute;
      left: 50%;
      top: 45%;
      transform: translate(-50%, -50%);
    }
  }

  h1 {
    text-align: center;
    color: $dark-gray;
    font-size: rem-calc(33);
    margin-bottom: 33px;
    margin-top: 50px;
  }

  .button-group {
    display: none;
  }

  .breadcrumbs {
    // TODO: hide breadcrumbs in module
    display: none;
  }

  // Form elements
  .js-form-type-textfield,
  .js-form-type-email,
  .js-form-type-password,
  .js-form-type-tel {
    position: relative;

    input[type='text'],
    input[type='email'],
    input[type='password'],
    input[type='tel'] {
      box-shadow: none;
      height: 54px;
      font-family: $condensed-font-family;
      border-top: none;
      border-left: none;
      border-right: none;
      border-bottom: 2px solid $medium-gray;
      font-size: 22px;
      padding: 0;
      margin-bottom: 22px;
      color: $dark-gray;

      &:focus,
      &:active {
        border-bottom: 2px solid $primary-color;
      }

      @include placeholder {
        visibility: hidden;
        opacity: 0;
      }
    }

    label {
      position: absolute;
      top: 15px;
      left: 0;
      color: $medium-gray;
      font-size: 13px;
      font-weight: bold;
      text-transform: uppercase;
      transition: all 0.2s ease-in-out;
    }
  }

  .fl-is-active,
  .fl-has-focus {
    label {
      font-size: 13px;
      top: -10px;
      left: 0;
    }
  }

  .js-form-type-select {
    label {
      display: none;
    }

    select {
      height: 54px;
      background-color: $white;
      box-shadow: none;

      &:active,
      &:focus {
        border-bottom: 2px solid $primary-color;
        outline: none;
      }
    }
  }

  .js-form-type-email,
  .js-form-type-password {
    .description {
      display: none;
    }
  }

  .button:not(.js-hide) {
    height: 50px;
    font-weight: 500;
    font-family: $body-font-family;
    text-transform: uppercase;
    font-size: rem-calc(17);
    @include button-style($primary-color, $black, $white);
    @include button-expand(true);
  }

  .form-actions {
    margin-top: 50px;
  }

  // File input
  .form-file-input {
    color: $dark-gray;
    background-color: $white;
    border: 2px solid $medium-gray;
    display: inline-block;
    cursor: pointer;
    position: relative;
    height: 44px;
    width: 150px;
    line-height: 41px;
    padding-left: 8px;
    transition: all ease 0.3s;

    &:hover {
      background-color: $light-gray;
    }

    &::after {
      content: '\f093';
      font-family: 'Font Awesome 5';
      font-size: 24px;
      color: $dark-gray;
      position: absolute;
      right: 11px;
      top: 0;
      font-weight: bold;
      z-index: 1;
    }
  }

  #edit-field-ein-file-0-upload--description {
    display: none;
  }

  .form-managed-file {
    position: relative;

    [id^='edit-field-ein-file-0-remove-button'] {
      background: url('@cpl_base/images/close-bold.svg') no-repeat center center;
      background-size: 16px 16px;
      width: 16px !important;
      height: 16px !important;
      font-size: 0;
      margin: 0;
      position: absolute;
      top: 12px;
      left: -3px;

      &:hover {
        opacity: 0.8;
        background-color: transparent;
      }

      &:focus {
        outline: none;
      }
    }

    .file--image {
      padding-left: 24px;
      font-family: $condensed-font-family;
      font-size: 22px;

      a {
        color: $dark-gray;
        position: relative;
        top: 4px;
      }
    }
  }

  .form-item-field-ein-file-0 {
    display: flex;
    justify-content: space-between;
    margin-top: $global-margin * 1.5;
    margin-bottom: $global-margin * 1.5;

    > label {
      border-bottom: 2px solid $medium-gray;
      width: 55%;
      color: $medium-gray;
      font-size: 13px;
      font-weight: 500;
      text-transform: uppercase;
    }
  }
}

// User pages slideshow
.node--user-page-slideshow-1 {
  min-height: calc(100vh - 131px);
  position: relative;

  h4 {
    color: $black;
  }

  .text-box-row {
    position: absolute;
    bottom: 90px;
    left: 35px;
    padding: 15px 30px 5px 30px;
    background-color: rgba($white, 0.9);
    width: 50%;
  }
}

// Reset password message
.user-pass-reset {
  text-align: center;
  padding-left: $global-padding;
  padding-right: $global-padding;
  margin-top: 50px;

  .form-actions {
    margin-top: 45px;
  }
}
