// Popular articles block
.block-blog-popular-articles {
  margin-top: 30px;
  margin-bottom: 50px;

  .block-title {
    font-size: rem-calc(34);
    line-height: 1.2;
    margin-bottom: rem-calc(20);
  }

  .blog-popular-articles {
    border-top: 1px solid $medium-gray;
  }

  .views-row {
    border-bottom: 1px solid $medium-gray;
    padding-bottom: 12px;
    padding-top: 12px;
  }

  .views-field-created {
    color: $dark-gray;
    text-transform: uppercase;
    font-size: rem-calc(14);
    margin-bottom: 5px;
  }

  .views-field-title {
    font-family: $header-font-family;
    line-height: 1.2;

    a {
      color: $black;

      &:hover {
        color: $primary-color;
      }
    }
  }
}

.cpl-blog-page-block,
.cpl-blog-page-block .views-row {
  @include grid-column-gutter(15);
}

.cpl-blog-page-block {
  .views-row {
    &:last-child {
      float: left;
    }
  }
}
