.cpl-product-page-style-6 {
  .product {
    .product-wrapper {
      @include grid-row;
    }

    .product-tabs {
      @include grid-row;
      @include clearfix;
    }

    .product-image:not(.is-not-gallery) {
      @include breakpoint(medium) {
        @include grid-column-size(8);
        @include grid-column-position(4);
        @include grid-column-gutter(0);
        float: left;
      }

      .product-image-zoom {
        @include breakpoint(large) {
          @include grid-column(9);
        }
      }

      .product-gallery {
        @include breakpoint(large) {
          @include grid-column(3);
        }
      }
    }

    .product-image.is-not-gallery {
      @include breakpoint(medium) {
        @include grid-column-size(6);
        @include grid-column-position(6);
        @include grid-column-gutter(0);
        float: left;
      }

      .product-image-zoom {
        @include breakpoint(large) {
          @include grid-column(12);
        }
      }
    }

    .product-price {
      .field-name-title {
        font-weight: bold;
        line-height: 1.3;
      }
    }

    .product-price:not(.is-not-gallery) {
      @include breakpoint(medium) {
        @include grid-column(4);
        @include grid-column-position(-8);
      }
    }

    .product-price.is-not-gallery {
      @include breakpoint(medium) {
        @include grid-column(6);
        @include grid-column-position(-6);
      }
    }

    .cloudzoom-gallery-active {
      @extend %product-active-gallery-left;
    }

    .cloudzoom-gallery {
      @include breakpoint(medium down) {
        display: block;
      }
    }
  }

  .product-tabs {
    @include breakpoint(small only) {
      margin-left: -#{$global-padding} !important;
      margin-right: -#{$global-padding} !important;
    }
  }

  hr {
    padding-top: 30px;
    margin-left: calc(-50vw + 50%);
    margin-right: calc(-50vw + 50%);
    max-width: none;
  }

  .field-name-price {
    .field-item {
      @extend %product-price-badge;
    }
  }

  .field-name-field-average-review {
    .field-item {
      @extend %product-review-one-line;
    }
  }

  .commerce-order-item-add-to-cart-form {
    @extend %product-select-light;
  }

  .js-form-type-number {
    @extend %product-field-number-light;
  }
}
