.favorite-products-view {
  @include breakpoint(small only) {
    margin-left: -#{$global-padding};
  }

  .views-row {
    margin-bottom: $global-margin;
    &:last-child {
      float: left;
    }
  }

  .image-box-row {
    border: 1px solid $light-gray;
  }

  .text-box-row {
    padding: 5px 0 5px;
  }

  &-empty {
    text-align: center;
    padding-top: 50px;
    padding-bottom: 50px;

    .far {
      font-size: rem-calc(64);
      color: $medium-gray;
    }

    h4 {
      color: $black;
      margin-top: 25px;
      margin-bottom: 5px;
    }

    .button {
      margin-top: $global-margin;
      padding-left: 30px;
      padding-right: 30px;
    }
  }
}
