// Flag
.product {
  .flag-favorite-product {
    position: absolute;
    top: 2px;
    left: 7px;
    z-index: 2;

    a {
      font-size: 0;

      &::after {
        font-family: 'Font Awesome 5';
        color: map-get($foundation-palette, alert);
        font-size: 22px;
      }

      &:hover {
        &::after {
          color: darken(map-get($foundation-palette, alert), 15%);
        }
      }
    }

    // Not liked
    &.action-flag {
      a {
        &::after {
          content: '\f004';
          font-weight: normal;
        }
      }
    }

    // Liked
    &.action-unflag {
      a {
        &::after {
          content: '\f004';
          font-weight: bold;
        }
      }
    }
  }

  .flag {
    .ajax-progress {
      display: none;
    }
  }
}

// Row Style 1
.product-row-style-1 {
  position: relative;
  height: 100%;

  h4 {
    font-weight: bold;
    font-family: $body-font-family;
  }

  h4,
  .field-name-price {
    font-size: 16px;
    line-height: 1.2;

    @include breakpoint(small only) {
      font-size: 13px;
      line-height: 1.2;
    }
  }

  .field-name-price {
    margin-top: 5px;
  }

  .field-name-field-product-image {
    z-index: 0;

    img {
      width: 100%;
    }

    .field-items {
      position: relative;

      // Slideshow when multiple product images
      &.is-slideshow {
        .field-item {
          position: absolute;
          top: 0;
          left: 0;
        }
      }
    }

    // Hide all images except first
    .field-item {
      &:not(:first-child) {
        display: none;
      }
    }
  }

  .content-box {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
  }

  .image-box-row {
    z-index: 1;
  }

  .text-box-row {
    position: relative;
    z-index: 1;
    margin-top: 3px;

    // 100% height, is broken on Safari. That's why we use flex-grow.
    flex-grow: 1;
    -webkit-flex-grow: 1;

    padding: 13px 20px;
    @include breakpoint(small only) {
      padding: 8px 15px;
    }
  }
}

// When teaser inside the view
.views-row {
  .product-row-style-1 {
    h4,
    .field-name-price {
      color: $black;
    }

    .text-box-row {
      background-color: $white;
    }
  }
}

// When teaser inside the paragraph
.paragraph--type--imgtxt {
  &.cpl-row-style-2 {
    .product-row-style-1 {
      .text-box-row {
        padding: 0;
        margin: 0;
      }

      .field-type-commerce-price {
        color: $black;
      }
    }
  }
}

// Sold out badge
.product-sold-out-badge {
  font-weight: bold;
  font-size: 11px;
  text-align: center;
  line-height: 1.2;
  text-transform: uppercase;
  background-color: $black;
  color: $white;
  display: inline-block;
  border-radius: 50%;
  width: 45px;
  height: 45px;
  padding-top: 11px;
  position: absolute;
  z-index: 2;
  right: 10px;
  top: 10px;
}
